import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HomePage } from '../../home/home';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LoaderProvider } from '../../../providers/loader';
import { ToastProvider } from '../../../providers/toast';
import { EventsService } from '../../../providers/event.service';
import { FleetManagement } from '../../../providers/fleet_management';
import { AttachDocumentsPage } from '../attach-documents';

/**
 * Generated class for the AddDocumentsMasterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-documents-master',
  templateUrl: 'add-documents-master.html',
})
export class AddDocumentsMasterPage {

  addForm: FormGroup;
  submitted = false;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public fleetManagement: FleetManagement,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public events: EventsService

  ) {

    this.addForm = this.fb.group({ 
      name: ['', Validators.required],
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddDocumentsMasterPage');
  }

  home() {
    this.navCtrl.setRoot(HomePage);
  }

  save(isValid) {
    this.submitted = true;
    if(isValid) {
      this.loader.show();
      var obj = {
        name: this.addForm.value.name
      };
      this.fleetManagement.addDocumentsMaster(obj).subscribe((res: any) => {

        this.loader.hide();
        this.addForm.reset();
        this.toast.show({ 'message': 'Added Successfully.' });
        this.navCtrl.push(AttachDocumentsPage, {flag: 1});

      }, err => {
        console.log("err >>>", err);
      });
    }
  }

}



