import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { LoaderProvider } from '../../providers/loader';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { RequestLoad } from '../../providers/request_load';
import { AddPodPage } from './add-pod/add-pod';
import { Accounts } from '../../providers/accounts';
import * as moment from 'moment';
import { EditPodPage } from './edit-pod/edit-pod';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Observable } from 'rxjs';
import { Trucks } from '../../providers/truck';

/**
 * Generated class for the ManagePodPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-pod',
  templateUrl: 'manage-pod.html',
})
export class ManagePodPage {

  mainPODList = [];

  truckListFrom: FormGroup;
  

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider,
    public accounts: Accounts,
    public trucks: Trucks,
    public fb: FormBuilder
  ) {

    // this.getPODList();
    this.truckListFrom = this.fb.group({
      truck_no: ['']
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManagePodPage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Action',
    //   buttons: [{
    //     text: 'Add',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(AddPodPage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(data, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit',
        icon: 'ios-create',
        handler: () => {
          this.navCtrl.push(EditPodPage, {"data": data });
        }
      }, {
        text: 'Remove',
        icon: 'ios-trash',
        handler: () => {
          this.deleteApi(data);
        }
      }]
    });
    await actionSheet.present();
  }


  getPODList() {

    this.loader.show();

    this.accounts.getPODList().subscribe( (res: any) => {

      var data = res.json();
      this.mainPODList = data.result;

      this.mainPODList.forEach(element => {
        element.pod_date_format = moment(element.pod_date).format("YYYY-MM-DD");
        
      });

      console.log("Result >>>>", data.result);
      this.loader.hide();

    }, err => {
      console.log("err >>>>", err);
    });

  }

  // edit(data) {
  //   // this.navCtrl.push(EditRequestLoadPage, {"data": data });
  // }

  deleteApi(data) {
    this.loader.show();
    var load_id = data._id;
    console.log("id  >>>", load_id);

    var pod_document_type = "POD";
    this.requestLoad.deleteAccountsLedger(data.deal_no, pod_document_type).subscribe((res: any) => {
      
      this.accounts.deletePOD(load_id).subscribe((res: any) => {
        this.loader.hide();
        this.navCtrl.push(ManagePodPage);
      }, err => {
        this.loader.hide();
        console.log("error");
      });
      
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }


  // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  onSelectTruck(e){
    console.log(">>>>>eeeee", e);
    this.mainPODList = [];
    this.loader.show();
    this.accounts.getListByTruckNo(e.truck_no).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.mainPODList = data.result;

      this.mainPODList.forEach(element => {
        element.pod_date_format = moment(element.pod_date).format("YYYY-MM-DD");
        
      });
     
    }, err => {
      this.loader.hide();
      console.log("error", err);
    });
  }
}



