import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ToastProvider } from '../../../../providers/toast';
import { LoaderProvider } from '../../../../providers/loader';
import { RequestLoad } from '../../../../providers/request_load';

/**
 * Generated class for the AddPaymentModePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-payment-mode',
  templateUrl: 'add-payment-mode.html',
})
export class AddPaymentModePage {

  addpaymentModeForm: FormGroup;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {
    this.addpaymentModeForm = this.fb.group({
      name: [''],
      description: ['']
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddPaymentModePage');
  }

  save() {
    const val = this.addpaymentModeForm.value;
    const obj = {
      name: val.name,
      description: val.description
    };

    this.loader.show();
    this.requestLoad.addPaymentMode(obj).subscribe(res => {
      this.loader.hide();
      this.toast.show({ 'message': 'Added Successfully.' });
      this.navCtrl.push('ManagePaymentModePage');
    }, err => {
      this.loader.hide();
      console.log('>>>>err', err);
    });
  }
}



