import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { AddUserPage } from '../admin-privileges/add-user/add-user';
import { EditUserPage } from '../edit-user/edit-user';
import { LoaderProvider } from '../../providers/loader';
import { HomePage } from '../home/home';

/**
 * Generated class for the ManageUserPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-user',
  templateUrl: 'manage-user.html',
})
export class ManageUserPage {

  usersList = [];
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider
    ) {
      this.getUsersList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageUserPage');
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  getUsersList() {
    this.loader.show();
    this.admin.getUsersList().subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.usersList = data.results;
      console.log("Data  >>>", this.usersList);
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }




  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add User',
    //     icon: 'add',
    //     handler: () => {
    //       // AdminPrivileges
          this.navCtrl.push('AddUserPage');
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }


  async presentActionSheet(userData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit User',
        icon: 'create',
        handler: () => {
          this.navCtrl.push(EditUserPage, {data: userData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteApi(userData);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteApi(id) {
    var user_id = id._id;
    console.log("id  >>>", user_id);
    this.admin.deleteUser(user_id).subscribe((res: any) => {
      this.navCtrl.push('ManageUserPage');
    }, err => {
      console.log("error");
    });
  }
}



