import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HomePage } from '../home/home';
import { FormArray, FormGroup, FormBuilder } from '@angular/forms';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { ToastProvider } from '../../providers/toast';
import { LoaderProvider } from '../../providers/loader';

/**
 * Generated class for the AuthLoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-auth-login',
  templateUrl: 'auth-login.html',
})
export class AuthLoginPage {
  loginForm: FormGroup

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public admin: AdminPrivilegeProvider
    
    ) {
      this.loginForm = this.fb.group({
        user_name: [''],
        password: [''],
      });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AuthLoginPage');
  }

  login() {
    var getFormData = this.loginForm.value;

    console.log("getFormData >>>>", getFormData);
    this.loader.show();
    this.admin.loginUser(getFormData).subscribe( (res: any) => {
              
      var data = res.json(); 
      console.log("data >>>>", data);
      this.loader.hide();

      if(data.result){
        // this.toast.show({ 'message': 'Done' });
        localStorage.setItem('login', 'true');
        localStorage.setItem('agency_name', data.result.agencies[0].name);
        localStorage.setItem('user_name', data.result.user_name);
        localStorage.setItem('user_id', data.result._id);


        this.navCtrl.setRoot(HomePage);
      } else {
        this.toast.show({ 'message': 'Invalid User Name/Password....!' });
      }
    }, err => {
      console.log("err >>>", err);
    });

    
  }

}



