import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, MenuController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { AuthLoginPage } from '../pages/auth-login/auth-login';
import { CallBookingRequestLoadPage } from '../pages/call-booking-request-load/call-booking-request-load';
import { AdminPrivilegesAddTruckPage } from '../pages/admin-privileges/admin-privileges-add-truck/admin-privileges-add-truck';
import { AddTransporterPage } from '../pages/admin-privileges/add-transporter/add-transporter';
import { AddDriverPage } from '../pages/admin-privileges/add-driver/add-driver';
import { AddCompanyPage } from '../pages/admin-privileges/add-company/add-company';
import { RequestLoadPage } from '../pages/admin-privileges/request-load/request-load';
import { ManageTransportPage } from '../pages/manage-transport/manage-transport';
import { ManageCompanyListPage } from '../pages/manage-company-list/manage-company-list';
import { ManageDriverPage } from '../pages/manage-driver/manage-driver';
import { ManageTruckListPage } from '../pages/manage-truck-list/manage-truck-list';
import { ManageRequestLoadPage } from '../pages/manage-request-load/manage-request-load';
import { AddAgenciesPage } from '../pages/admin-privileges/add-agencies/add-agencies';
import { ManageAgenciesPage } from '../pages/manage-agencies/manage-agencies';
import { AddUserPage } from '../pages/admin-privileges/add-user/add-user';
import { ManageUserPage } from '../pages/manage-user/manage-user';
import { ManageEmployeesPage } from '../pages/manage-employees/manage-employees';
import { AddEmployeePage } from '../pages/admin-privileges/add-employee/add-employee';
import { ManagePostLoadPage } from '../pages/manage-post-load/manage-post-load';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  selectedMenu;

  rootPage: any = localStorage.getItem('login') ? HomePage : AuthLoginPage;

  pages: Array<{title: string, component?: any, sub_pages?: any}>;

  constructor(
    public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public menuCtrl: MenuController
  ) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Dashboard', component: HomePage },
      { title: 'Post Load', sub_pages: [
          { title: 'Post Load', component: ManagePostLoadPage },
          { title: 'Trucks Availability'}
        ] 
      },
      { title: 'Loads', sub_pages: [
          { title: 'Request Load', component: ManageRequestLoadPage },
          { title: 'Loads Availability'}
        ]
      },
      { title: 'Accounts', sub_pages: [
          { title: 'Ledger'},
          { title: 'POD'}
        ]
      },
      { title: 'Marketing', sub_pages: [
          { title: 'Add Company', component: ManageCompanyListPage },
          { title: 'Add Transporter', component: ManageTransportPage },
          { title: 'Add Employee', component: ManageEmployeesPage },
          { title: 'Post Driver Vacancy'},
          { title: 'Employee Target'},
          { title: 'Mkt.Reports'},
          { title: 'Employee Performance'},
          { title: 'Employee Tracking'},
          { title: 'State Wise Business'},
          { title: 'Inventory'}
        ]
      },
      { title: 'Company Bidding' },
      { title: 'Truck Bidding' },
      { title: 'Fin.Reports', sub_pages: [
          { title: 'Rate of Return'},
          { title: 'Yield'}
        ]
      },
      { title: 'Fleet Management', sub_pages: [
          { title: 'Add Trucks', component: ManageTruckListPage },
          { title: 'Add Drivers', component: ManageDriverPage },
          { title: 'Attach Document'},
          { title: 'Document Expiry / Reminder'},
          { title: 'Truck Verification'},
          { title: 'Drivers Availability'}
        ] 
      },
      { title: 'Admin Priviliges', sub_pages: [
          { title: 'Add Agencies', component: ManageAgenciesPage },
          { title: 'Digital Marketing'},
          { title: 'Vision Target'},
          { title: 'Fastag'},
          { title: 'User Creation', component: ManageUserPage }
        ]
      },
      { title: 'Driver App' },
      { title: 'Deal Final' },
      { title: 'Log Out', component: AuthLoginPage }
      


      // component: ManageTransportPage 
      // { title: 'Call Booking', component: CallBookingRequestLoadPage },
      // { title: 'Admin Privileges', sub_pages: [
      //     { title: 'Manage Request Load', component: ManageRequestLoadPage },
      //     { title: 'Manage Transporters', component: ManageTransportPage },
      //     { title: 'Manage Companies', component: ManageCompanyListPage },
      //     { title: 'Manage Drivers', component: ManageDriverPage },
      //     { title: 'Manage Trucks', component: ManageTruckListPage },
      //     { title: 'Manage Agencies', component: ManageAgenciesPage }
      //   ]
      // },
      // // Post Load, Load Request, Transporter, Agencies, Drivers, Employee, Documents, Users

      // { title: 'Load Request', component: ManageRequestLoadPage  },
      // { title: 'Transporter', component: ManageTransportPage },
      // { title: 'Agencies', component: ManageAgenciesPage },
      // { title: 'Drivers', component: ManageDriverPage },
      // { title: 'Employee', component: ManageEmployeesPage },
      // { title: 'Documents' },
      // { title: 'Users', component: ManageUserPage },
      // { title: 'Login', component: AuthLoginPage },
    ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page, index) {
    if (page.component) {
      // Reset the content nav to have just this page
      // we wouldn't want the back button to show in this scenario
      this.nav.setRoot(page.component);
      this.menuCtrl.close();
    } else {
      if (this.selectedMenu) {
        this.selectedMenu = undefined;
      } else {
        this.selectedMenu = index;
      }
    }
  }

  openSubPage(page) {
    this.nav.push(page.component);
    this.menuCtrl.close();
  }
}



