import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { Trucks } from '../../../providers/truck';
import { HomePage } from '../../home/home';
import { NgForm, FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ToastProvider } from '../../../providers/toast';
import { LoaderProvider } from '../../../providers/loader';
import { Observable } from 'rxjs';
import { AddDriverPage } from '../add-driver/add-driver';

/**
 * Generated class for the AdminPrivilegesAddTruckPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-admin-privileges-add-truck',
  templateUrl: 'admin-privileges-add-truck.html',
})
export class AdminPrivilegesAddTruckPage {

  types = [
    {name: 'OPEN', value: 'OPEN'},
    {name: 'CONTAINER', value: 'CONTAINER'},
    {name: 'TRAILER', value: 'TRAILER'},
  ];

  capacity = [];
  transporterNames = [];
  driverNames = [];
  addForm: FormGroup;
  submitted = false;
  data = {
    truck_no: '',
    truck_type: '',
    truck_capacity: '',
    transporter_id: '',
    driver_id: ''
  }

  fetchFlag = false;
  transporterDetails;
  otpFlag = false;
  otp_no;

  transporterFlag = true;
  agencyFlag = false;
  driverFlag = false;
  guaranterName = "";
  guaranterType = "";

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public trucks: Trucks,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public fb: FormBuilder
  ) {
    this.addForm = this.fb.group({
      truck_no: ['', Validators.compose([Validators.required, Validators.pattern(/^((?!\s{1,}).)*$/)])],
      truck_type: ['', Validators.compose([Validators.required])],
      truck_capacity: ['', Validators.compose([Validators.required])],
      transporter_id: ['', Validators.compose([Validators.required])],
      driver_id: [''],
      // driver_id: ['', Validators.compose([Validators.required])],
      transport_srno: [''],
      agency_name: [''],
      user_name: [''],
      otp: [''],

      guaranter_name: [''],
      guaranter_type: [''],
      authorised_by: [''],
      mobile_no: ['']
      

    });

    this.addForm.controls['user_name'].setValue(localStorage.getItem('user_name'));
  }

  ionViewDidLoad() {
    this.getTransporterNames();
    this.getDriverNames();
    console.log('ionViewDidLoad AdminPrivilegesAddTruckPage');
  }

  public noWhitespaceValidator(control: FormControl) {
    const isWhitespace = (control.value || '').trim().length === 0;
    const isValid = !isWhitespace;
    return isValid ? null : { 'whitespace': true };
}

  // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for Transporter Name list
  transporterName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchTransporterName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for Transporter Name list
  DriverName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchDriverName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }
  
  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  onSelectTruck(e){
    console.log("Select truck >>>", e);
  }

  radioSelect(event) {
    console.log("Here ->", event);

    if(event == "transporter" ) {
      this.transporterFlag = true;
      this.agencyFlag = false;
      this.driverFlag = false;
      this.addForm.controls['guaranter_type'].setValue('transporter');
    }

    if(event == "agency" ) {
      this.transporterFlag = false;
      this.agencyFlag = true;
      this.driverFlag = false;
      this.addForm.controls['guaranter_type'].setValue('agency');
    }

    if(event == "driver" ) {
      this.transporterFlag = false;
      this.agencyFlag = false;
      this.driverFlag = true;
      this.addForm.controls['guaranter_type'].setValue('driver');
    }
  }

  getDetailsByRadio() {

    console.log("Here In Details");
    this.guaranterType = this.addForm.value.guaranter_type;

    if(this.guaranterType == "transporter" ) {

      this.loader.show();
      this.guaranterName = this.addForm.value.guaranter_name.transport_name;
      this.trucks.getTransporterDetailsByName(this.addForm.value.guaranter_name.transport_name).subscribe((res: any) => {
        var data = res.json(); 
        console.log("data >>", data);
        this.addForm.controls['mobile_no'].setValue(data.cmobile_no_1);
        this.loader.hide();
      }, err => {
        console.log(">>>error", err);
      });

    }

    if(this.guaranterType == "agency" ) {
      
      this.loader.show();
      this.guaranterName = this.addForm.value.guaranter_name.agency_name;
      this.trucks.getAgencyDetailsByName(this.addForm.value.guaranter_name.agency_name).subscribe((res: any) => {
        var data = res.json(); 
        console.log("data >>", data);
        this.addForm.controls['mobile_no'].setValue(data.cmobile_no_1);
        this.loader.hide();

      }, err => {
        console.log(">>>error", err);
      });

    }

    if(this.guaranterType == "driver" ) {

      this.loader.show();
      this.guaranterName = this.addForm.value.guaranter_name.driver_name;
      this.trucks.getDriverDetailsByName(this.addForm.value.guaranter_name.driver_name).subscribe((res: any) => {
        var data = res.json(); 
        console.log("data >>", data);
        this.addForm.controls['mobile_no'].setValue(data.mobile_no_1);
        this.loader.hide();

      }, err => {
        console.log(">>>error", err);
      });
      
    }
    
  }
  

  getCapacity(){
    var ttype = this.addForm.value.truck_type;
    this.trucks.getCapacityOfTruck().subscribe((res: any) => {
      var data = res.json(); 
      // console.log('res >>>>>>> ', res);
      this.capacity = [];
      data.results.forEach(r => {
        if (r.type == ttype) {
          r.capacity.forEach( a => {
            var obj = {
              "name": a.ton,
              "value": a.ton,
            }
            this.capacity.push(obj);
          });
        }
      });
      console.log('capacity >>>>>>> ', this.capacity);
    }, err => {

      console.log(">>>error", err);
    });
  }

  getTransporterNames(){
    this.trucks.getTransporterNames().subscribe((res: any) => {
      var data = res.json(); 
      data.result.forEach(r => {
        var obj = {
          "name": r.transport_name,
          "value": r._id
        };
        this.transporterNames.push(obj);
      });
    }, err => {
      console.log(">>>error", err);
    });
  }

  getDriverNames(){
    this.trucks.getDriverNames().subscribe((res: any) => {
      var data = res.json(); 
      data.result.forEach(r => {
        var obj = {
          "name": r.driver_name,
          "value": r._id
        };
        this.driverNames.push(obj);
      });
    }, err => {
      console.log(">>>error", err);
    });
  }

  fetch(){
    var srNo = this.addForm.value.transport_srno;
    
    this.loader.show();
    
    this.trucks.getBySrnoTransporter(srNo).subscribe((res: any) => {
      var data = res.json(); 
      this.transporterDetails = data.results;
      console.log("Srno >>>", data.results);
      this.fetchFlag = true;
      this.loader.hide();
    }, err => {
      console.log(">>>error", err);
    });

  }

  sendOTP() {
    var mobileNo = this.addForm.value.mobile_no;
    var otpNo = Math.floor(1000 + Math.random() * 9000);
    console.log("MobileNo >>>", mobileNo);
    console.log("otpNo >>>", otpNo);
    this.otp_no = otpNo;
    
    this.trucks.sendOTP(mobileNo, otpNo).subscribe((res: any) => {
      var data = res.json(); 

      console.log("res >>>", res);
      this.fetchFlag = true;
      this.loader.hide();
    }, err => {
      console.log(">>>error", err);
    });
  }

  checkOTP() {
    if(this.addForm.value.otp == this.otp_no) {
      this.otpFlag = false;
    } else {
      this.otpFlag = true; 
    }
  }

  manualVerified(){

  }

  save(isValid, value) {
    this.submitted = true;
    if (isValid && this.addForm.value.otp) {

      if (value.truck_no.truck_no) {
        this.toast.error({ 'message': 'Truck Already Exist..!' });
      } else {
        var obj = {
          "truck_no": value.truck_no,
          "type": value.truck_type,
          "capacity": value.truck_capacity,
          "agency_name": value.agency_name.agency_name ? value.agency_name.agency_name: value.agency_name,
          
          "transport_id": value.transporter_id._id,
          "driver_id": value.driver_id._id,

          "guaranter_name": this.guaranterName,
          "guaranter_type": this.guaranterType,
          "authorised_by": value.authorised_by,
          "mobile_no": value.mobile_no,

          "user_name": value.user_name
        };
    
        console.log("obj >>>>>", obj);
        this.loader.show();
        this.trucks.addTruck(obj).subscribe(res => {
    
          this.capacity = [];
          this.addForm.reset();
          this.loader.hide();
          this.toast.show({ 'message': 'Added successfully.' });
          this.navCtrl.setRoot('ManageTruckListPage');
        }, err => {
    
          this.toast.error({ 'message': 'Something went wrong.' });
          console.log(">>>>>Err", err);
        });
      }
    }
  }

  addNewDriver() {
    this.navCtrl.push('AddDriverPage');
  }
}



