import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EventsService } from '../../providers/event.service';
import { ToastProvider } from '../../providers/toast';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import { FormBuilder, FormGroup } from '@angular/forms';
import { HomePage } from '../home/home';

/**
 * Generated class for the EditUserPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-user',
  templateUrl: 'edit-user.html',
})
export class EditUserPage {

  details;
  editForm: FormGroup;
  agenciesNameList = [];
  permissionsList = [];
  finalAgenciesNameList = [];
  userRoles = ['Admin', 'Office Staff', 'Marketing Staff', 'Cluster Manager'];
  assignList = ['Backoffice', 'Agencies', 'Marketing', 'Investor'];
  AgenciesArray = [];
  addAssignArray = [];
  assignFinalList= [];
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public events: EventsService
  ) {
    this.details = this.navParams.get('data');

    console.log("details >>>", this.details);
    this.editForm = this.fb.group({ 
      user_name: [''],
      user_role: [''],
      assign: [''],
      agencies: [''],
      permissions: ['']
    });

    this.editForm.controls['user_name'].setValue(this.details.user_name);
    this.editForm.controls['user_role'].setValue(this.details.user_role);

    this.assignList.forEach(r => {
      var flag = false;
      this.details.assigns.forEach(e => {
        if(r == e.assign) {
          flag = true;
          var obj = { "assign": r };
          this.addAssignArray.push(obj);
        }
      });
      var obj = {"name": r, "flag": flag };
      this.assignFinalList.push(obj);
    });

    


    this.getAgenciesNameList();
    this.getUserPermissionList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditUserPage');
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  ngOnInit() {

  }

  getAgenciesNameList(){
    this.admin.getAgenciesNameList().subscribe((res: any) => {
      var data = res.json(); 

      data.results.forEach(r => {
        var obj = { "name": r.name}
        this.agenciesNameList.push(obj);
      });
      // console.log("data >>>", this.agenciesNameList);

      this.agenciesNameList.forEach(r => {
        // console.log("in agenciesNameList>>>", r);
        var flag = false;
        this.details.agencies.forEach(e => {
          if(r.name == e.name) {
            flag = true;
            var obj = { "name": r.name };
            this.AgenciesArray.push(obj);
          }
        });
        var obj = {"name": r.name, "flag": flag };
        this.finalAgenciesNameList.push(obj);
      });
  
      // console.log("finalAgenciesNameList >>>", this.finalAgenciesNameList);
      // console.log("AgenciesArray >>>", this.AgenciesArray);
    }, err => {
      console.log("err >>>", err);
    });
  }

  finalPermissionsList = [];
  groupnames = [];
  grouppermissions_row = [];

  getUserPermissionList(){
    this.loader.show();
    this.admin.getUserPermissionList().subscribe((res: any) => {
      var data = res.json(); 
      this.loader.hide();
      data.result.forEach(r => {
        this.permissionsList.push(r);
      });
      // console.log("data >>>", this.permissionsList);

      var rowArray = [];
      this.permissionsList.forEach(r => {
        this.details.priviliges.forEach(dr => {
          var pArray = [];
          if(r.group_name == dr.group_name){

            r.permissions_group.forEach(pr => {
              var flag = false;
              dr.permissions_group.forEach(pdr => {
                if(pr.name == pdr.name){
                  flag = true;

                  var inseobj = {"group": r.group_name, "name": pr.name, "value": pr.value};

                  this.groupnames.push(r.group_name);
                  this.grouppermissions_row.push(inseobj);
                }
              });
              var obj = {"name": pr.name, "value": pr.value, "flag": flag};
              pArray.push(obj);
   
            });
            // console.log(pArray);
           

            var main_obj = {"group_name": r.group_name, "permissions_group": pArray};
            rowArray.push(main_obj);
          }
        });
      });

      // console.log("rowArray >>>", rowArray);

      this.permissionsList.forEach(n => {
        var fl = 0;
        rowArray.forEach(ra => {
          if (n.group_name == ra.group_name) {
            fl = 1;
            this.finalPermissionsList.push(ra);
          }
        });
        // console.log(n.group_name, "flag >>>", fl);
        if(fl == 0) {
          this.finalPermissionsList.push(n);
        }
      });

      console.log("finalPermissionsList >>>", this.finalPermissionsList);
      console.log("groupnames >>>", this.groupnames);
      console.log("grouppermissions_row >>>", this.grouppermissions_row);
      
    }, err => {
      this.loader.hide();
      console.log("err >>>", err);
    });
  }

  addPermissions(event, gn, pname, pvalue) {
    if ( event.target.checked ) {
      var obj = {"group": gn, "name": pname, "value": pvalue};

      this.groupnames.push(gn);
      this.grouppermissions_row.push(obj);
    } else {
      var i = 0;
      this.grouppermissions_row.forEach(element => {

        if (element.name == pname && element.group == gn) {
          console.log(" IN    >>>>", i);
          this.grouppermissions_row.splice(i, 1);
        }
        i = i + 1;
      });
      console.log("this.grouppermissions_row >>>>", this.grouppermissions_row);
    }

  }
  
  addAgencies(event, aname, avalue) {
    if ( event.target.checked ) {
      var obj = { "name": aname };
      // , "value": avalue
      this.AgenciesArray.push(obj);
    } else {
      var i = 0;
      this.AgenciesArray.forEach(element => {

        if (element.name == aname) {
          console.log(" IN    >>>>", i);
          this.AgenciesArray.splice(i, 1);
        }
        i = i + 1;
      });
      console.log("this.AgenciesArray >>>>", this.AgenciesArray);
    }
  }

  addAssignfor(event, aname) {
    if ( event.target.checked ) {
      var obj = { "assign": aname };
      this.addAssignArray.push(obj);
    } else {
      var i = 0;
      this.addAssignArray.forEach(element => {

        if (element.assign == aname) {
          console.log(" IN    >>>>", i);
          this.addAssignArray.splice(i, 1);
        }
        i = i + 1;
      });
      console.log("this.addAssignArray >>>>", this.addAssignArray);
    }
  }

  update(){


    var new_groupnames =  Array.from(new Set(this.groupnames)); 
    var priviligesArray = [];
    new_groupnames.forEach(r => {
      var permissionsGroupArray = [];
      this.grouppermissions_row.forEach(d => {
        if(d.group == r) {
          var p = {"name": d.name, "value": d.value};
          permissionsGroupArray.push(p);
        }
        
      });
      var obj = { "group_name": r, "permissions_group": permissionsGroupArray}
      priviligesArray.push(obj);
    });

    var obj = {
      "user_name": this.editForm.value.user_name,
      // "password": this.details.password,
      "user_role": this.editForm.value.user_role,
      "assigns": this.addAssignArray,
      "agencies": this.AgenciesArray,
      "priviliges": priviligesArray
    }

    console.log("Final  >>>", obj);
    
    this.loader.show();
    this.admin.updateUser(this.details._id, obj).subscribe((res: any) => {

      this.loader.hide();
      this.editForm.reset();
      this.toast.show({ 'message': 'Update Successfully.' });
      this.navCtrl.setRoot('ManageUserPage');

    }, err => {
      console.log("err >>>", err);
    });
  }

}



