  selectDealNoData(data) {
    console.log("Deal No Data >>>>", data);
    this.addForm.controls['deal_no'].setValue(data.deal_no);
    this.addForm.controls['transporter_name'].setValue(data.transporter_name);

    this.dealDate = data.deal_date;
    this.dealFrom = data.from;
    this.dealTo = data.to;
    this.dealConsignor = data.company_name;
    this.DealNoDetails = data;
    this.mainPODAmount = data.balance_advance;

    this.loader.show();
    this.accounts.getPostLoadByTicketNo(data.company_ticket_no).subscribe((res: any) =>{
      var postData = res.json();
      this.loader.hide();

      if(postData.results[0]) {
        this.postLoadData = postData.results[0];
        this.addForm.controls['dispatch_date'].setValue(this.postLoadData.despatch_date);
        this.addForm.controls['delivery_date'].setValue(this.postLoadData.delivery_date);

        if (this.postLoadData.late_delivery_amount) {
          this.lateDeliveryAmount = this.postLoadData.late_delivery_amount;
        }

        if (this.postLoadData.detention_amount) {
          this.detentionAmount = this.postLoadData.detention_amount;
        }

        this.postLoadPaymentMode = this.postLoadData.payment_mode;
        
        
      }


      this.getAgencyId();
      this.getCompanyId();
      this.getTransporterId();
      this.getAgencyListByName();
      
      
      console.log("postLoadData >>>>", data.results);
    }, err => {
      console.log("err >>>>", err);
    });
  }

  setLateDelivery(event) {

    var deliveryDate = moment(this.addForm.value.delivery_date, "YYYY-MM-DD");
    var arrivalDate = moment(this.addForm.value.arrival_date, "YYYY-MM-DD");

    var lateDelivery = arrivalDate.diff(deliveryDate, 'days')+1  
    this.addForm.controls['late_delivery'].setValue(lateDelivery);  

    var cal = this.lateDeliveryAmount * lateDelivery;
    this.addForm.controls['company_late_delivery_amount'].setValue(cal);  
    
    if(this.addForm.value.unloading_date) {
      this.setDetention("");
    }
  }

  setDetention(event) {
    var arrivalDate = moment(this.addForm.value.arrival_date, "YYYY-MM-DD");
    var unloadingDate = moment(this.addForm.value.unloading_date, "YYYY-MM-DD");


    var detentionDiff = unloadingDate.diff(arrivalDate, 'days')+1  
    this.addForm.controls['detention'].setValue(detentionDiff);
    
    
    var cal = this.detentionAmount * detentionDiff;
    this.addForm.controls['company_detention_amount'].setValue(cal);  

  }

