import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController, Thumbnail } from 'ionic-angular';
import { LoaderProvider } from '../../providers/loader';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { HomePage } from '../home/home';
import { Observable } from 'rxjs';
import { Trucks } from '../../providers/truck';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { ToastProvider } from '../../providers/toast';
import { Accounts } from '../../providers/accounts';
import { RequestLoad } from '../../providers/request_load';
import { EditAccountsTransactionsPage } from './edit-accounts-transactions/edit-accounts-transactions';
import * as moment from 'moment';
/**
 * Generated class for the AccountsTransactionsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-accounts-transactions',
  templateUrl: 'accounts-transactions.html',
})
export class AccountsTransactionsPage {

  saveForm: FormGroup;
  editForm: FormGroup;

  toDate = new Date().toISOString();

  voucherTypes = ['Receipt', 'Payment', 'Contra', 'Journal', 'Debit Note', 'Credit Note'];
  accountsHead = [];
  againsts = ['DOCUMENT', 'EMI', 'INVESTMENT', 'MAINTENANCE', 'SALARY', 'SITE ADVANCE', 'TYRE'];

  mainAccoutsList = [];
  mainPaymentTypes = [];

  accounts_data: FormArray;

  mainAccountsTransactions = [];

  display = true;
  editData = false;

  truckNoList = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public fb: FormBuilder,
    public trucks: Trucks,
    public accounts: Accounts,
    public requestLoad: RequestLoad
  ) {
    this.saveForm = this.fb.group({
      agency_name: [''],
      voucher_type: [''],
      voucher_no: [''],
      date: [this.toDate],
      // account_head: [''],
      // payment_type: [''],
      // against: [''],
      // account_name: [''],
      truck_no: [''],
      naration: [''],
      debit: [0],
      credit: [0],

      accounts_data: this.fb.array([this.accountsDataForm()]),
    });

    this.editForm = this.fb.group({
      edit_voucher_type: [''],
      edit_payment_type: [''],
      edit_naration: ['']
    });

    // this.getAccounts();
    this.getPaymentType(); 
    this.getAccountsTransactions();


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AccountsTransactionsPage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }

  isReadonly() {
    return this.isReadonly;   //return true/false 
  }


  get accountsData() { return <FormArray>this.saveForm.get('accounts_data'); }

  accountsDataForm() {
    return this.fb.group({
      account_name: [''],
      account_debit: [''],
      account_credit: [''],
    })
  }

  addTo(): void {
    this.accounts_data = this.saveForm.get('accounts_data') as FormArray;
    this.accounts_data.push(this.accountsDataForm());
  }

  removeTo(index) {
    if (index === 0) {
      this.toast.error({ 'message': 'Cannot remove this form.' });
    } else {
      this.accounts_data.removeAt(index);
      this.calculationDrCr();
    }
  }

  // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  getVoucherNo() {

    if(this.saveForm.value.agency_name.agency_name && this.saveForm.value.voucher_type){

      this.loader.show();
      var agencyName = this.saveForm.value.agency_name.agency_name ;
      var voucherType = this.saveForm.value.voucher_type ;
      this.accounts.getMaxVoucherNo(agencyName, voucherType).subscribe((res: any) => {
        var data = res.json();
        this.loader.hide();
        console.log("Max No >>>>>", data.max_voucher_no);
        this.saveForm.controls['voucher_no'].setValue(data.max_voucher_no);

      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>Err", err);
      });
    }

    if(this.saveForm.value.agency_name.agency_name){
      this.getTruckNoByAgencyLedger();
    }
  }

  getPaymentType(){

    this.requestLoad.getPaymentTypesList().subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;

      dataList.forEach(element => {
        this.mainPaymentTypes.push(element.name);
      });
      console.log("mainPaymentTypes >>>>", this.mainPaymentTypes);


    }, err => {
      console.log(">>>>error", err);
    });
    
  }

  calculationDrCr() {

    // console.log("accounts_data >>>>", this.saveForm.value.accounts_data);

    var totalDebit = 0 ;
    var totalCredit = 0 ;
    this.saveForm.value.accounts_data.forEach(r => {
      
      if(r.account_debit){
        totalDebit = totalDebit + r.account_debit;
      }

      if(r.account_credit) {
        totalCredit = totalCredit + r.account_credit;
      }
    });

    this.saveForm.controls['debit'].setValue(totalDebit);
    this.saveForm.controls['credit'].setValue(totalCredit);

  }

  getAccountsTransactions() {

    this.loader.show();
    this.accounts.getAccountsTransactions().subscribe((res: any) => {
      var data = res.json(); 
      this.mainAccountsTransactions = data.results;
      this.loader.hide();

      this.mainAccountsTransactions.forEach(element => {
        
        element.date_frmt = moment(element.date).format('YYYY-MM-DD HH:mm');
      });
      console.log("mainAccountsTransactions >>>>", this.mainAccountsTransactions);


    }, err => {
      console.log(">>>>error", err);
    });

  }

  save() {

    var voucherTypesArray = [
      {"name": 'Receipt', "value": "REC"}, 
      {"name": 'Payment', "value": "PYT"}, 
      {"name": 'Contra', "value": "CON"}, 
      {"name": 'Journal', "value": "JOR"}, 
      {"name": 'Debit Note', "value": "DNT"}, 
      {"name": 'Credit Note',  "value": "CNT"},
    ];

    var getFormData = this.saveForm.value;
    var accountsDataArray = [];
    var accountsLedgerArray = [];
    var voucherDocumentType = "";

    voucherTypesArray.forEach(r => {

      if(r.name == getFormData.voucher_type) {
        voucherDocumentType = r.value;
      }      
    });

    var voucherNoObj = {
      agency_name: getFormData.agency_name.agency_name,
      voucher_type: getFormData.voucher_type,
      voucher_no: getFormData.voucher_no
    };

    var accountsTransactionsObj = {
      agency_name: getFormData.agency_name.agency_name,
      voucher_type: getFormData.voucher_type,
      voucher_document_type: voucherDocumentType,
      voucher_no: getFormData.voucher_no,
      date: getFormData.date,
      // account_head: "",
      // payment_type: getFormData.payment_type,
      // against: getFormData.against,
      // account_name: getFormData.account_name,

      truck_no: getFormData.truck_no,
      accounts_data: accountsDataArray,
      naration: getFormData.naration,
      debit: getFormData.debit,
      credit: getFormData.credit,
      user_name: localStorage.getItem('user_name')
    };


    getFormData.accounts_data.forEach(r => {
      r.account_name = r.account_name.account_name ? r.account_name.account_name: r.account_name;
      accountsDataArray.push(r);
    });

    

    console.log("accountsTransactionsObj >>>>>>", accountsTransactionsObj);
    

    this.loader.show();

    // Add Accounts Transactions
    this.accounts.addAccountsTransactions(accountsTransactionsObj).subscribe((res: any) => {

      var accountsTransactionsData = res.json();
      console.log("accountsTransactionsData >>>>>>", accountsTransactionsData.max_voucher_no);

      getFormData.accounts_data.forEach(r => {
        r.account_name = r.account_name.account_name ? r.account_name.account_name: r.account_name;
        accountsDataArray.push(r);
  
        var obj = {
          "agency_name": getFormData.agency_name.agency_name ? getFormData.agency_name.agency_name: getFormData.agency_name,
          "branch_name" : getFormData.agency_name.agency_name ? getFormData.agency_name.agency_name: getFormData.agency_name,
          "account_name": r.account_name,
          "truck_no": getFormData.truck_no,
          "narration": getFormData.naration,
          "sr_no": accountsTransactionsData.max_voucher_no,
          "document_type" : voucherDocumentType,
          "debit_amount" : r.account_debit,
          "credit_amount" : r.account_credit,
          "voucher_date" : getFormData.date,
          "user_name" : localStorage.getItem('user_name')
        };
        accountsLedgerArray.push(obj);
      });



      // Add Accounts Ledger Records 
      this.requestLoad.addAccountsLedger(accountsLedgerArray).subscribe((res: any) => {



        // Add Voucher 
        this.accounts.addVoucherNo(voucherNoObj).subscribe((res: any) => {
          this.loader.hide();
          this.saveForm.reset();
          this.toast.show({ 'message': 'Added successfully.' });
          this.getAccountsTransactions();
          // this.navCtrl.setRoot();
        }, err => {
          this.loader.hide();
          this.toast.error({ 'message': 'Something went wrong.' });
          console.log(">>>>>Err", err);
        });
      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>Err", err);
      });
    }, err => {
      this.loader.hide();
      this.toast.error({ 'message': 'Something went wrong.' });
      console.log(">>>>>Err", err);
    });
  }

  reset() {
    this.saveForm.reset();
  }

  edit(data) {

    this.navCtrl.push(EditAccountsTransactionsPage, {"data": data });
    // console.log("edit  >>>>>", data);

    // this.display = false;
    // this.editData = true;
    
    // this.editForm.controls['edit_voucher_type'].setValue(data.voucher_type);
    // this.editForm.controls['edit_payment_type'].setValue(data.payment_type);
    // this.editForm.controls['edit_naration'].setValue(data.naration);
  }

  cancel() {
    this.display = true;
    this.editData = false;
  }

  // update(data) {


  //   var obj = {
  //     agency_name: data.agency_name,
  //     voucher_type: this.editForm.value.edit_voucher_type,
  //     voucher_no: data.voucher_no,
  //     date: data.date,
  //     account_head: "",
  //     payment_type: this.editForm.value.edit_payment_type,
  //     against: data.against,
  //     accounts_data: data.accounts_data,
  //     naration: this.editForm.value.edit_naration,
  //     debit: data.debit,
  //     credit: data.credit,
  //     user_name: data.user_name
  //   };


  //   console.log("update  >>>>>", obj);

  //   this.loader.show();

  //   this.display = true;
  //   this.editData = false;
  //   this.accounts.updateAccountsTransactions(data._id, obj).subscribe((res: any) => {
  //     this.loader.hide();
  //     this.toast.error({ 'message': 'Updated successfully.' });
  //     this.getAccountsTransactions();
      
  //   }, err => {
  //     console.log(">>>>error", err);
  //   });

  // }

  delete(data) {
    console.log("delete  >>>>>", data);

    var voucherTypesArray = [
      {"name": 'Receipt', "value": "REC"}, 
      {"name": 'Payment', "value": "PYT"}, 
      {"name": 'Contra', "value": "CON"}, 
      {"name": 'Journal', "value": "JOR"}, 
      {"name": 'Debit Note', "value": "DNT"}, 
      {"name": 'Credit Note',  "value": "CNT"},
    ];
    var voucherDocumentType = "";

    voucherTypesArray.forEach(r => {

      if(r.name == data.voucher_type) {
        voucherDocumentType = r.value;
      }      
    });


    this.loader.show();

    // Delete Accounts Ledger Records 
    var document_type = voucherDocumentType;
    var agencyName = data.agency_name;
    this.requestLoad.deleteAccountsTransactionsLedger(data.voucher_no, document_type, agencyName).subscribe((res: any) => {

      this.accounts.deleteAccountsTransactions(data._id).subscribe((res: any) => {
        this.loader.hide();
        this.toast.error({ 'message': 'Deleted successfully.' });
        this.getAccountsTransactions();
      }, err => {
        console.log(">>>>error", err);
      });
    }, err => {
      console.log(">>>>error", err);
    });
  }  

  getTruckNoByAgencyLedger() {

    this.truckNoList = [];
    var agencyName = this.saveForm.value.agency_name.agency_name ? this.saveForm.value.agency_name.agency_name: this.saveForm.value.agency_name;
    // this.loader.show();
    this.accounts.getTruckNoByAgencyLedger(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;
      this.truckNoList = dataList;
      console.log("truck No List >>>>", this.truckNoList);
      // this.loader.hide();


    }, err => {
      console.log(">>>>error", err);
    });

  }

  // Search query for Accounts Name list
  accountsName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      var agencyName = this.saveForm.value.agency_name.agency_name ? this.saveForm.value.agency_name.agency_name: this.saveForm.value.agency_name;

      return this.accounts.searchAccountsNameByAgency(keyword, agencyName).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // searchAccountsNameByAgency

}



