import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { RequestLoad } from '../../../../providers/request_load';
import { LoaderProvider } from '../../../../providers/loader';
import { ToastProvider } from '../../../../providers/toast';
import { HomePage } from '../../../home/home';

/**
 * Generated class for the AddAccountTypePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-account-type',
  templateUrl: 'add-account-type.html',
})
export class AddAccountTypePage {

  form: FormGroup;
  submitted = false;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {

    this.form = this.fb.group({
      name: ['', Validators.required],
      field: ['']
    });
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }

  save(isValid) {
    this.submitted = true;
    if(isValid) {
      const val = this.form.value;
      const obj = {
        name: val.name,
        // field: val.field
      };
  
      this.loader.show();
      this.requestLoad.addAccountType(obj).subscribe(res => {
        this.loader.hide();
        this.toast.show({ 'message': 'Added Successfully.' });
        this.navCtrl.push('ManageAccountTypesPage');
      }, err => {
        this.loader.hide();
        console.log('>>>>err', err);
      });
    }
  }

}



