  getAccounts(){

    var agency_name = localStorage.getItem('agency_name');
    console.log("agency_name >>>>", agency_name);

    this.loader.show();
    this.requestLoad.getAccountsByAgency(agency_name).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;
      
      dataList.forEach(element => {
        this.mainAccoutsList.push({"srno": element.srno, "account_name": element.account_name});
      });
      
      console.log("mainAccoutsList >>>>", this.mainAccoutsList);
      this.createOldCards();
      this.loader.hide();
    }, err => {
      console.log(">>>>error", err);
    });

  }

  createOldCards() {

    this.data.company_advance_data.forEach(element => {

      var srno; 
      this.mainAccoutsList.forEach(r => {
        if (r.account_name == element.company_account_name) {
          srno = r.srno;
        }
      });

      element.srno = srno;
      element.date_format = moment(element.company_date).format('DD-MM-YYYY');
      this.mainCompanyAdvance.push(element);
    });
    
    this.data.transporter_advance_data.forEach(element => {
    
      var srno; 
    
      this.mainAccoutsList.forEach(r => {
        if (r.account_name == element.transporter_account_name) {
          srno = r.srno;
        }
      });
    
      element.srno = srno;
      element.date_format = moment(element.transporter_date).format('DD-MM-YYYY');
      this.mainTransporterAdvance.push(element);
    });

    console.log("mainCompanyAdvance >>>>", this.mainCompanyAdvance);
    console.log("mainTransporterAdvance >>>>", this.mainTransporterAdvance);
  }

