  addCalculation() {
    var getFormData = this.editForm.value;

    var rateCalculation = getFormData.rate_calculation;
    var truckRates = getFormData.truck_rates ? getFormData.truck_rates: 0;
    var truckWeight = getFormData.truck_weight ? getFormData.truck_weight: 0;
    var actualWeight = getFormData.actual_weight ? getFormData.actual_weight: 0;
    var freightTotal = 0

    if (rateCalculation == "Fixed"){
      freightTotal = truckRates;
    }

    if (rateCalculation == "Actual Weight"){
      freightTotal = truckRates * actualWeight;
    }

    if (rateCalculation == "Truck Capacity"){
      freightTotal = truckRates * truckWeight;
    }

    this.editForm.controls['freight'].setValue(freightTotal);
  }

  getRequestLoadById() {
    // this.loader.show();
    this.requestLoad.getRequestLoadByID(this.editForm.value.transporter_ticket_no).subscribe((res: any) => {
      // this.loader.hide();
      var data = res.json(); 
      var dataList = data.results;
      if(dataList[0]){
        this.editForm.controls['truck_no'].setValue(dataList[0].truck_no ? dataList[0].truck_no: "");
        this.editForm.controls['transporter_salesman'].setValue(dataList[0].salesman ? dataList[0].salesman: "");
        this.editForm.controls['transporter_name'].setValue(dataList[0].transporter_name ? dataList[0].transporter_name: "");
        
        this.requestLoadID = dataList[0]._id;
      }
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });  
  }

  getPostLoadById() {
    // this.loader.show();
    this.requestLoad.getPostLoadByID(this.editForm.value.company_ticket_no).subscribe((res: any) => {
      // this.loader.hide();
      var data = res.json(); 
      var dataList = data.results;

      if(dataList[0]){
        this.editForm.controls['company_salesman'].setValue(dataList[0].salesman ? dataList[0].salesman: "");
        this.editForm.controls['company_name'].setValue(dataList[0].company_name ? dataList[0].company_name: "");
        this.editForm.controls['from'].setValue(dataList[0].from ? dataList[0].from: "");
        this.editForm.controls['loading_place'].setValue(dataList[0].loading_place ? dataList[0].loading_place: "");
        if (dataList[0].destinations[0]) {
          this.editForm.controls['to'].setValue(dataList[0].destinations[0].to ? dataList[0].destinations[0].to: "");
          this.editForm.controls['unloading_place'].setValue(dataList[0].destinations[0].unloading_place ? dataList[0].destinations[0].unloading_place: "");
        }
        this.editForm.controls['rates_company'].setValue(dataList[0].rate ? dataList[0].rate: 0);
        this.editForm.controls['advance_company'].setValue(dataList[0].advance ? dataList[0].advance: 0);
        this.editForm.controls['weight_company'].setValue(dataList[0].ton ? dataList[0].ton: 0);
        this.editForm.controls['truck_weight'].setValue(dataList[0].ton ? dataList[0].ton: 0);
        
        this.postLoadID = dataList[0]._id;
      }
      
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

