import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { ToastProvider } from '../../../providers/toast';
import { LoaderProvider } from '../../../providers/loader';
import { RequestLoad } from '../../../providers/request_load';
import { AdminPrivilegeProvider } from '../../../providers/admin-privilege/admin-privilege';
import { HomePage } from '../../home/home';
import { ManageRewardsPage } from './add-rewards-redemption/manage-rewards';
import { EditRewardsRedemptionPage } from './edit-rewards-redemption/edit-rewards-redemption';

/**
 * Generated class for the ManageRewardsRedemptionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-rewards-redemption',
  templateUrl: 'manage-rewards-redemption.html',
})
export class ManageRewardsRedemptionPage {

  rewardsRedemptionList = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public admin: AdminPrivilegeProvider
  ) {
    this.getRewardsRedemption();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageRewardsRedemptionPage');
  }

  getRewardsRedemption() {
    this.loader.show();
    this.admin.getRewardsRedemptionList().subscribe(res => {
      console.log('>>>>Hello res', res);
      let data = res.json();
      this.loader.hide();
      this.rewardsRedemptionList = data.results;
    }, err => {
      console.log('>>>>Hello err', err);
    });
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Action',
    //   buttons: [{
    //     text: 'Add',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(ManageRewardsPage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(redemption, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit',
        icon: 'create',
        handler: () => {
          this.navCtrl.push(EditRewardsRedemptionPage, {data: redemption});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deletePaymentType(redemption);
        }
      }]
    });
    await actionSheet.present();
  }

  deletePaymentType(redemption) {
    const conf = confirm('Are you sure you want to delete This ?');

    if(conf) {
      this.loader.show();

      var document_type = "RRedm";
      // Delete Accounts Ledger Records
      this.requestLoad.deleteAccountsLedger(redemption.trno, document_type).subscribe((res: any) => {
        
        // Delete Rewards Ledger
        this.requestLoad.deleteRewardsLedger(redemption.trno).subscribe((res: any) => {

          // Delete Rewards Redemption
          this.admin.deleteRewardsRedemption(redemption._id).subscribe((res: any) => {
            this.loader.hide();
            this.toast.show({ 'message': 'Deleted Successfully.' });
            this.getRewardsRedemption();
          }, err => {
            this.loader.hide();
            console.log("error");
          });

        }, err => {
          this.loader.hide();
          console.log("error");
        });

      }, err => {
        this.loader.hide();
        console.log("error");
      });
    }
  }
}



