import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { RequestLoad } from '../../../providers/request_load';
import { HomePage } from '../../home/home';
import { AddPaymentModePage } from './add-payment-mode/add-payment-mode';
import { LoaderProvider } from '../../../providers/loader';
import { EditPaymentModePage } from './edit-payment-mode/edit-payment-mode';
import { ToastProvider } from '../../../providers/toast';

/**
 * Generated class for the ManagePaymentModePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-payment-mode',
  templateUrl: 'manage-payment-mode.html',
})
export class ManagePaymentModePage {

  modes: any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {
    this.getPaymentMode();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManagePaymentModePage');
  }

  getPaymentMode() {
    this.loader.show();
    this.requestLoad.getPaymentModeList().subscribe(res => {
      this.loader.hide();
      console.log('>>>>Hello res', res);
      let data = res.json();
      this.modes = data.results;
    }, err => {
      this.loader.hide();
      console.log('>>>>Hello err', err);
    });
  } 

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Payment Mode',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(AddPaymentModePage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(userData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit Payment Mode',
        icon: 'create',
        handler: () => {
          this.navCtrl.push(EditPaymentModePage, {data: userData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deletePaymentMode(userData._id);
        }
      }]
    });
    await actionSheet.present();
  }

  deletePaymentMode(id) {
    const conf = confirm('Are you sure you want to delete this payement mode ?');

    if(conf) {
      this.loader.show();
      this.requestLoad.deletePaymentMode(id).subscribe((res: any) => {
        this.loader.hide();
        this.toast.show({ 'message': 'Deleted Successfully.' });
        this.getPaymentMode();
      }, err => {
        this.loader.hide();
        console.log("error");
      });
    }
  }
}



