import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { RequestLoad } from '../../../../providers/request_load';
import { LoaderProvider } from '../../../../providers/loader';
import { ToastProvider } from '../../../../providers/toast';

/**
 * Generated class for the EditPaymentModePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-payment-mode',
  templateUrl: 'edit-payment-mode.html',
})
export class EditPaymentModePage {

  editpaymentModeForm: FormGroup;
  details: any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {

    this.details = this.navParams.get('data');

    this.editpaymentModeForm = this.fb.group({
      name: [''],
      description: ['']
    });

    this.editpaymentModeForm.controls['name'].setValue(this.details.name);
    this.editpaymentModeForm.controls['description'].setValue(this.details.description);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddPaymentModePage');
  }

  update() {
    const val = this.editpaymentModeForm.value;
    const obj = {
      name: val.name,
      description: val.description
    };

    this.loader.show();
    this.requestLoad.editPaymentMode(obj, this.details._id).subscribe(res => {
      this.loader.hide();
      this.toast.show({ 'message': 'Updated Successfully.' });
      this.navCtrl.push('ManagePaymentModePage');
    }, err => {
      this.loader.hide();
      console.log('>>>>err', err);
    });
  }
}



