import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { RequestLoad } from '../../../../providers/request_load';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { AdminPrivilegeProvider } from '../../../../providers/admin-privilege/admin-privilege';
import { ToastProvider } from '../../../../providers/toast';
import { LoaderProvider } from '../../../../providers/loader';
import { Trucks } from '../../../../providers/truck';
import { HomePage } from '../../../home/home';
import { ManageRewardsRedemptionPage } from '../manage-rewards-redemption';

/**
 * Generated class for the ManageRewardsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-rewards',
  templateUrl: 'manage-rewards.html',
})
export class ManageRewardsPage {
  addForm: FormGroup;
  toDate = new Date().toISOString();
  mainAccoutsList = [];
  mainPaymentTypes = [];
  user_name = "";
  salesmanList = [];
  // dealFinalObj;
  agencyId = ''; 

  maxTrNo;
  constructor(
    public navCtrl: NavController,
    public fb: FormBuilder,
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public trucks: Trucks,
    public admin: AdminPrivilegeProvider,
  ) {
    this.addForm = this.fb.group({
      date: [this.toDate],
      salesman: [''],
      points_generated: [''],
      redemption: [''],
      payment_type: [''],
      account_name: [''],
      remarks: [''],
      username: [''],
      srno: ['']
    });

    this.getPaymentType();
    this.getAccounts();
    this.user_name = localStorage.getItem('user_name');
    this.getSalesmanList();
    this.getMaxTrNoRewardsRedemption();
    this.getAgencyId();
  }
  
  ionViewDidLoad() {
    this.addForm.controls['username'].setValue(this.user_name);
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }

  getPaymentType(){

    this.requestLoad.getPaymentTypesList().subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;

      dataList.forEach(element => {
        this.mainPaymentTypes.push(element.name);
      });
      console.log("mainPaymentTypes >>>>", this.mainPaymentTypes);
    }, err => {
      console.log(">>>>error", err);
    });
  }

  getMaxTrNoRewardsRedemption(){

    this.admin.getMaxTrNoRewardsRedemption().subscribe((res: any) => {
      var data = res.json(); 
      this.maxTrNo = data.max_trno;
      console.log("maxTrNo >>>>", this.maxTrNo);
    }, err => {
      console.log(">>>>error", err);
    });
  }

  getAccounts(){

    var agency_name = localStorage.getItem('agency_name');
    console.log("agency_name >>>>", agency_name);

    this.requestLoad.getAccountsByAgency(agency_name).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;

      // console.log("mainAccoutsList >>>>", dataList);

      dataList.forEach(element => {
        this.mainAccoutsList.push(element.account_name);
      });
      console.log("mainAccoutsList >>>>", this.mainAccoutsList);


    }, err => {
      console.log(">>>>error", err);
    });

  }

  getSalesmanList(){
    var agencyName = localStorage.getItem('agency_name');
    this.admin.getListByAgency(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;

      console.log("mainAccoutsList >>>>", dataList);
      dataList.forEach(element => {
        this.salesmanList.push(element.first_name);
      });
      console.log("salesmanList >>>>", this.salesmanList);
    }, err => {
      console.log(">>>>error", err);
    });
  }

  getRewarsPoints() {
    console.log("Here >>>>");
    this.loader.show();
    var salesman = this.addForm.value.salesman;
    this.requestLoad.getSalesman(salesman).subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      
      this.addForm.controls['points_generated'].setValue(data.points_generated);
    }, err => {
      console.log(">>>>error", err);
    });
  }

  // getDealFinalDetails() {
  //   var dealNo = this.addForm.value.srno;
  //   this.requestLoad.getSrNo(dealNo).subscribe((res: any) => {
  //     var data = res.json(); 
  //     this.dealFinalObj = data.results;

  //     console.log("dealFinalObj >>>>", this.dealFinalObj);

  //     this.getAgencyId();
  //   }, err => {
  //     console.log(">>>>error", err);
  //   });
  // }

  getAgencyId(){
    var agencyName = localStorage.getItem('agency_name');
    this.trucks.searchAgencyName(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      
      this.agencyId = data[0]._id;
      console.log("agencyId >>>>", this.agencyId);
    }, err => {
      console.log(">>>>error", err);
    });
  
  }
       
  save() {

    var agencyName = localStorage.getItem('agency_name');

    var formData = this.addForm.value;
    formData.trno = this.maxTrNo;
    var obj = {
      "agency_name": agencyName,
      "branch": localStorage.getItem('agency_name'),
      "agency_id":  this.agencyId,
      "salesman": formData.salesman,
      "truck_no": "",
      "srno": this.maxTrNo,
      "narration_ref_no": "Deal no "+ this.maxTrNo,
      "document_type": "RRedm",
      "debit_amount": formData.redemption,
      "credit_amount": '',
      "voucher_date": formData.date,
      "username": formData.username
    };
    var finalArr = [];
    finalArr.push(obj);

    

    var accountsLedgerArray = [];

    var userName = localStorage.getItem('user_name');
    var userId = localStorage.getItem('user_id');

    
    // **************** Rewards ******************
    var rewardsMainCredit = {
      
      "agency_name": agencyName,
      "branch_id": this.agencyId,
      "branch_name": agencyName,
      "account_name": formData.account_name,
      "truck_no": "",
      "sr_no": this.maxTrNo,
      // "deal_id": "", 
      "narration": "Being Cash Paid Against Rewards Points",
      "narration_ref_no": "",
      "document_type": "RRedm",
      "debit_amount": 0,
      "credit_amount": formData.redemption,
      "voucher_date": formData.date,
      "party_name": "",
      "user_name": userName,
      "user_id": userId 
    };
    accountsLedgerArray.push(rewardsMainCredit);

    var rewardsMainDebit = {
      
      "agency_name": agencyName,
      "branch_id": this.agencyId,
      "branch_name": agencyName,
      "account_name": "Rewards Redemption",
      "truck_no": "",
      "sr_no": this.maxTrNo,
      // "deal_id": "", 
      "narration": "Being Cash Paid Against Rewards Points",
      "narration_ref_no": "",
      "document_type": "RRedm",
      "debit_amount": formData.redemption,
      "credit_amount": 0,
      "voucher_date": formData.date,
      "party_name": "",
      "user_name": userName,
      "user_id": userId 
    };
    accountsLedgerArray.push(rewardsMainDebit);

    // console.log("accountsLedgerArray >>>>", accountsLedgerArray);
    // ********************************************


    


    this.loader.show();

    console.log("addRewardsRedemption >>>>", formData);
    console.log("addRewardsLedger >>>>", finalArr);
    console.log("addAccountsLedger >>>>", accountsLedgerArray);

    this.admin.addRewardsRedemption(formData).subscribe((res: any) => {
      this.requestLoad.addRewardsLedger(finalArr).subscribe((res: any) => {
        this.requestLoad.addAccountsLedger(accountsLedgerArray).subscribe((res: any) => {
          this.loader.hide();
          this.addForm.reset();
          this.toast.show({ 'message': 'Added successfully.' });
          this.navCtrl.setRoot(ManageRewardsRedemptionPage);
        }, err => {
          this.loader.hide();
          this.toast.error({ 'message': 'Something went wrong.' });
          console.log(">>>>>Err", err);
        });
      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>Err", err);
      });
    }, err => {
      this.loader.hide();
      this.toast.error({ 'message': 'Something went wrong.' });
      console.log(">>>>>Err", err);
    });
  }

}



