import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { ToastProvider } from '../../providers/toast';
import { LoaderProvider } from '../../providers/loader';
import { Accounts } from '../../providers/accounts';
import { Trucks } from '../../providers/truck';
import { EventsService } from '../../providers/event.service';
import { HomePage } from '../home/home';
import { Observable } from 'rxjs';

/**
 * Generated class for the RewardsTrialBalancePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-rewards-trial-balance',
  templateUrl: 'rewards-trial-balance.html',
})
export class RewardsTrialBalancePage {

  fetchForm: FormGroup;
  toDate = new Date().toISOString();
  tableList = false;
  mainTrialList = [];
  totalDebit = 0;
  totalCredit = 0;
  branchName = [];

  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public events: EventsService,
    public trucks: Trucks,
    public accounts: Accounts,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public admin: AdminPrivilegeProvider
  ) {
    this.fetchForm=this.fb.group({
      from_date: [this.toDate],
      to_date: [this.toDate],
      agency_name: [''],
      // branch_name: ['']
    });
    this.getAccountsLedgerBranchName();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RewardsTrialBalancePage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }

  // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  getAccountsLedgerBranchName() {
    // this.loader.show();
    this.accounts.getAccountsLedgerBranchName().subscribe( (res: any) => {
      // this.loader.hide();
      var data = res.json();
      this.branchName = data.results;
      console.log("Accounts Name >>>>>", this.branchName);
    }, err => {
      // this.loader.hide();
      // this.toast.error({ 'message': 'Something went wrong while.' });
      console.log(">>>>>Err", err);
    });

  }
  
  fetch() {
    this.mainTrialList = [];
    this.totalCredit = 0;

    var filters = {
      from_date: '',
      to_date: '',
      agency_name: '',
      // salesman: '',
      // branch_name: ''
    };
    
    if (this.fetchForm.value.from_date){
      filters.from_date = this.fetchForm.value.from_date;
    } else {
      filters.from_date = '';
    }
    if (this.fetchForm.value.to_date){
      filters.to_date = this.fetchForm.value.to_date;
    } else {
      filters.to_date = '';
    }

    if (this.fetchForm.value.agency_name){
      filters.agency_name = this.fetchForm.value.agency_name.agency_name;
    } else {
      filters.agency_name = '';
    }

    // if (this.fetchForm.value.branch_name){
    //   filters.branch_name = this.fetchForm.value.branch_name;
    // } else {
    //   filters.branch_name = '';
    // }

    this.loader.show();
    
    this.accounts.displayRewardsTrialBalance(filters).subscribe( (res: any) => {
      
      var data = res.json();

      var totalCreditArray = 0;
      var totalDeditArray = 0;
      data.results.forEach(element => {
        var accountName = element.salesman;
        var debitAmount = element.dr;
        var creditAmount = element.cr;

        if(creditAmount == 0 && debitAmount == 0) {
          console.log("creditAmount and debitAmount  0");
        } else {
          var obj = {
            "salesman": accountName,
            "debit_amount": debitAmount,
            "credit_amount": Math.abs(creditAmount)
          };
          totalDeditArray = totalDeditArray + debitAmount;
          totalCreditArray = totalCreditArray + creditAmount;

          this.mainTrialList.push(obj);
          
          this.totalDebit = Math.abs(totalDeditArray);
          this.totalCredit = Math.abs(totalCreditArray);
        }
      });
      
      console.log("TrialBalance >>>>>", this.mainTrialList);
      this.tableList = true;
      this.loader.hide();
   
    }, err => {
      this.loader.hide();
      this.toast.error({ 'message': 'Something went wrong while.' });
      console.log(">>>>>Err", err);
    });

  }

}



