import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import * as moment from 'moment';
import { HomePage } from '../home/home';

/**
 * Generated class for the ManageDriverPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-driver',
  templateUrl: 'manage-driver.html',
})
export class ManageDriverPage {

  lists = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider
    ) {
  }

  ionViewDidLoad() {
    this.getList();
    console.log('ionViewDidLoad ManageDriverPage');
  }

  getList() {
    this.loader.show();
    this.admin.getDriverList().subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();

      data.result.forEach(element => {
        element.expiry_date = moment(element.expiry_date).format('DD-MM-YYYY');
      });

      this.lists = data.result;

      this.lists.forEach(r => {
        let locations = [];
        r.route_desired.forEach(l => {
          locations.push(l.location);
        });
        console.log("locations >>", locations.toString());
        r.route_preferred = locations.toString();
      });
      console.log("lists >>", this.lists);
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  edit(driverData) {
    this.navCtrl.push('EditDriverDetailsPage', {data: driverData});
  }

  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Driver',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push('AddDriverPage');
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(driverData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit driver list',
        icon: 'create',
        handler: () => {
          this.navCtrl.push('EditDriverDetailsPage', {data: driverData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteApi(driverData);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteApi(id) {
    var driver_id = id._id;
    console.log("id  >>>", driver_id);
    this.admin.deleteDriver(driver_id).subscribe((res: any) => {
      this.navCtrl.push('ManageDriverPage');
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



