import { Component, NgZone, ElementRef, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { RequestLoad } from '../../providers/request_load';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import { MapsAPILoader } from '@agm/core';
import { Observable } from 'rxjs';

/**
 * Generated class for the TrucksAvailabilityPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-trucks-availability',
  templateUrl: 'trucks-availability.html',
})
export class TrucksAvailabilityPage {

  dataList = [];

  @ViewChild("searchFrom")
  public searchElementRefFrom: ElementRef;

  @ViewChild("searchTo")
  public searchElementRefTo: ElementRef;


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
  ) {
    this.listTrucksAvailability();

    // setTimeout(() => {
    //   this.mapsAPILoader.load().then(() => {
    //     let formSearch = new google.maps.places.Autocomplete(this.searchElementRefFrom.nativeElement, {
    //       componentRestrictions: {"country": "IN"},
    //       types: []
    //     });
    //     formSearch.addListener("place_changed", () => {
    //       this.ngZone.run(() => {
    //         //get the place result
    //         let place: google.maps.places.PlaceResult = formSearch.getPlace();
    //         console.log("place   >>>>>>", place.address_components[0].long_name);
    //         console.log("vicinity   >>>>>>", place.vicinity);
    //         (<HTMLInputElement>document.getElementById('From')).value = place.vicinity;
    //       });
    //     });

    //     let toSearch = new google.maps.places.Autocomplete(this.searchElementRefTo.nativeElement, {
    //       componentRestrictions: {"country": "IN"},
    //       types: []
    //     });
    //     toSearch.addListener("place_changed", () => {
    //       this.ngZone.run(() => {
    //         //get the place result
    //         let place: google.maps.places.PlaceResult = toSearch.getPlace();
    //         console.log("place   >>>>>>", place.address_components[0].long_name);
    //         console.log("vicinity   >>>>>>", place.vicinity);
    //         (<HTMLInputElement>document.getElementById('TO')).value = place.vicinity;
    //       });
    //     });
    //   });
    // }, 1000);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TrucksAvailabilityPage');
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  // Search query for From list
  fromSearch = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.requestLoad.searchFromRequest(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for To list
  toSearch = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.requestLoad.searchToRequest(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  listTrucksAvailability() {
    this.loader.show();
    this.requestLoad.listTrucksAvailability().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      
      var i = 1;
      data.results.forEach(element => {
        element.srno = i;
        i = i + 1;
      });

      console.log('Data >>>>>>>', data.results);
      this.dataList = data.results;


      this.dataList.forEach( r => {
        
        var str = [];
        r.destinations.forEach(t => {
          str.push(t.to);        
        });
        console.log("str >>>>>", str);
        r.status = 'Pending';
        r.destinations_str = str;

        var negotiate = "";
        if(r.negotiate[0]) {
          var Ton = r.negotiate[0].ton ? r.negotiate[0].ton: "0";
          var Rates = r.negotiate[0].rates ? r.negotiate[0].rates: "0";
          negotiate = Ton+ " ton * " + Rates + " rates";
        }
        r.negotiate = negotiate;

      });

      console.log('Data >>>>>>>', this.dataList);
      this.filterFromData();
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

  filterKeys: string[] = ['from'];
  filterKeysTo: string[] = ['destinations_str'];
  filteredData = [];
  search_from = '';
  search_to = '';

  filterFromData(): void {
    if (this.search_from) {
      this.filteredData = this.dataList.filter(obj =>
        this.filterKeys.some(propertyName =>
          obj[propertyName].toString().toLowerCase().includes(this.search_from.toLowerCase())));
    } else if (this.search_to) {
      this.filteredData = this.dataList.filter(obj =>
        this.filterKeysTo.some(propertyName =>
          obj[propertyName].toString().toLowerCase().includes(this.search_to.toLowerCase())));
    } else {
      this.filteredData = this.dataList;
    }
  }

}



