import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { RequestLoad } from '../../../../providers/request_load';
import { LoaderProvider } from '../../../../providers/loader';
import { ToastProvider } from '../../../../providers/toast';

/**
 * Generated class for the EditCancelReasonsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-cancel-reasons',
  templateUrl: 'edit-cancel-reasons.html',
})
export class EditCancelReasonsPage {

  cancelReasonForm: FormGroup;
  page_name: string;
  details: any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {

    this.details = this.navParams.get('data');

    this.page_name = this.navParams.get('page');
    this.cancelReasonForm = this.fb.group({
      cancel_reason: ['']
    });

    this.cancelReasonForm.controls['cancel_reason'].setValue(this.details.cancel_reason);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditCancelReasonsPage');
  }

  update() {
    const val = this.cancelReasonForm.value;
    const obj = {
      name: this.page_name,
      cancel_reason: val.cancel_reason
    };

    this.loader.show();
    this.requestLoad.editCancelReason(obj, this.details._id).subscribe(res => {
      this.loader.hide();
      this.toast.show({ 'message': 'Updated Successfully.' });
      this.navCtrl.push('ManageCancelReasonsPage', { name: this.page_name });
    }, err => {
      this.loader.hide();
      console.log('>>>>err', err);
    });
  }
}



