import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Observable } from 'rxjs';
import { EventsService } from '../../providers/event.service';
import { RequestLoad } from '../../providers/request_load';
import { Trucks } from '../../providers/truck';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import { ToastProvider } from '../../providers/toast';
import { Accounts } from '../../providers/accounts';
import * as moment from 'moment';
import { HomePage } from '../home/home';
/**
 * Generated class for the RewardsLedgerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-rewards-ledger',
  templateUrl: 'rewards-ledger.html',
})
export class RewardsLedgerPage {
  

  fetchForm: FormGroup;
  tableList = false;
  mainAccoutsList = [];
  salesmanList = [];
  rewardsLedgerList=[]; 
  totalCredit = 0;
  totalDebit = 0;
  accountsLedgerList = [];
  closingAccount = 0;
  toDate = new Date().toISOString();


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public events: EventsService,
    public trucks: Trucks,
    public accounts: Accounts,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public admin: AdminPrivilegeProvider
  ) {
    
    this.fetchForm=this.fb.group({
      from_date: [this.toDate],
      to_date: [this.toDate],
      agency_name: [''],
      salesman:['']
    });
      // this.fetchForm.controls['agency_name'].setValue(this.agencyName);

    this.getSalesmanList();
    this.getAccounts();
  }


  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  getAccounts(){
    var agency_name = localStorage.getItem('agency_name');
    console.log("agency_name >>>>", agency_name);
    this.requestLoad.getAccountsByAgency(agency_name).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;
      // console.log("mainAccoutsList >>>>", dataList);
      dataList.forEach(element => {
        this.mainAccoutsList.push(element.account_name);
      });
      console.log("mainAccoutsList >>>>", this.mainAccoutsList);
    }, err => {
      console.log(">>>>error", err);
    });

  }

  onselectDealFinalSalesman() {
  } 

  getSalesmanList(){
    var agencyName = localStorage.getItem('agency_name');

    this.admin.getListByAgency(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;

      console.log("mainAccoutsList >>>>", dataList);

      dataList.forEach(element => {
        this.salesmanList.push(element.first_name);
      });
      console.log("salesmanList >>>>", this.salesmanList);


    }, err => {
      console.log(">>>>error", err);
    });
  }

  fetch() {
    this.rewardsLedgerList = [];
    
    var fromDate = this.fetchForm.value.from_date;
    var toDate = this.fetchForm.value.to_date;
    // var agency_name = this.fetchForm.value.agency_name;
    var salesman = this.fetchForm.value.salesman;
    console.log("Data >>>>>",  fromDate, toDate,salesman);

    var filters = {
      from_date: '',
      to_date: '',
      agency_name:'',
      salesman:''
    };
    
    if (this.fetchForm.value.from_date){
      filters.from_date = this.fetchForm.value.from_date;
    } else {
      filters.from_date = '';
    }
    if (this.fetchForm.value.to_date){
      filters.to_date = this.fetchForm.value.to_date;
    } else {
      filters.to_date = '';
    }
    if (this.fetchForm.value.agency_name){
      filters.agency_name = this.fetchForm.value.agency_name;
    } else {
      filters.agency_name = '';
    }
    if (this.fetchForm.value.salesman){
      filters.salesman = this.fetchForm.value.salesman;
    } else {
      filters.salesman = '';
    }
    this.loader.show();
    this.tableList = true;
    this.accounts.displayRewardLedger(filters).subscribe( (res: any) => {
      this.loader.hide();
      var data = res.json();
      console.log("Accounts >>>>>", data);

      this.totalCredit = data.total_credit;
      this.totalDebit = data.total_debit;

      var totalClosing = 0;
      
      data.results.forEach(element => {
        element.date_format = moment(element.voucher_date).format('DD/MM/YYYY');
        
        if(element.debit_amount){
          totalClosing = Math.abs(totalClosing + element.debit_amount);
        } else {
          totalClosing = Math.abs(totalClosing - element.credit_amount);
        }

        this.closingAccount = totalClosing;
         element.closing_list = totalClosing;
        // this.accountsLedgerList.push(element);
        this.accountsLedgerList=data.results;

      });
   
    }, err => {
      this.loader.hide();
      this.toast.error({ 'message': 'Something went wrong while.' });
      console.log(">>>>>Err", err);
    });

    }
  

}



