import { Component, ElementRef, ViewChild, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import { FormControl } from '@angular/forms';
import { MapsAPILoader } from '@agm/core';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { HomePage } from '../home/home';

/**
 * Generated class for the ManageTransportPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-transport',
  templateUrl: 'manage-transport.html',
})
export class ManageTransportPage {

  lists = [];
  citiesList = [];
  public latitude: number;
  public longitude: number;
  public searchControl: FormControl;
  public zoom: number;

  @ViewChild("search")
  public searchElementRef: ElementRef;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
    ) {
  }

  ionViewDidLoad() {
    // this.getList();
    this.getCitiesList();
    console.log('ionViewDidLoad ManageTransportPage');
  }

  ngOnInit() {

    // this.zoom = 4;
    // this.latitude = 39.8282;
    // this.longitude = -98.5795;

    // //create search FormControl
    this.searchControl = new FormControl();

    // this.mapsAPILoader.load().then(() => {
    //   let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
    //     types: ["address"]
    //   });
    //   autocomplete.addListener("place_changed", () => {
    //     this.ngZone.run(() => {
    //       //get the place result
    //       let place: google.maps.places.PlaceResult = autocomplete.getPlace();
    //       console.log("place   >>>>>>", place);
    //       console.log("vicinity   >>>>>>", place.vicinity);

    //       // //verify result
    //       // if (place.geometry === undefined || place.geometry === null) {
    //       //   return;
    //       // }
    //     });
    //   });
    // });

  }
  
  Home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  // Search query for truck no list
  searchTransporterAndCity = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.admin.searchTransportsAndCity(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }
  
  getCityDetails(c) {
    this.loader.show();
    this.admin.getTransportListByCity(c.city).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      
      this.lists = data.results;

      this.lists.forEach(r => {
        let locations = [];
        r.route_desired.forEach(l => {
          locations.push(l.location);
        });
        console.log("locations >>", locations.toString());
        r.route_preferred = locations.toString();
      });
      console.log("lists >>", this.lists);

    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  getTNDetails(c) {
    this.loader.show();
    this.admin.getTransportListByName(c).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.lists = data.results;
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  getCitiesList() {
    this.loader.show();
    this.admin.getTransportCitiesList().subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.citiesList = data.results;
    }, err => {
      console.log("error");
      this.loader.hide();
    });
  }

  // getList() {
  //   this.loader.show();
  //   this.admin.getTransportList().subscribe((res: any) => {
  //     this.loader.hide();
  //     let data = res.json();
  //     this.lists = data.result;
  //   }, err => {
  //     this.loader.hide();
  //     console.log("error", err);
  //   });
  // }

  edit(transportData) {
    this.navCtrl.push('EditTransportDetailsPage', {data: transportData});
  }

  onSelect(e){
    console.log("e >>>>", e);

    if(e.name == "city") {
      this.getCityDetails({"city": e.value})
    }
    if(e.name == "transport_name") {
      this.getTNDetails(e.value);
    }
  }

  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Transporter',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push('AddTransporterPage');
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(transportData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit transport list',
        icon: 'create',
        handler: () => {
          this.navCtrl.push('EditTransportDetailsPage', {data: transportData});
        }
      },{
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteApi(transportData);
        }
      }, {
        text: 'Block',
        icon: 'ios-archive',
        handler: () => {
          console.log('Share clicked');
        }
      }]
    });
    await actionSheet.present();
  }

  deleteApi(id) {
    var transporter_id = id._id;
    console.log("id  >>>", transporter_id);
    this.admin.deleteTransporter(transporter_id).subscribe((res: any) => {
      this.navCtrl.push('ManageTransportPage');
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



