import { Component, ViewChild, ElementRef, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LoaderProvider } from '../../../providers/loader';
import { ToastProvider } from '../../../providers/toast';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AdminPrivilegeProvider } from '../../../providers/admin-privilege/admin-privilege';
import { Observable } from 'rxjs';
import { Trucks } from '../../../providers/truck';
import { EventsService } from '../../../providers/event.service';
import { MapsAPILoader } from '@agm/core';
import { ManageEmployeesPage } from '../../manage-employees/manage-employees';
import * as moment from 'moment';
import { HomePage } from '../../home/home';
import { CommonHelper } from '../../../helpers/common';


/**
 * Generated class for the AddEmployeePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-employee',
  templateUrl: 'add-employee.html',
})
export class AddEmployeePage {

  addForm: FormGroup;
  categoryArray = ['Office Staff', 'Marketing Staff', 'Driver', 'Traffic', 'Cluster Manager'];
  // reportsArray = [{id: '', name: 'Cluster Manager'}];
  reportsArray = [];
  submitted = false;

  staffFlag = true;
  locationFlag = false;
  configFlag = false;

  dist = false;
  @ViewChild("search")
  public searchElementRef: ElementRef;
  // [(ngModel)]="n"
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public fb: FormBuilder,
    public events: EventsService,
    public trucks: Trucks,
    public admin: AdminPrivilegeProvider,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
    ) {

      this.addForm = this.fb.group({
        first_name: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
        last_name: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
        mobile_no: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
        email: ['', Validators.email],
        category: [''],
        reports_to: [''],
        reward_points: [''],
        from: [''],
        to: [''],
        agency_name: [''],
        work_location: [''],
        location_restriction: [''],
        latitude: [''],
        longitude: [''],
        check_in_out: [false],
        auto_log_out: [false],
        location_restriction_check: [false],
        live_tractrking: [false],
        check_out_reminders: [false],
        distance: [''],
        ideal_marker_time: ['']
      });

      this.getClusterManagersList();
    }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddEmployeePage');
  }

  // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  getLocation(e){
    e.preventDefault();
    this.events.broadcast('location');
  }

  getClusterManagersList() {

    // if( this.addForm.value.category == "Cluster Manager") {
      console.log("Yes...");

      // this.loader.show();
      this.admin.getEmployeesList().subscribe((res: any) => {
        // this.loader.hide();
        let data = res.json();

        // this.reportsArray = [];
        
        // this.reportsArray.push({id: '', name: 'Cluster Manager'});

        data.results.forEach(element => {
          if(element.category == "Cluster Manager") {
            var obj = {id: element._id, name: element.first_name};
            this.reportsArray.push(obj);
          }  
        });
      }, err => {
        this.loader.hide();
        console.log("error");
      });

    // } else {
    //   console.log("No...");
    //   this.reportsArray = [{id: '', name: 'Cluster Manager'}];
    // }
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  save(){
    let tdDate = moment().format('YYYY-MM-DD');
    this.submitted = true;
  
      console.log("Data >>>", this.addForm);
      var formData = this.addForm.value;
      formData.agency_name = formData.agency_name.agency_name;

      if(formData.from) {
        formData.from = tdDate + "T" + formData.from;
      }

      if (formData.to) {
        formData.to = tdDate + "T" + formData.to;
      }



      console.log("formData >>>>>", formData);
      if(formData.reports_to == ""){
        delete formData.reports_to;
      } 
      console.log("formData >>>>>", formData);
      
    
      // formData.ideal_marker_time = tdDate + "T" + formData.ideal_marker_time;
    

      this.loader.show();
      this.admin.addEmployee(formData).subscribe(res => {
        this.loader.hide();
        console.log("res >>>>>", res);
        // this.navCtrl.push(HomePage);
        this.addForm.reset();
        this.toast.show({ 'message': 'Added successfully.' });
        this.navCtrl.setRoot('ManageEmployeesPage');
      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>Err", err);
      }); 
    // }
  }

  nextStaff() {
    this.staffFlag = true;
    this.locationFlag = false;
    this.configFlag = false;
  }

  nextLocation() {
    this.staffFlag = false;
    this.locationFlag = true;
    this.configFlag = false;

    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        let presentCity = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentCity.getPlace();
            let lat = place.geometry.location.lat();
            let lng = place.geometry.location.lng();
            localStorage.setItem('latlng', JSON.stringify({
              lat: lat,
              lng: lng
            }));
            this.events.broadcast('location');
            this.addForm.controls['latitude'].setValue(lat);
            this.addForm.controls['longitude'].setValue(lng);
            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);
            
            (<HTMLInputElement>document.getElementById('Location')).value = pre_city;
            this.addForm.controls['work_location'].setValue(pre_city);
          });
        });
    
      });
    }, 2000);
  }

  nextConfig() {
    this.staffFlag = false;
    this.locationFlag = false;
    this.configFlag = true;
  }

  inout(event) {
    if(event.checked) {
      this.addForm.value.check_in_out = true;
    } else {
      this.addForm.value.check_in_out = false;
    }
  }

  autoLogOut(event) {
    if(event.checked) {
      this.addForm.value.auto_log_out = true;
    } else {
      this.addForm.value.auto_log_out = false;
    }
  }

  locationCheck(event) {
    if(event.checked) {
      this.addForm.value.location_restriction_check = true;
    } else {
      this.addForm.value.location_restriction_check = false;
    }
  }

  liveTractrking(event) {
    if(event.checked) {
      this.addForm.value.live_tractrking = true;
    } else {
      this.addForm.value.live_tractrking = false;
    }
  }

  checkReminders(event) {
    if(event.checked) {
      this.addForm.value.check_out_reminders = true;
      this.dist = true;
    } else {
      this.addForm.value.check_out_reminders = false;
      this.dist = false;
    }
  }
}



