import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { RequestLoad } from '../../../providers/request_load';
import { LoaderProvider } from '../../../providers/loader';
import { ToastProvider } from '../../../providers/toast';
import { HomePage } from '../../home/home';
import { AddCancelReasonsPage } from './add-cancel-reasons/add-cancel-reasons';
import { EditCancelReasonsPage } from './edit-cancel-reasons/edit-cancel-reasons';

/**
 * Generated class for the ManageCancelReasonsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-cancel-reasons',
  templateUrl: 'manage-cancel-reasons.html',
})
export class ManageCancelReasonsPage {

  page_name: string;
  reasons: any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {

    this.page_name = this.navParams.get('name');
    console.log('>>>>>>.Page_name', this.page_name);
    this.getCancelReasons();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageCancelReasonsPage');
  }

  getCancelReasons() {
    this.loader.show();
    this.requestLoad.getCancelReasons(this.page_name).subscribe(res => {
      this.loader.hide();
      let data = res.json();
      this.reasons = data.results;
    }, err => {
      this.loader.hide();
      console.log('>>>>Hello err', err);
    });
  } 

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    let pageName = 'Add ' + this.page_name + ' Cancel Reasons';
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: pageName,
        icon: 'add',
        handler: () => {
          this.navCtrl.push(AddCancelReasonsPage, { page: this.page_name });
        }
      }]
    });
    await actionSheet.present();
  }

  async presentActionSheet(userData, e: Event) {
    let pageName = 'Edit ' + this.page_name + ' Cancel Reasons';
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: pageName,
        icon: 'create',
        handler: () => {
          this.navCtrl.push(EditCancelReasonsPage, { data: userData, page: this.page_name});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteCancelReason(userData._id);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteCancelReason(id) {
    const conf = confirm('Are you sure you want to delete this reason ?');

    if(conf) {
      this.loader.show();
      this.requestLoad.deleteCancelReason(id).subscribe((res: any) => {
        this.loader.hide();
        this.toast.show({ 'message': 'Deleted Successfully.' });
        this.getCancelReasons();
      }, err => {
        this.loader.hide();
        console.log("error");
      });
    }
  }

}



