import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { RequestLoad } from '../../../../providers/request_load';
import { LoaderProvider } from '../../../../providers/loader';
import { ToastProvider } from '../../../../providers/toast';

/**
 * Generated class for the AddCancelReasonsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-cancel-reasons',
  templateUrl: 'add-cancel-reasons.html',
})
export class AddCancelReasonsPage {

  cancelReasonForm: FormGroup;
  page_name: string;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {
    this.page_name = this.navParams.get('page');

    console.log('>>>>PageName', this.page_name);

    this.cancelReasonForm = this.fb.group({
      cancel_reason: ['']
    });
  }

  ionViewDidLoad() {
  }

  save() {
    const val = this.cancelReasonForm.value;
    const obj = {
      name: this.page_name,
      cancel_reason: val.cancel_reason
    };

    this.loader.show();
    this.requestLoad.addCancelReason(obj).subscribe(res => {
      this.loader.hide();
      this.toast.show({ 'message': 'Added Successfully.' });
      this.navCtrl.push('ManageCancelReasonsPage', { name: this.page_name });
    }, err => {
      this.loader.hide();
      console.log('>>>>err', err);
    });
  }
}



