import { Component, Input } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { IonicPage, NavController, NavParams,App } from 'ionic-angular';
import {TrafficOperationPage} from '../../pages/traffic-operation/traffic-operation'
import {TrafficQualityEdit} from '../../pages/traffic-quality-edit/traffic-quality-edit'

/**
 * Generated class for the TrafficOpsDealsTabComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'traffic-ops-deals-tab',
  templateUrl: 'traffic-ops-deals-tab.html'
})
export class TrafficOpsDealsTabComponent {
  deal_statuses = [
    "Deal Final",
    "Truck Verification",
    "Bank Details / Loading Status",
    "Recievable advance",
    "Pending POD - T",
    "Pending POD - C",
    "Receivable Balance",
    "Commission"
  ];

isShowing=false;
isEditing=false;
pendingVerification:any;
pendingAdvance:any;
pendingBalance:any;
podSubmit:any;

  @Input() data:any;
  list:any;
  addForm: FormGroup;
  bank_amount:any;
  constructor(
    public fb: FormBuilder,
    public navCtrl:NavController,
    public app:App
  ) {
    this.addForm = fb.group({
      vehicleVerification: [false], 
      advanceBalanceCompany: [''],
      balFrtCompany: [''],
      advanceBalanceTransporter: [''],
      balFrtTransporter: [''],
      podT: [false],
      podC: [true],
      status:[true]
    });
    // console.log();
    this.list=JSON.parse(localStorage.data);
    var __this=this;
    console.log(this.list);
      app.viewWillEnter.subscribe(
      () => __this.list=JSON.parse(localStorage.data))

      this.pendingVerification=this.list.filter((e)=>{
        return !e.traffic_operations.document_verification;
      });
var tmp=[];
var tmp2=[];
this.list=this.list.map((e)=>{
        // tmp.push()   
        e.tmp=[];
        e.traffic_operations.bank_details.forEach((k)=>{
        // tmp.push(k.bank_amount) 
                if(!isNaN(k.bank_amount)){          
        e.tmp.push(k.bank_amount)
      }
        });

        e.amountTotal=e.tmp.reduce((a, b) => a + b, 0)
        return e;
      });
this.list=this.list.map((e)=>{
        // tmp.push()   
        e.tmp2=[];
        e.receivable_advance.forEach((k)=>{
        // tmp.push(k.bank_amount)   
        if(!isNaN(k.cheque_amount)){          
        e.tmp2.push(k.cheque_amount)
        }
        });

        e.amountTotal_recive=e.tmp2.reduce((a, b) => a + b, 0)
        return e;
      });

  



  }


  editBoth(id,index){
    localStorage.setItem("index",id);
    localStorage.setItem("selectedIndex",index?index:0);

    if(localStorage.quality){
      
      this.navCtrl.push(TrafficQualityEdit);
    return;
  }
    this.navCtrl.push(TrafficOperationPage);
  }
  saveBoth(){
    this.isShowing=false;

  }
  save(){
    this.isEditing=false;

  }
  edit(){
    this.isEditing=true;

  }

}



