import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { LoaderProvider } from '../../providers/loader';
import { RequestLoad } from '../../providers/request_load';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { HomePage } from '../home/home';
import { AddDealFinalNewPage } from './add-deal-final-new/add-deal-final-new';
import { EditDealFinalNewPage } from './edit-deal-final-new/edit-deal-final-new';
import { TrafficOperationsPage } from '../traffic-operations/traffic-operations';

/**
 * Generated class for the ManageDealFinalNewPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-deal-final-new',
  templateUrl: 'manage-deal-final-new.html',
})
export class ManageDealFinalNewPage {

  dataList = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider
  ) {
    this.getDealFinalList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageDealFinalNewPage');
  }

  

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Deal Final',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(AddDealFinalNewPage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(data, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit Deal Final',
        icon: 'ios-create',
        handler: () => {
          this.navCtrl.push(EditDealFinalNewPage, {"data": data });
        }
      }, {
        text: 'Traffic Operation',
        icon: 'ios-clipboard',
        handler: () => {
          this.navCtrl.push(TrafficOperationsPage, { "data": data });
        }
      }, {
        text: 'Remove',
        icon: 'ios-trash',
        handler: () => {
          this.deleteApi(data);
        }
      }]
    });
    await actionSheet.present();
  }

  getDealFinalList() {
    this.loader.show();
    this.requestLoad.getDealFinalNewListSorted().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      this.dataList = data.results;

      console.log('Data >>>>>>>', this.dataList);
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }


  deleteApi(id) {
    console.log("Data  >>>", id);
    var load_id = id._id;
    console.log("id  >>>", load_id);

    this.getPostLoadById(id.company_ticket_no);
    this.getRequestLoadById(id.transporter_ticket_no);
    this.loader.show();

    // // ------>> Update Post Load 
    // var postLoadObj = {"status": "Pending"};
    // this.requestLoad.updatePostLoadStatus(id.company_ticket_no, postLoadObj).subscribe(res => {
    //   console.log("Updated Post Load>>>>>");
    // }, err => {
    //   console.log(">>>>>Err", err);
    // });
    
    

    // // ------>> Update Request Load 

    // var requestLoadObj = {"status": "Pending"};
    // this.requestLoad.updateRequestLoadStatus(id.transporter_ticket_no, requestLoadObj).subscribe(res => {
    //   console.log("Updated Request Load>>>>>");
    // }, err => {
    //   console.log(">>>>>Err", err);
    // });
    

    // Delete Deal Final Record
    this.requestLoad.deleteDealFinalNew(load_id).subscribe((res: any) => {

      // Delete Accounts Ledger Records
      var document_type = "DF";
      this.requestLoad.deleteAccountsLedger(id.deal_no, document_type).subscribe((res: any) => {

        // Delete Rewards Ledger Records
        this.requestLoad.deleteRewardsLedger(id.deal_no).subscribe((res: any) => {

          this.loader.hide();
          this.navCtrl.push(ManageDealFinalNewPage);

        }, err => {
          this.loader.hide();
          console.log("error");
        });
      }, err => {
        this.loader.hide();
        console.log("error");
      });
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  getRequestLoadById(ticket_no) {
    // this.loader.show();
    this.requestLoad.getRequestLoadIdByTicketNo(ticket_no).subscribe((res: any) => {
      // this.loader.hide();
      var data = res.json(); 
      var dataList = data.results;
      if(dataList[0]){
        var requestLoadID = dataList[0]._id;
        console.log("requestLoadID >>>>", requestLoadID);

        var requestLoadObj = {"status": "Pending"};

        this.requestLoad.updateRequestLoadStatus(requestLoadID, requestLoadObj).subscribe(res => {
          console.log("Updated Request Load>>>>>");
        }, err => {
          console.log(">>>>>Err", err);
          // this.toast.error({ 'message': 'Something went wrong.' });
        });
      }
    }, err => {
      // this.loader.hide();
      console.log(">>>>error", err);
    });  
  }

  getPostLoadById(ticket_no) {
    // this.loader.show();
    this.requestLoad.getPostLoadIdByTicketNo(ticket_no).subscribe((res: any) => {
      // this.loader.hide();
      var data = res.json(); 
      var dataList = data.results;
      if(dataList[0]){
        var postLoadID = dataList[0]._id;
        console.log("postLoadID >>>>", postLoadID);

        var postLoadObj = {"status": "Pending"};

        this.requestLoad.updatePostLoadStatus(postLoadID, postLoadObj).subscribe(res => {
          console.log("Updated Post Load>>>>>");
        }, err => {
          console.log(">>>>>Err", err);
          // this.toast.error({ 'message': 'Something went wrong.' });
        });
      }
      
    }, err => {
      // this.loader.hide();
      console.log(">>>>error", err);
    });
  }
}



