import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { HomePage } from '../home/home';

/**
 * Generated class for the ManageAgenciesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-agencies',
  templateUrl: 'manage-agencies.html',
})
export class ManageAgenciesPage {

  lists = [];
  citiesList = [];
  public searchControl: FormControl;
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider
    ) {
  }

  ionViewDidLoad() {
    // this.getList();
    this.getCitiesList();
  }

  ngOnInit() {
    this.searchControl = new FormControl(); 
  }
  
  getCitiesList() {
    this.loader.show();
    this.admin.getAgenciesCitiesList().subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.citiesList = data.results;
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  // getList() {
  //   this.loader.show();
  //   this.admin.getAgencies().subscribe((res: any) => {
  //     this.loader.hide();
  //     let data = res.json();
  //     this.lists = data.results;
  //   }, err => {
  //     this.loader.hide();
  //     console.log("error");
  //   });
  // }

  edit(agencyData) {
    this.navCtrl.push('EditAgenciesDetailsPage', {data: agencyData});
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }
  getCityDetails(c) {
    this.loader.show();
    this.admin.getAgencyListByCity(c.city).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.lists = data.results;
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  getAgyDetails(c) {
    this.loader.show();
    this.admin.getAgencyListByName(c).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.lists = data.results;
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  onSelect(e){
    console.log("e >>>>", e);

    if(e.name == "city") {
      this.getCityDetails({"city": e.value})
    }
    if(e.name == "agency_name") {
      this.getAgyDetails(e.value);
    }
  }

  // Search query for Agency list
  searchAgencyAndCity = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.admin.searchAgenciesAndCity(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add agency',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push('AddAgenciesPage');
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(agencyData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit agency list',
        icon: 'create',
        handler: () => {
          this.navCtrl.push('EditAgenciesDetailsPage', {data: agencyData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteApi(agencyData);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteApi(id) {
    var agency_id = id._id;
    console.log("id  >>>", agency_id);
    this.admin.deleteAgency(agency_id).subscribe((res: any) => {
      this.navCtrl.push('ManageAgenciesPage');
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



