import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { RequestLoad } from '../../../providers/request_load';
import { HomePage } from '../../home/home';
import { AddPaymentTypePage } from './add-payment-type/add-payment-type';
import { LoaderProvider } from '../../../providers/loader';
import { EditPaymentTypePage } from './edit-payment-type/edit-payment-type';
import { ToastProvider } from '../../../providers/toast';

/**
 * Generated class for the ManagePaymentTypePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-payment-type',
  templateUrl: 'manage-payment-type.html',
})
export class ManagePaymentTypePage {

  types: any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {
    this.getPaymentType();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManagePaymentTypePage');
  }

  getPaymentType() {
    this.requestLoad.getPaymentTypesList().subscribe(res => {
      console.log('>>>>Hello res', res);
      let data = res.json();
      this.types = data.results;
    }, err => {
      console.log('>>>>Hello err', err);
    });
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Payment Type',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(AddPaymentTypePage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(userData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit Payment Type',
        icon: 'create',
        handler: () => {
          this.navCtrl.push(EditPaymentTypePage, {data: userData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deletePaymentType(userData._id);
        }
      }]
    });
    await actionSheet.present();
  }

  deletePaymentType(id) {
    const conf = confirm('Are you sure you want to delete this payement type ?');

    if(conf) {
      this.loader.show();
      this.requestLoad.deletePaymentType(id).subscribe((res: any) => {
        this.loader.hide();
        this.toast.show({ 'message': 'Deleted Successfully.' });
        this.getPaymentType();
      }, err => {
        this.loader.hide();
        console.log("error");
      });
    }
  }
}



