import { Component, Output, EventEmitter, Input } from '@angular/core';
import { CommonHelper } from '../../helpers/common';
import { EventsService } from '../../providers/event.service';

declare var google: any;

/**
 * Generated class for the GoogleMapsComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'google-maps',
  templateUrl: 'google-maps.html'
})
export class GoogleMapsComponent {

  text: string;
  zoom:number;
  address: any;

  lat: number;
  lng: number;

  @Output() googleAddress: EventEmitter<any> = new EventEmitter<any>();
  @Output() googleAddressForUpdate: EventEmitter<any> = new EventEmitter<any>();

  numDeltas = 100;
  delay = 10; //milliseconds
  count = 0;
  deltaLat;
  deltaLng;
  marker;

  constructor(public events: EventsService) {
    
    let latlng = JSON.parse(localStorage.getItem('latlng'));

    if(latlng) {
      this.lat = parseFloat(latlng.lat);
      this.lng = parseFloat(latlng.lng);
    } else {
      this.lat = parseFloat("23.8375602");
      this.lng = parseFloat("78.7135212");
      localStorage.setItem('latlng', JSON.stringify({lat: this.lat, lng: this.lng}));
    }
        
    // if( latlng ) {
    //   this.setCurrentLocation(latlng.lat, latlng.lng);
    //   console.log("Get...........>>>")
    // }

    this.events.on('location', () => {
      console.log("ok");
      let latlng = JSON.parse(localStorage.getItem('latlng'));
      this.setCurrentLocation(latlng.lat, latlng.lng);
    });
  }

  private setCurrentLocation(lat?: number, lng?: number) {
    console.log("call setCurrentLocation...........>>>")
    if ('geolocation' in navigator) {

      console.log("If In...........>>>", lat, lng);
      navigator.geolocation.getCurrentPosition((position) => {
        this.lat = lat ? lat : position.coords.latitude;
        this.lng = lng ? lng : position.coords.longitude;
        this.zoom = 15;
        this.getGeoLocation(this.lat, this.lng);
        localStorage.setItem('latlng', JSON.stringify({lat: this.lat, lng: this.lng}));
      });
    }
  }

  markerDragEnd(e) {
    this.markerDraggedAddress(e.coords.lat, e.coords.lng);
    this.getGeoLocation(e.coords.lat, e.coords.lng);
  }

  // Get address from lat and lng
  getGeoLocation(lat: number, lng: number) {
    if (navigator.geolocation) {
      let latlng = new google.maps.LatLng(lat, lng);
      let geocoder = new google.maps.Geocoder();

      geocoder.geocode({'latLng': latlng}, (results, status) => {
        if (status == google.maps.GeocoderStatus.OK) {
          if (results[1]) {
            let addr1 = CommonHelper.getGeocodeLocationParts(results[0]);
            let addr2 = CommonHelper.getAddressParts(addr1); 
            setTimeout(() => {
              this.googleAddress.emit(addr2);
              this.googleAddressForUpdate.emit(addr2);
            }, 2000);
            let address_line2 = addr2.address_line2 ? addr2.address_line2 + ', ' : '';
            this.address = addr2.address_line1 + ', ' + address_line2 + addr2.city + ', ' + addr2.state + ', ' + addr2.country + ', ' +  addr2.pincode;
          }
        }
      });
    }
  }

  markerDraggedAddress(lat, lng) {
    if (navigator.geolocation) {
      let latlng = new google.maps.LatLng(lat, lng);
      let geocoder = new google.maps.Geocoder();

      geocoder.geocode({'latLng': latlng}, (results, status) => {
        if (status == google.maps.GeocoderStatus.OK) {
          if (results[1]) {
            let addr1 = CommonHelper.getGeocodeLocationParts(results[0]);
            let addr2 = CommonHelper.getAddressParts(addr1); 
            this.googleAddressForUpdate.emit(addr2);
            let address_line2 = addr2.address_line2 ? addr2.address_line2 + ', ' : '';
            this.address = addr2.address_line1 + ', ' + address_line2 + addr2.city + ', ' + addr2.state + ', ' + addr2.country + ', ' +  addr2.pincode;
          }
        }
      });
    }
  }

  onMapClick(result) {
    this.transition(result.coords.lat, result.coords.lng);
  }

  transition(lat, lng){
    this.count = 0;
    this.deltaLat = (lat - this.lat)/this.numDeltas;
    this.deltaLng = (lng - this.lng)/this.numDeltas;
    this.moveMarker();
  }

  moveMarker(){
    this.lat += this.deltaLat;
    this.lng += this.deltaLng;
    var latlng = new google.maps.LatLng(this.lat, this.lng);
    // this.marker.setPosition(latlng);
    // if(this.count != this.numDeltas){
    //     this.count++;
    //     setTimeout(this.moveMarker, this.delay);
    // }
  }

}



