import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { RequestLoad } from '../../providers/request_load';
import { Observable } from 'rxjs';
import 'rxjs/add/observable/of';
import { FormBuilder, FormArray, FormGroup, FormControl } from '@angular/forms';
import { LoaderProvider } from '../../providers/loader';
import { ToastProvider } from '../../providers/toast';
import { Trucks } from '../../providers/truck';
import { HomePage } from '../home/home';

/**
 * Generated class for the MatchCompanyListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-match-company-list',
  templateUrl: 'match-company-list.html',
})
export class MatchCompanyListPage {
  transporter_name = "";
  truck_no = "";
  from = "";
  loading_place = "";
  id = "";
  srno = "";
  loctions = [];
  companyList = [];
  statuses = ['Load Available', 'No Load'];
  countCompany = 0;
  addForm: FormGroup;
  addMultipleNegotiate: FormArray; 
  materialList = [];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public toastController: ToastController,
    public formBuilder: FormBuilder,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public trucks: Trucks
    ) {
      this.id = navParams.get('id');
      this.srno = navParams.get('srno');
      this.transporter_name = navParams.get('transporter_name');
      this.truck_no = navParams.get('truck_no');
      this.loctions = navParams.get('to');
      this.from = navParams.get('from');
      console.log("loading_place >>>", navParams.get('loading_place'));
      // this.loading_place = navParams.get('loading_place');
      this.getRequestLoadList();
      this.statuses = ['Load Available', 'No Load'];
      console.log(" >+> ", this.statuses);

      this.addForm = this.formBuilder.group({
        addMultipleNegotiate: this.formBuilder.array([])
      });
      this.getMaterials();

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MatchCompanyListPage');
  }

  ngOnInit() {
    console.log('ID >>>>>', this.id);
  }

  get NegotiateData(): FormArray { return this.addForm.get('addMultipleNegotiate') as FormArray; }

  createItem(data): FormGroup {
    return this.formBuilder.group({
      company_name: new FormControl(data.company_name ? data.company_name : ''),
      from: new FormControl(data.from ? data.from : ''),
      // to: new FormControl(data.to ? data.to : ''),
      // unloading_place: new FormControl(data.unloading_place ? data.unloading_place : ''),
      // material: new FormControl(data.material ? data.material : ''),
      // ton: new FormControl(data.ton ? data.ton : ''),
      // rates: new FormControl(data.rates ? data.rates : ''),
      status: new FormControl(data.status ? data.status : ''),
      // advance: new FormControl(data.advance ? data.advance : '')
    });
  }

  getRequestLoadList() {
    this.loader.show();
    this.requestLoad.getNegotiateDetails(this.id).subscribe((res: any) => {
      this.loader.hide();
      var data = res.json();

      if (data.results[0]) {
        // this.companyList = data.results;
        var company_groups = [];
        data.results.forEach(element => {
          company_groups.push(this.createItem(element));
          this.countCompany = this.countCompany + 1;
        });
        this.companyList = data;

        this.addForm = this.formBuilder.group({
          addMultipleNegotiate: this.formBuilder.array(company_groups)
        });
      } else {        
        this.requestLoad.getMatchCompanyList(this.id).subscribe((res: any) => {
          var data = res.json(); 
          var company_groups = [];
          data.forEach(element => {
            company_groups.push(this.createItem(element));
            this.countCompany = this.countCompany + 1;
          });
          this.companyList = data;

          this.addForm = this.formBuilder.group({
            addMultipleNegotiate: this.formBuilder.array(company_groups)
          });
        });
      }
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

  // Search query for truck no list
  companyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.requestLoad.searchCompany(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  onSelectCompany(e) {
    console.log(e);
    this.countCompany = this.countCompany + 1;
    var obj = {
      "company_name": e.company_name,
      "from": this.from
      // "to": ""
      // "unloading_place": ""
    }

    this.addMultipleNegotiate = this.addForm.get('addMultipleNegotiate') as FormArray;
    this.addMultipleNegotiate.push(this.createItem(obj));
    // this.companyList.unshift(obj);
  }

  update(){
    const getFromData = this.addForm.value;
    var obj = { "negotiate": getFromData.addMultipleNegotiate};
    this.loader.show();
    this.requestLoad.updateNegotiate(this.id, obj).subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      // this.addForm.reset();
      this.toast.show('Updated successfully.');
    }, err => {
      this.loader.hide();
    });
  }

  getMaterials(){
    this.trucks.getMaterials().subscribe((res: any) => {
      var data = res.json(); 
      
      data.results.forEach(r => {
        this.materialList.push(r.name);
      });
      console.log("getMaterials >>>", this.materialList);

    }, err => {
      console.log(">>>error", err);
    });
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

}



