import { Component, Input, OnInit } from '@angular/core';
import { FleetManagement } from '../../providers/fleet_management';
import * as moment from 'moment';
import { Traffic } from '../../providers/traffic';
import { ToastProvider } from '../../providers/toast';
/**
 * Generated class for the TrafficOpsTrucksVerificationComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'traffic-ops-trucks-verification',
  templateUrl: 'traffic-ops-trucks-verification.html'
})
export class TrafficOpsTrucksVerificationComponent implements OnInit {

  text: string;

  @Input() data;
  fileList;
  datas;

  constructor(
    public fleetManagement: FleetManagement,
    public traffic: Traffic,
    public toast: ToastProvider,
  ) {
  }

  ngOnInit() {
    this.datas=JSON.parse(localStorage.data)[Number(localStorage.index)];
    this.fetchDocs(JSON.parse(localStorage.data)[Number(localStorage.index)].truck_no);
  }

  fetchDocs(truckNo) {
    console.log(this.datas);
    this.fleetManagement.getfilesList(this.datas.truck_no).subscribe(res => {
      let fileList = res.json(); // existing files
      let finalList = [];
      fileList.forEach(mf => {
        mf.files.forEach(f => {
          let endDt = moment(f.enddate);
          let today = moment();
          f.is_expired = endDt.isBefore(today);

          finalList.push(f);
        });
      });

      console.log(finalList);
      this.fileList = finalList;
    });
  }

  documentTypeOkay() {
    
    var obj = {
      document_verification: true
    }; 
    this.traffic.updateDocumentType(this.datas._id, obj).subscribe(res => {
      this.datas.traffic_operations.document_verification = true;
      this.toast.show({ 'message': 'Updated successfully.' });
      console.log("Okay >>>", this.datas);

      var dt=JSON.parse(localStorage.data).map((e,i)=>{
        if(i==Number(localStorage.index)){
          e.traffic_operations.document_verification =true;
        }
        return  e;
      })
      localStorage.data=JSON.stringify(dt);


    }, err => {
      this.toast.error({ 'message': 'Something went wrong.' });
      console.log(">>>>>Err", err);
    });
    
  }
  documentTypeNotOkay() {
    // console.log("NotOkay");
    
    var obj = {
      document_verification: false
    };
    this.traffic.updateDocumentType(JSON.parse(localStorage.data)[Number(localStorage.index)]._id, obj).subscribe(res => {
      this.datas.traffic_operations.document_verification = false;
      this.toast.show({ 'message': 'Updated successfully.' });
      console.log("NotOkay >>>", this.datas);
      var dt=JSON.parse(localStorage.data).map((e,i)=>{
        if(i==Number(localStorage.index)){
          e.traffic_operations.document_verification =false;
        }
        return  e;
      })
      localStorage.data=JSON.stringify(dt);


    }, err => {
      this.toast.error({ 'message': 'Something went wrong.' });
      console.log(">>>>>Err", err);
    });
  }

}



