import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ManageTransportPage } from '../manage-transport/manage-transport';
import { ManageCompanyListPage } from '../manage-company-list/manage-company-list';
import { ManageTruckListPage } from '../manage-truck-list/manage-truck-list';
import { ManageDriverPage } from '../manage-driver/manage-driver';
import { ManageAgenciesPage } from '../manage-agencies/manage-agencies';
import { ManageRequestLoadPage } from '../manage-request-load/manage-request-load';
import { AddUserPage } from '../admin-privileges/add-user/add-user';
import { ManageUserPage } from '../manage-user/manage-user';
import { ManageEmployeesPage } from '../manage-employees/manage-employees';
import { AddTransporterPage } from '../admin-privileges/add-transporter/add-transporter';
import { AddCompanyPage } from '../admin-privileges/add-company/add-company';
import { AddEmployeePage } from '../admin-privileges/add-employee/add-employee';
import { AdminPrivilegesAddTruckPage } from '../admin-privileges/admin-privileges-add-truck/admin-privileges-add-truck';
import { AddDriverPage } from '../admin-privileges/add-driver/add-driver';
import { AddAgenciesPage } from '../admin-privileges/add-agencies/add-agencies';
import { ManagePostLoadPage } from '../manage-post-load/manage-post-load';
import { LoadsAvailabilityPage } from '../loads-availability/loads-availability';
import { TrucksAvailabilityPage } from '../trucks-availability/trucks-availability';
import { DealFinalPage } from '../manage-deal-final/deal-final/deal-final';
import { ManageDealFinalPage } from '../manage-deal-final/manage-deal-final';
import { ManagePaymentTypePage } from '../admin-privileges/manage-payment-type/manage-payment-type';
import { ManagePaymentModePage } from '../admin-privileges/manage-payment-mode/manage-payment-mode';
import { ManageCancelReasonsPage } from '../admin-privileges/manage-cancel-reasons/manage-cancel-reasons';
import { ManageAccountTypesPage } from '../admin-privileges/manage-account-types/manage-account-types';
import { ManageAccountCategoriesPage } from '../admin-privileges/manage-account-categories/manage-account-categories';
import { ManageAccountsMasterPage } from '../manage-accounts-master/manage-accounts-master';
import { ManageAccountsLedgerPage } from '../manage-accounts-ledger/manage-accounts-ledger';
import { RewardsLedgerPage } from '../rewards-ledger/rewards-ledger';
import { TrialBalancePage } from '../trial-balance/trial-balance';
import { ManageRewardsRedemptionPage } from '../admin-privileges/manage-rewards-redemption/manage-rewards-redemption';
import { RewardsTrialBalancePage } from '../rewards-trial-balance/rewards-trial-balance';
import { AccountsTransactionsPage } from '../accounts-transactions/accounts-transactions';
import { ManagePodPage } from '../manage-pod/manage-pod';
import { AddAdvanceAmountPage } from '../manage-advance-amount/add-advance-amount/add-advance-amount';
import { ManageDealFinalNewPage } from '../manage-deal-final-new/manage-deal-final-new';
import { ManageAdvanceAmountPage } from '../manage-advance-amount/manage-advance-amount';
import { AttachDocumentsPage } from '../attach-documents/attach-documents';
import { TrafficOperationsPage } from '../traffic-operations/traffic-operations';
import { TrafficQualityControl } from '../traffic-quality-control/traffic-quality-control';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})


export class HomePage {

  main = true;
  load = false;
  postload = false;
  account = false;
  marketing = false;
  report = false;
  fleet = false;
  admin = false;
  marketingSub = false;

  constructor(public navCtrl: NavController) {

  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  onTrafficControl(){
    this.navCtrl.push(TrafficQualityControl);
  }

  onManagePostLoad(){
    this.navCtrl.push(ManagePostLoadPage);
  }

  onManageTransports(){
    this.navCtrl.push(ManageTransportPage);
  }

  onManageCompanies(){
    this.navCtrl.push(ManageCompanyListPage);
  }

  onManageTrucks(){
    this.navCtrl.push(ManageTruckListPage);
  }

  onManageDrivers(){
    this.navCtrl.push(ManageDriverPage);
  }

  onManageAgencies(){
    this.navCtrl.push(ManageAgenciesPage);
  }

  onManageRequests(){
    this.navCtrl.push(ManageRequestLoadPage);
  }

  onUser(){
    this.navCtrl.push(ManageUserPage);
  }

  onAddCompany(){
    this.navCtrl.push(ManageCompanyListPage);
  }

  onAddTransporter(){
    this.navCtrl.push(ManageTransportPage);
  }

  onAddEmployee(){
    this.navCtrl.push(ManageEmployeesPage);
  }

  onAddTrucks(){
    this.navCtrl.push(ManageTruckListPage);
  }

  onAddDrivers(){
    this.navCtrl.push(ManageDriverPage);
  }

  onAddAgency(){
    this.navCtrl.push(ManageAgenciesPage);
  }

  onAddUser(){
    this.navCtrl.push(ManageUserPage);
  }

  onEmployee(){
    this.navCtrl.push(ManageEmployeesPage);
  }

  onLoadsAvailability(){
    this.navCtrl.push(LoadsAvailabilityPage);
  }

  onTrucksAvailability(){
    this.navCtrl.push(TrucksAvailabilityPage);
  }

  onDealFinal() {
    this.navCtrl.push(ManageDealFinalPage);
    // this.navCtrl.push(DealFinalPage);
  }

  onDealFinalNew() {
    this.navCtrl.push(ManageDealFinalNewPage);
  }

  onPaymentMode(){
    this.navCtrl.push(ManagePaymentModePage);
  }

  onPaymentType(){
    this.navCtrl.push(ManagePaymentTypePage);
  }

  onRequstLoadCancel(){
    this.navCtrl.push(ManageCancelReasonsPage, { name: 'Request Load'});
  }

  onPostLoadCancel(){
    this.navCtrl.push(ManageCancelReasonsPage, { name: 'Post Load'});
  }

  onDealFinalCancel(){
    this.navCtrl.push(ManageCancelReasonsPage, { name: 'Deal Final'});
  }

  onAccountTypes(){
    this.navCtrl.push(ManageAccountTypesPage);
  }

  onAccountCategories(){
    this.navCtrl.push(ManageAccountCategoriesPage);
  }

  onAccountMaster() {
    this.navCtrl.push(ManageAccountsMasterPage);
  }

  onAccountsLedger() {
    this.navCtrl.push(ManageAccountsLedgerPage);
  }

  onRewards() {
    this.navCtrl.push(ManageRewardsRedemptionPage);
  }

  onRewardsLedger() {
    this.navCtrl.push(RewardsLedgerPage);
  }

  onTrialBalance() {
    this.navCtrl.push(TrialBalancePage);
  }

  onRewardsTrialBalance() {
    this.navCtrl.push(RewardsTrialBalancePage);
  }

  onAccountsTransactions() {
    this.navCtrl.push(AccountsTransactionsPage);
  }

  onPOD() {
    this.navCtrl.push(ManagePodPage);
  }

  onAdvanceAmount() {
    // this.navCtrl.push(AddAdvanceAmountPage);
    this.navCtrl.push(ManageAdvanceAmountPage);
  }

  onAttachDocuments() {
    this.navCtrl.push(AttachDocumentsPage);
  }

  onDealFinalDashboard() {
    this.navCtrl.push(TrafficOperationsPage);
  }

  showPostLoad(){
    this.main = false;
    this.postload = true;
    this.load = false;
    this.account = false;
    this.marketing = false;
    this.report = false;
    this.fleet = false;
    this.admin = false;
  }

  showLoad(){
    this.main = false;
    this.postload = false;
    this.load = true;
    this.account = false;
    this.marketing = false;
    this.report = false;
    this.fleet = false;
    this.admin = false;
  }

  showAccount(){
    this.main = false;
    this.postload = false;
    this.load = false;
    this.account = true;
    this.marketing = false;
    this.report = false;
    this.fleet = false;
    this.admin = false;
  }

  showMarket(){
    this.main = false;
    this.postload = false;
    this.load = false;
    this.account = false;
    this.marketing = true;
    this.report = false;
    this.fleet = false;
    this.admin = false;
  }

  showMarketSub(){
    this.marketingSub = true;
    this.marketing = false;

  }
  showReport(){
    this.main = false;
    this.postload = false;
    this.load = false;
    this.account = false;
    this.marketing = false;
    this.report = true;
    this.fleet = false;
    this.admin = false;
  }

  showFleet(){
    this.main = false;
    this.postload = false;
    this.load = false;
    this.account = false;
    this.marketing = false;
    this.report = false;
    this.fleet = true;
    this.admin = false;
  }

  showAdmin(){
    this.main = false;
    this.postload = false;
    this.load = false;
    this.account = false;
    this.marketing = false;
    this.report = false;
    this.fleet = false;
    this.admin = true;
  }
}



