import { Component, NgModule, ElementRef, ViewChild, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { NgForm } from '@angular/forms';
import { FormGroup, FormBuilder, FormArray, Validators, FormControl } from '@angular/forms';
import { RequestLoad } from '../../../providers/request_load';
import { Trucks } from '../../../providers/truck';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import { ToastProvider } from '../../../providers/toast';
import { LoaderProvider } from '../../../providers/loader';
import { ManagePostLoadPage } from '../manage-post-load';
import { HomePage } from '../../home/home';
import { MapsAPILoader } from '@agm/core';
import { CommonHelper } from '../../../helpers/common';
import { AdminPrivilegeProvider } from '../../../providers/admin-privilege/admin-privilege';

/**
 * Generated class for the AddPostLoadPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-post-load',
  templateUrl: 'add-post-load.html',
})
export class AddPostLoadPage {

  submitted = false;
  paymentModes = ['To Pay', 'To Be Billed', 'Advance To Pay', 'Advance Pod'];
  materialsArray = [];
  types = [
    {name: 'OPEN', value: 'OPEN'},
    {name: 'CONTAINER', value: 'CONTAINER'},
    {name: 'TRAILER', value: 'TRAILER'},
  ];

  statuses = ['Pending', 'Cancel', 'Deal Final'];

  capacity = [];

  toDate = new Date().toISOString();
  data = {
    destinations: []
  };
  addForm: FormGroup;
  addMultipleDestinations: FormArray;

  
  count = 1;

  getTo = "";
  getUnload = "";

  DestinationsArray = [];
  toIndex;
  unloadingIndex;

  salesmanList = [];

  @ViewChild("searchFrom")
  public searchElementRefFrom: ElementRef;

  @ViewChild("searchLoadingPlace")
  public searchElementRefLoadingPlace: ElementRef;

  @ViewChild("searchTo")
  public searchElementRefTo: ElementRef;

  @ViewChild("searchUnloadingPlace")
  public searchElementRefUnloadingPlace: ElementRef;

  

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public formBuilder: FormBuilder,
    public requestLoad: RequestLoad,
    public trucks: Trucks,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    public admin: AdminPrivilegeProvider,
    ) {
      this.getSalesmanList();

      this.addForm = this.formBuilder.group({
        posting_date: [this.toDate],
        agency_name: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
        company_name: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
        despatch_date: [this.toDate],
        delivery_date: [this.toDate],
        from: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.maxLength(25)])],
        loading_place: [''],
        // addMultipleDestinations: this.formBuilder.array([this.createItem()]),
        // unloading_place: [''],
        material: [''],
        ton: [''],
        truck_type: [''],
        capacity: [''],
        rate: [''],
        payment_mode: [''],
        advance: [''],
        status: ['', Validators.compose([Validators.required])],
        to: [''],
        unloading_place: [''],
        salesman: ['', Validators.compose([Validators.required])],
        late_delivery_amount: [''],
        detention_amount: [''],
      });   

      this.getMaterials();

      

      setTimeout(() => {
        this.mapsAPILoader.load().then(() => {
          let formSearch = new google.maps.places.Autocomplete(this.searchElementRefFrom.nativeElement, {
            componentRestrictions: {"country": "IN"},
            types: []
          });
          formSearch.addListener("place_changed", () => {
            this.ngZone.run(() => {
              //get the place result
              let place: google.maps.places.PlaceResult = formSearch.getPlace();
              let components = CommonHelper.extract_components(place);
              let pre_city = CommonHelper.get_sublocality(components);
              console.log(components, pre_city);

              (<HTMLInputElement>document.getElementById('From')).value = pre_city;
              this.addForm.controls['from'].setValue(pre_city);
              // this.addForm.value.form = place.vicinity;
             
            });
          });
  
          let LoadingPlace = new google.maps.places.Autocomplete(this.searchElementRefLoadingPlace.nativeElement, {
            componentRestrictions: {"country": "IN"},
            types: []
          });
          LoadingPlace.addListener("place_changed", () => {
            this.ngZone.run(() => {
              //get the place result
              let place: google.maps.places.PlaceResult = LoadingPlace.getPlace();
              let components = CommonHelper.extract_components(place);
              let pre_city = CommonHelper.get_sublocality(components);
              console.log(components, pre_city);

              (<HTMLInputElement>document.getElementById('LoadingPlace')).value = pre_city;
              this.addForm.controls['loading_place'].setValue(pre_city);
            });
          }); 

          let toSearch = new google.maps.places.Autocomplete(this.searchElementRefTo.nativeElement, {
            componentRestrictions: {"country": "IN"},
            types: []
          });
          toSearch.addListener("place_changed", () => {
            this.ngZone.run(() => {
              //get the place result
              let place: google.maps.places.PlaceResult = toSearch.getPlace();
              let components = CommonHelper.extract_components(place);
              let pre_city = CommonHelper.get_sublocality(components);
              console.log(components, pre_city);

              (<HTMLInputElement>document.getElementById('TO')).value = pre_city;
              this.getTo = pre_city;
              
              // let addr = this.addForm.get('addMultipleDestinations') as FormArray;
              // let first = addr.value[this.toIndex];
              // first.to = place.vicinity;
                    
            });
          });
  
          let UnloadingPlace = new google.maps.places.Autocomplete(this.searchElementRefUnloadingPlace.nativeElement, {
            componentRestrictions: {"country": "IN"},
            types: []
          });
          UnloadingPlace.addListener("place_changed", () => {
            this.ngZone.run(() => {
              //get the place result
              let place: google.maps.places.PlaceResult = UnloadingPlace.getPlace();
              let components = CommonHelper.extract_components(place);
              let pre_city = CommonHelper.get_sublocality(components);
              console.log(components, pre_city);

              (<HTMLInputElement>document.getElementById('UnloadingPlace')).value = pre_city;
              this.getUnload = pre_city;

              // let addr = this.addForm.get('addMultipleDestinations') as FormArray;
              // let first = addr.value[this.unloadingIndex];
              // first.unloading_place = place.vicinity;
            });
          }); 
        });
      }, 2000);
  }

  get destinationsData(): FormArray { return this.addForm.get('addMultipleDestinations') as FormArray; }

  createItem(): FormGroup {
    return this.formBuilder.group({
      to: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      unloading_place: ['']

    });
  }

  getIndex(ind) {
    console.log(ind);

    this.toIndex = ind;
    this.unloadingIndex = ind;

  }

  getSalesmanList(){
    var agencyName = localStorage.getItem('agency_name');

    this.admin.getListByAgency(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;

      console.log("mainAccoutsList >>>>", dataList);

      dataList.forEach(element => {
        this.salesmanList.push(element.first_name);
      });
      console.log("salesmanList >>>>", this.salesmanList);


    }, err => {
      console.log(">>>>error", err);
    });
  }
  
  addTo(): void {
    if(this.count < 3) {

      // setTimeout(() => {
        this.mapsAPILoader.load().then(() => {
          let toSearch = new google.maps.places.Autocomplete(this.searchElementRefTo.nativeElement, {
            componentRestrictions: {"country": "IN"},
            types: []
          });
          toSearch.addListener("place_changed", () => {
            this.ngZone.run(() => {
              //get the place result
              let place: google.maps.places.PlaceResult = toSearch.getPlace();
              let components = CommonHelper.extract_components(place);
              let pre_city = CommonHelper.get_sublocality(components);
              console.log(components, pre_city);
              (<HTMLInputElement>document.getElementById('TO')).value = pre_city;
              
              let addr = this.addForm.get('addMultipleDestinations') as FormArray;
              let first = addr.value[this.toIndex];
              first.to = pre_city;
                    
            });
          });
  
          let UnloadingPlace = new google.maps.places.Autocomplete(this.searchElementRefUnloadingPlace.nativeElement, {
            componentRestrictions: {"country": "IN"},
            types: []
          });
          UnloadingPlace.addListener("place_changed", () => {
            this.ngZone.run(() => {
              //get the place result
              let place: google.maps.places.PlaceResult = UnloadingPlace.getPlace();
              let components = CommonHelper.extract_components(place);
              let pre_city = CommonHelper.get_sublocality(components);
              console.log(components, pre_city);
              // (<HTMLInputElement>document.getElementById('State')).value = place.address_components[0].long_name;
              (<HTMLInputElement>document.getElementById('UnloadingPlace')).value = pre_city;

              let addr = this.addForm.get('addMultipleDestinations') as FormArray;
              let first = addr.value[this.unloadingIndex];
              first.unloading_place = pre_city;
            });
          }); 
        });
      // }, 1000);
      this.addMultipleDestinations = this.addForm.get('addMultipleDestinations') as FormArray;
      this.addMultipleDestinations.push(this.createItem());
      this.count = this.count + 1;

      

    } else {
      this.toast.show({ 'message': 'Maximum Add only Three Destinations.' });
    }
  }

  removeTo(index) {
    if (index === 0) {
      this.toast.error({ 'message': 'Cannot remove this form.' });
    } else {
      this.addMultipleDestinations.removeAt(index);
      this.count = this.count - 1
    }
  }
  
  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  addToArray() {
    if(this.DestinationsArray.length < 3) {
      var obj = {"to": this.getTo, "unloading_place": this.getUnload};
      this.DestinationsArray.push(obj);
      this.addForm.get('to').setValue('');
      this.addForm.get('unloading_place').setValue('');
    } else {
      this.toast.show({ 'message': 'Maximum Add only Three Destinations.' });
    }
  }

  removeToArray(index) {
    this.DestinationsArray.splice(index, 1);
    console.log("array >>>", this.DestinationsArray);
  }
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad RequestLoadPage', this.toDate);
  }

   // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for Transporter Name list
  transporterName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchTransporterName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

   // Search query for Agency Name list
   agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  onSelectTruck(){
    console.log("Select truck >>>");
  }

  getMaterials(){
    this.trucks.getMaterials().subscribe((res: any) => {
      var data = res.json(); 
      data.results.forEach(r => {
        this.materialsArray.push(r.name);
      });
      console.log('Materials data >>>>>>> ', this.materialsArray);
    }, err => {
      console.log(">>>error", err);
    });
  }

  getCapacity(){
    var ttype = this.addForm.value.truck_type;
    this.trucks.getCapacityOfTruck().subscribe((res: any) => {
      var data = res.json(); 
      // console.log('res >>>>>>> ', res);
      this.capacity = [];
      data.results.forEach(r => {
        if (r.type == ttype) {
          r.capacity.forEach( a => {
            var obj = {
              "name": a.ton,
              "value": a.ton,
            }
            this.capacity.push(obj);
          });
        }
      });
      console.log('capacity >>>>>>> ', this.capacity);
    }, err => {

      console.log(">>>error", err);
    });
  }

  // Search query for truck no list
  companyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.requestLoad.searchCompany(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  save(isValid) {

    this.submitted = true;
    console.log(" isValid >>>>>", isValid);
    console.log(">>>>>>>>>>COntrols", this.addForm);

    if (isValid) {
      const getFromData = this.addForm.value;

      if(getFromData.to && getFromData.unloading_place) {
        var addMore = {"to": this.getTo, "unloading_place": this.getUnload};
        this.DestinationsArray.push(addMore);
      }

      var obj = {
        "posting_date": getFromData.posting_date,
        "agency_name": getFromData.agency_name.agency_name,
        "company_name": getFromData.company_name.company_name,
        "despatch_date": getFromData.despatch_date,
        "delivery_date": getFromData.delivery_date,
        "from": getFromData.from,
        "loading_place": getFromData.loading_place,
        // "destinations": getFromData.addMultipleDestinations,
        "destinations": this.DestinationsArray,
        // // "unloading_place": getFromData.unloading_place,
        "material": getFromData.material,
        "ton": getFromData.ton,
        "truck_type": getFromData.truck_type,

        "capacity": getFromData.capacity,
        "rate": getFromData.rate,
        "payment_mode": getFromData.payment_mode,
        "advance": getFromData.advance,
        "status": getFromData.status,
        "salesman": getFromData.salesman,

        "late_delivery_amount": getFromData.late_delivery_amount,
        "detention_amount": getFromData.detention_amount,
      }
      console.log(" obj >>>>>", obj);
      this.loader.show();
      this.requestLoad.addPostLoad(obj).subscribe(res => {
        this.loader.hide();
        this.addForm.reset();
        this.toast.show({ 'message': 'Added successfully.' });
        this.navCtrl.setRoot(ManagePostLoadPage);
      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>Err", err);
      });
    }
      
  }

}



