import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { LoaderProvider } from '../../providers/loader';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { RequestLoad } from '../../providers/request_load';
import { EditAdvanceAmountPage } from './edit-advance-amount/edit-advance-amount';
import { AddAdvanceAmountPage } from './add-advance-amount/add-advance-amount';
import { HomePage } from '../home/home';
import { Accounts } from '../../providers/accounts';
import * as moment from 'moment';

/**
 * Generated class for the ManageAdvanceAmountPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-advance-amount',
  templateUrl: 'manage-advance-amount.html',
})
export class ManageAdvanceAmountPage {

  dataList = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public accounts: Accounts,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider
  ) {
    this.getAdvanceAmountList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageAdvanceAmountPage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Action',
    //   buttons: [{
    //     text: 'Add',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(AddAdvanceAmountPage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(data, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit',
        icon: 'ios-create',
        handler: () => {
          this.navCtrl.push(EditAdvanceAmountPage, {"data": data });
        }
      }, {
        text: 'Remove',
        icon: 'ios-trash',
        handler: () => {
          this.deleteApi(data);
        }
      }]
    });
    await actionSheet.present();
  }

  getAdvanceAmountList() {
    this.loader.show();
    this.accounts.getAdvanceAmountList().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      this.dataList = data.results;

      this.dataList.forEach(element => {
        element.date_frmt = moment(element.date).format('YYYY-MM-DD');
      });
      
      console.log('Data >>>>>>>', this.dataList);
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }


  deleteApi(data) {
    console.log("Data  >>>", data);
    var load_id = data._id;
    console.log("id  >>>", load_id);

    this.loader.show();

    // Delete Deal Final Record
    this.accounts.deleteAdvanceAmount(load_id).subscribe((res: any) => {

      var delDealNo = data.deal_no;
      var delDocumentType = "DFADV";
      var delPartyCategory = data.party_category;

      // Delete Advance Amount Accounts Ledger
      this.accounts.deleteAdvanceAmountAccountsLedger(delDealNo, delDocumentType, delPartyCategory).subscribe((res: any) => {


        // // Delete Rewards Ledger Records
        // this.requestLoad.deleteRewardsLedger(data.sr_no).subscribe((res: any) => {

        this.loader.hide();
        this.navCtrl.push(ManageAdvanceAmountPage);
      //   }, err => {
      //     this.loader.hide();
      //     console.log("error");
      //   });
      }, err => {
        this.loader.hide();
        console.log("error");
      });
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



