import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { LoaderProvider } from '../../providers/loader';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { RequestLoad } from '../../providers/request_load';
import { AddPostLoadPage } from './add-post-load/add-post-load';
import { EditPostLoadPage } from './edit-post-load/edit-post-load';
import { HomePage } from '../home/home';
import { TrucksAvailabilityPage } from '../trucks-availability/trucks-availability';
import { DealFinalPage } from '../manage-deal-final/deal-final/deal-final';
import { AddDealFinalNewPage } from '../manage-deal-final-new/add-deal-final-new/add-deal-final-new';

/**
 * Generated class for the ManagePostLoadPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-post-load',
  templateUrl: 'manage-post-load.html',
})
export class ManagePostLoadPage {

  dataList = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider
  ) {
    this.getPostLoadList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManagePostLoadPage');
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Add Post Load',
        icon: 'add',
        handler: () => {
          this.navCtrl.push(AddPostLoadPage);
        }
      }]
    });
    await actionSheet.present();
  }

  gotoAdd() {
    this.navCtrl.push(AddPostLoadPage);
  }

  async presentActionSheet(data, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Trucks Available',
        icon: 'cube',
        handler: () => {
          this.navCtrl.push(TrucksAvailabilityPage);
        }
      }, {
        text: 'Edit Post Load',
        icon: 'ios-create',
        handler: () => {
          this.navCtrl.push(EditPostLoadPage, {"data": data });
        }
      // }, {
      //   text: 'Deal Final',
      //   icon: 'checkmark',
      //   handler: () => {
      //     this.navCtrl.push(DealFinalPage, {"postData": data });
      //   }
      }, {
        text: 'Deal Final',
        icon: 'checkmark',
        handler: () => {
          this.navCtrl.push(AddDealFinalNewPage, {"postData": data });
        }
      },{
        text: 'Remove',
        icon: 'ios-trash',
        handler: () => {
          this.deleteApi(data);
        }
      }]
    });
    await actionSheet.present();
  }

  getPostLoadList() {
    this.loader.show();
    this.requestLoad.getPostLoadList().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      this.dataList = data.results;

      this.dataList.forEach(r => {
        var destinationsArray = [];
        r.destinations.forEach(d => {
          destinationsArray.push(d.to);
        });

        r.destArray = destinationsArray;
        r.status = r.status ? r.status: "Pending";
      });
      console.log('Data >>>>>>>', this.dataList);
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

  deleteApi(id) {
    var load_id = id._id;
    console.log("id  >>>", load_id);
    this.requestLoad.deletePostLoadList(load_id).subscribe((res: any) => {
      this.navCtrl.push(ManagePostLoadPage);
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



