import { Component, Input } from '@angular/core';
import { Traffic } from '../../providers/traffic';
import { FleetManagement } from '../../providers/fleet_management';
import { ToastProvider } from '../../providers/toast';

/**
 * Generated class for the TrafficOpsReceivableAdvanceComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'traffic-ops-receivable-advance',
  templateUrl: 'traffic-ops-receivable-advance.html'
})
export class TrafficOpsReceivableAdvanceComponent {

  text: string;
  saveData:any={};
  paymentMode:any;
  paymentTypes:any;
  isChange:any=false;
  receivable_advance:any={
    diesel:{}
  };
  @Input() data;
  constructor(
    public traffic: Traffic,
    public toast: ToastProvider,
    public fleetManagement: FleetManagement,
    ) {
    console.log('Hello TrafficOpsReceivableAdvanceComponent Component');
    this.text = 'Hello World';
    this.data=JSON.parse(localStorage.data)[Number(localStorage.index)];
    this.saveData=JSON.parse(localStorage.data)[Number(localStorage.index)];

    this.saveData.receivable_advance= this.saveData.receivable_advance.map((e)=>{ 
      e.final_amount=this.saveData.transporter_freight-e.cheque_amount;
      this.saveData.transporter_freight=e.final_amount
      return e;
    });

    if(this.saveData.receivable_advance[0]){
          this.receivable_advance=this.saveData.receivable_advance[0];
    }

    this.fleetManagement.getPaymentMode().subscribe((res:any) => {
      console.log("Okay >>>", JSON.parse(res._body));
      this.paymentMode=JSON.parse(res._body).results;

    }, err => {
      this.toast.error({ 'message': 'Something went wrong.' });
    });

    this.fleetManagement.getPaymentType().subscribe((res:any) => {
      console.log("Okay >>>", JSON.parse(res._body));
      this.paymentTypes=JSON.parse(res._body).results;
    }, err => {
      this.toast.error({ 'message': 'Something went wrong.' });
    });

  }  

  save(){
      if(!this.isChange){
              this.saveData.receivable_advance[0]=this.receivable_advance;
      }else{
              this.saveData.receivable_advance.push(this.receivable_advance);
      }
    this.traffic.updateReceivableCash(this.saveData._id, this.saveData).subscribe(res => {
      this.toast.show({ 'message': 'Updated successfully.' });
      console.log("Okay >>>", res);

      this.saveData.receivable_advance.map((e)=>{ 
      e.final_amount=this.saveData.transporter_freight-e.cheque_amount;
      this.saveData.transporter_freight=e.final_amount
      return e;
    });
      


    }, err => {
      this.toast.error({ 'message': 'Something went wrong.' });
      console.log(">>>>>Err", err);
    });

  }

  change(){
    
  }

  addNew(){
   this.isChange=true;
   // this.saveData={}
   // this.saveData.receivable_advance;
   this.receivable_advance={
     diesel:{}
   };
  }

}




