  addCalculation() {
    var getFormData = this.addForm.value;

    var rateCalculation = getFormData.rate_calculation;
    var truckRates = getFormData.truck_rates ? getFormData.truck_rates: 0;
    var truckWeight = getFormData.truck_weight ? getFormData.truck_weight: 0;
    var actualWeight = getFormData.actual_weight ? getFormData.actual_weight: 0;
    var freightTotal = 0

    // calculations = ['Fixed', 'Actual Weight', 'Truck capacity'];
    
    if (rateCalculation == "Fixed"){
      freightTotal = truckRates;
    }

    if (rateCalculation == "Actual Weight"){
      freightTotal = truckRates * actualWeight;
    }

    if (rateCalculation == "Truck Capacity"){
      freightTotal = truckRates * truckWeight;
    }

    this.addForm.controls['freight'].setValue(freightTotal);
  }
  
  getRequestLoadById() {
    this.loader.show();
    this.requestLoad.getRequestLoadByID(this.addForm.value.transporter_ticket_no).subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      var dataList = data.results;
      if(dataList[0]){
        this.addForm.controls['truck_no'].setValue(dataList[0].truck_no ? dataList[0].truck_no: "");
        this.addForm.controls['transporter_salesman'].setValue(dataList[0].salesman ? dataList[0].salesman: "");
        this.addForm.controls['transporter_name'].setValue(dataList[0].transporter_name ? dataList[0].transporter_name: "");
        this.requestLoadID = dataList[0]._id;
      }
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });  
  }

  getPostLoadById() {
    this.loader.show();
    this.requestLoad.getPostLoadByID(this.addForm.value.company_ticket_no).subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      var dataList = data.results;

      if(dataList[0]){
        this.addForm.controls['company_salesman'].setValue(dataList[0].salesman ? dataList[0].salesman: "");
        this.addForm.controls['company_name'].setValue(dataList[0].company_name ? dataList[0].company_name: "");
        this.addForm.controls['from'].setValue(dataList[0].from ? dataList[0].from: "");
        this.addForm.controls['loading_place'].setValue(dataList[0].loading_place ? dataList[0].loading_place: "");
        if (dataList[0].destinations[0]) {
          this.addForm.controls['to'].setValue(dataList[0].destinations[0].to ? dataList[0].destinations[0].to: "");
          this.addForm.controls['unloading_place'].setValue(dataList[0].destinations[0].unloading_place ? dataList[0].destinations[0].unloading_place: "");
        }
        this.addForm.controls['rates_company'].setValue(dataList[0].rate ? dataList[0].rate: 0);
        this.addForm.controls['advance_company'].setValue(dataList[0].advance ? dataList[0].advance: 0);
        this.addForm.controls['weight_company'].setValue(dataList[0].ton ? dataList[0].ton: 0);
        this.addForm.controls['truck_weight'].setValue(dataList[0].ton ? dataList[0].ton: 0);
        this.postLoadID = dataList[0]._id;
      }
      
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

