import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { LoaderProvider } from '../../providers/loader';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { AddEmployeePage } from '../admin-privileges/add-employee/add-employee';
import { EditEmployeePage } from '../edit-employee/edit-employee';
import { HomePage } from '../home/home';

/**
 * Generated class for the ManageEmployeesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-employees',
  templateUrl: 'manage-employees.html',
})
export class ManageEmployeesPage {
  employeesList = [];
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider
  ) {
    this.getEmployeesList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageEmployeesPage');
  }

  getEmployeesList() {
    this.loader.show();
    this.admin.getEmployeesList().subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.employeesList = data.results;
      console.log("Data  >>>", this.employeesList);
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }


  Home(){
    this.navCtrl.setRoot(HomePage);
  }


  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Employee',
    //     icon: 'add',
    //     handler: () => {
    //       // AdminPrivileges
          this.navCtrl.push("AddEmployeePage");
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }


  async presentActionSheet(employeeData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit Employee',
        icon: 'create',
        handler: () => {
          this.navCtrl.push(EditEmployeePage, {data: employeeData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteApi(employeeData);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteApi(id) {
    var employee_id = id._id;
    console.log("id  >>>", employee_id);
    this.admin.deleteEmployee(employee_id).subscribe((res: any) => {
      this.navCtrl.push("ManageEmployeesPage");
    }, err => {
      console.log("error");
    });
  }

}



