import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { RequestLoad } from '../../../../providers/request_load';
import { LoaderProvider } from '../../../../providers/loader';
import { ToastProvider } from '../../../../providers/toast';
import { HomePage } from '../../../home/home';

/**
 * Generated class for the EditAccountTypePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-account-type',
  templateUrl: 'edit-account-type.html',
})
export class EditAccountTypePage {

  form: FormGroup;
  details: any;
  submitted = false;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {

    this.details = this.navParams.get('data');
    console.log('>>>Details', this.details);

    this.form = this.fb.group({
      name: ['', Validators.required],
      field: ['']
    });

    this.form.controls['name'].setValue(this.details.name);
    // this.form.controls['field'].setValue(this.details.field);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddPaymentModePage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  update(isValid) {
    this.submitted = true;
    if(isValid) {
      const val = this.form.value;
      const obj = {
        name: val.name,
        // field: val.field
      };
  
      this.loader.show();
      this.requestLoad.editAccountType(obj, this.details._id).subscribe(res => {
        this.loader.hide();
        this.toast.show({ 'message': 'Updated Successfully.' });
        this.navCtrl.push('ManageAccountTypesPage');
      }, err => {
        this.loader.hide();
        console.log('>>>>err', err);
      });
    }
  }
}


