import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { RequestLoad } from '../../providers/request_load';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import * as moment from 'moment';

/**
 * Generated class for the TrafficOperationsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-traffic-operations',
  templateUrl: 'traffic-operations.html',
})
export class TrafficOperationsPage {

  categories = [
    'Deal Final', 
    'Truck Verification', 
    'Bank Details / Loading Status', 
    'Receivable Advance',
    'Pending POD - T',
    'Pending POD - C',
    'Receivable Balance',
    'Commission'
  ];

  activeTab = this.categories[0];
  deal:any;
  isShow=false;
  pendingVerification:any;
  pendingAdvance:any;
pendingBalance=[];
pendingRcvBalance=[];
podSubmit:any;
podRecive:any;

pendingAmnt:any;
pendingRcvAmnt:any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider
  ) {
    localStorage.removeItem("quality");

  }


     ionViewDidEnter(){
          console.log('ionViewDidLoad TrafficOperationsPage');
    // this.deal = this.navParams.get('data');
    localStorage.removeItem("quality");  
    this.getDealFinalList();
  
}
  ionViewDidLoad() {
    console.log('ionViewDidLoad TrafficOperationsPage');
    // this.deal = this.navParams.get('data');
    localStorage.removeItem("quality");
    
    // this.getDealFinalList();
  }

  home() {
    this.navCtrl.setRoot(HomePage);
  }

  setActiveTab(c) {
    this.activeTab = c;
  }

  getDealFinalList() {
    console.log("getting <<<");
    this.loader.show();
    this.requestLoad.getDealFinalNewListSorted().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      data.results.forEach(d => {
        d.deal_date = moment(d.deal_date).format('DD/MM/YYYY');
      });
      this.deal = data.results;

      console.log('Data >>>>>>>', this.deal);

var tmp=[];
var tmp2=[];
this.deal=this.deal.map((e)=>{
        // tmp.push()   
        e.tmp=[];
        e.traffic_operations.bank_details.forEach((k)=>{
        // tmp.push(k.bank_amount) 
                if(!isNaN(k.bank_amount)){          
        e.tmp.push(k.bank_amount)
      }
        });

        e.amountTotal=e.tmp.reduce((a, b) => a + b, 0)
        return e;
      });
this.deal=this.deal.map((e)=>{
        // tmp.push()   
        e.tmp2=[];
        e.receivable_advance.forEach((k)=>{
        // tmp.push(k.bank_amount)   
        if(!isNaN(k.cheque_amount)){          
        e.tmp2.push(k.cheque_amount)
        }
        });

        e.amountTotal_recive=e.tmp2.reduce((a, b) => a + b, 0)
        return e;
      });


      localStorage.setItem("data",JSON.stringify(this.deal));

        this.pendingVerification=this.deal.filter((e)=>{
        return !e.traffic_operations.document_verification;
      });

        this.podSubmit=this.deal.filter((e)=>{
        return !e.traffic_operations.pod_c_uploaded;
        });
        this.podRecive=this.deal.filter((e)=>{
        return !e.traffic_operations.pod_t_uploaded;
        });
        
        this.deal=this.deal.map((e)=>{
          if(e.transporter_freight-e.receivable_advance.cheque_amount>0){
            this.pendingBalance.push(e.transporter_freight-e.receivable_advance.cheque_amount);
          }
          if(e.transporter_freight-e.traffic_operations.bank_details.bank_amount>0){
            this.pendingRcvBalance.push(e.transporter_freight-e.traffic_operations.bank_details.bank_amount);
          }
          return e;
        });
        console.log(this.pendingBalance);
        this.pendingAmnt=this.pendingBalance.reduce((a, b) => a + b, 0);
        this.pendingRcvAmnt=this.pendingRcvBalance.reduce((a, b) => a + b, 0);

      this.isShow=true;

    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

}



