import { Component, Input } from '@angular/core';
import { Traffic } from '../../providers/traffic';
import { ToastProvider } from '../../providers/toast';

/**
 * Generated class for the TrafficOpsPendingPodTComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'traffic-ops-pending-pod-t',
  templateUrl: 'traffic-ops-pending-pod-t.html'
})
export class TrafficOpsPendingPodTComponent {

  text: string;
  datas: any;

  @Input() data;

  constructor(
     public toast: ToastProvider,
    public traffic: Traffic,
    ) {
    console.log('Hello TrafficOpsPendingPodTComponent Component');
    this.datas=JSON.parse(localStorage.data)[Number(localStorage.index)];
    this.text = 'Hello World';
  }


    save() {
    this.traffic.upadtePOD(this.datas._id, this.datas).subscribe(res => {
      this.toast.show({ 'message': 'Updated successfully.' });
      // console.log("Okay >>>", this.datas);
    }, err => {
      this.toast.error({ 'message': 'Something went wrong.' });
      console.log(">>>>>Err", err);
    });
  }


}



