import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { LoaderProvider } from '../../providers/loader';
import { Observable } from 'rxjs';
import { Trucks } from '../../providers/truck';
import { FormGroup, FormBuilder } from '@angular/forms';
import { HomePage } from '../home/home';

/**
 * Generated class for the ManageTruckListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-truck-list',
  templateUrl: 'manage-truck-list.html',
})
export class ManageTruckListPage {

  lists = [];
  truckListFrom: FormGroup;
  show = false;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    public trucks: Trucks,
    public fb: FormBuilder
    ) {

      this.truckListFrom = this.fb.group({
        truck_no: ['']
      });
  }

  ionViewDidLoad() {
    // this.getList();
    console.log('ionViewDidLoad ManageTransportPage');
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  onSelectTruck(e){
    console.log(">>>>>eeeee", e);
    this.loader.show();
    this.admin.getTruckData(e.truck_no).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.lists = data.result;
      this.show = true;
      console.log(">>>>>>>truckList", this.lists);
    }, err => {
      this.loader.hide();
      console.log("error", err);
    });
  }

  // getList() {
  //   this.loader.show();
  //   this.admin.getTruckList().subscribe((res: any) => {
  //     this.loader.hide();
  //     let data = res.json();
  //     this.lists = data.result;
  //   }, err => {
  //     this.loader.hide();
  //     console.log("error", err);
  //   });
  // }

  edit(truckData) {
    console.log(">>>>Data", truckData);
    this.navCtrl.push('EditTruckDetailsPage', {data: truckData});
  }

  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Truck',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push('AdminPrivilegesAddTruckPage');
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(truckData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit truck list',
        icon: 'create',
        handler: () => {
          this.navCtrl.push('EditTruckDetailsPage', {data: truckData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteApi(truckData);
        }
      }]
    });
    await actionSheet.present();
  }


  deleteApi(id) {
    var truck_id = id._id;
    console.log("id  >>>", truck_id);
    this.admin.deleteTruck(truck_id).subscribe((res: any) => {
      this.navCtrl.push('ManageTruckListPage');
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



