import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { SettingPage } from '../setting/setting';
import { LoaderProvider } from '../../providers/loader';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { HomePage } from '../home/home';

/**
 * Generated class for the ManageCompanyListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-company-list',
  templateUrl: 'manage-company-list.html',
})
export class ManageCompanyListPage {

  lists = [];
  citiesList = [];
  public searchControl: FormControl;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public admin: AdminPrivilegeProvider,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider
    ) {
  }

  ionViewDidLoad() {
    // this.getList();
    this.getCitiesList();

  }

  ngOnInit() {
    this.searchControl = new FormControl(); 
  }

  getCitiesList() {
    this.loader.show();
    this.admin.getCompaniesCitiesList().subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.citiesList = data.results;
      console.log("citiesList >>>>>>", this.citiesList);
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  getCpyDetails(c) {
    this.loader.show();
    this.admin.getCompanyListByName(c).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.lists = data.results;
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  // getList() {
  //   this.loader.show();
  //   this.admin.getCompanyList().subscribe((res: any) => {
  //     this.loader.hide();
  //     let data = res.json();
  //     this.lists = data.results;
  //   }, err => {
  //     this.loader.hide();
  //     console.log("error");
  //   });
  // }

  edit(companyData) {
    this.navCtrl.push('EditCompanyDetailsPage', {data: companyData});
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  // Search query for Company  list
  searchCompanyAndCity = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.admin.searchCompaniesAndCity(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }
  

  getCityDetails(c) {
    this.loader.show();
    this.admin.getCompanyListByCity(c.city).subscribe((res: any) => {
      this.loader.hide();
      let data = res.json();
      this.lists = data.results;
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  onSelect(e){
    console.log("e >>>>", e);

    if(e.name == "city") {
      this.getCityDetails({"city": e.value})
    }
    if(e.name == "company_name") {
      this.getCpyDetails(e.value);
    }
  }

  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Company',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push('AddCompanyPage');
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(companyData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit company list',
        icon: 'create',
        handler: () => {
          this.navCtrl.push('EditCompanyDetailsPage', {data: companyData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteApi(companyData);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteApi(id) {
    var company_id = id._id;
    console.log("id  >>>", company_id);
    this.admin.deleteCompany(company_id).subscribe((res: any) => {
      this.navCtrl.push('ManageCompanyListPage');
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



