import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HomePage } from '../home/home';
import { AddDocumentsMasterPage } from './add-documents-master/add-documents-master';
import { EventsService } from '../../providers/event.service';
import { ToastProvider } from '../../providers/toast';
import { LoaderProvider } from '../../providers/loader';
import { FleetManagement } from '../../providers/fleet_management';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { Trucks } from '../../providers/truck';
import { EditAttachDocumentsPage } from './edit-attach-documents/edit-attach-documents';

/**
 * Generated class for the AttachDocumentsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-attach-documents',
  templateUrl: 'attach-documents.html',
})
export class AttachDocumentsPage {

  toDate = new Date().toISOString();
  attachmentsFlag = true;
  documentsMatruesterFlag = false;
  documentsList= [];
  editDocumentForm: FormGroup;
  addAttachment: FormGroup;
  activeList = false;


  listFlag = true;
  editFlag = false;

  fileList = [];
  file;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public fleetManagement: FleetManagement,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public events: EventsService,
    public trucks: Trucks,
  ) {

    this.editDocumentForm = this.fb.group({ 
      name: [''],
    });

    this.addAttachment = this.fb.group({ 
      truck_no: [''],
      start_date: [this.toDate],
      documentname: [''],
      end_date: [this.toDate],
      file: [''],
    });

    var fromDocument = this.navParams.get('flag');
    if(fromDocument == 1) {
      this.onDocumentsMaster();
    }
    
    
    if(fromDocument == 0) {
      this.addAttachment.controls['truck_no'].setValue(this.navParams.get('truck_no'));
      this.getOldtrucksRecords();
    } else {
      this.getDocumentsMaster();
    }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AttachDocumentsPage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }

  onAttachments() {
    console.log("onAttachments >>>>");
    this.attachmentsFlag = true;
    this.documentsMatruesterFlag = false;
  }

  onDocumentsMaster() {
    console.log("onDocumentsMaster >>>>"); 
    this.attachmentsFlag = false;
    this.documentsMatruesterFlag = true;

  }

  getDocumentsMaster() {
    this.loader.show();
    this.documentsList = [];
    this.fleetManagement.listDocumentsMaster().subscribe((res: any) => {
      var data = res.json(); 
      this.documentsList = data.results;
      this.loader.hide();
    }, err => {
      console.log("err >>>", err);
    });
  }

  addDocumentMaster() {
    this.navCtrl.push(AddDocumentsMasterPage);
  }

  editDocumentMaster(data) {
    this.listFlag = false;
    this.editFlag = true;
  }

  deleteDocumentMaster(data) {
    this.listFlag = true;
    this.editFlag = false;   
    
    this.loader.show();
    this.fleetManagement.deleteDocumentsMaster(data._id).subscribe((res: any) => {
      var data = res.json(); 
      this.loader.hide();

      this.getDocumentsMaster();
    }, err => {
      console.log("err >>>", err);
    });
  }

  CancelDocumentMaster() {
    this.listFlag = true;
    this.editFlag = false;
  }

  updateDocumentMaster(data) {
    this.listFlag = true;
    this.editFlag = false;

    this.loader.show();

    var obj = {
      name: this.editDocumentForm.value.name
    }
    this.fleetManagement.updateDocumentsMaster(data._id, obj).subscribe((res: any) => {
      var data = res.json(); 
      this.loader.hide();

      this.getDocumentsMaster();
    }, err => {
      console.log("err >>>", err);
    });
    
  }

  // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  fetch() {
    this.activeList = true;
    this.loader.show();
    const truck_no = this.addAttachment.value.truck_no.truck_no ? this.addAttachment.value.truck_no.truck_no: this.addAttachment.value.truck_no;

    this.fleetManagement.getfilesList(truck_no).subscribe((res: any)  => {

      this.fileList = res.json(); // existing files

      // add existing files to documents master
      this.documentsList.forEach(d => {
        this.fileList.forEach(f => {
          if (d.name === f.documentname) {
            d.files = f.files;

            if(d.files[0]) {
              d.files.forEach(at => {
                var attachdocument_row = at.attachdocument.split('uploads/');
                at.attachdocument_format = attachdocument_row[1];
              });
            }
          }
        });
      });

      this.loader.hide();

    }, err => {
        // on error
    });
  }

  onSelectCSV(event) {
    this.file = event.target.files[0];
    console.log('file >>>>>>>', this.file);
  }

  save(data) {
    // data.disabled = true;

    console.log('data >>>>>>>', data);

    const getFromData = this.addAttachment.value;

    

    const d = new FormData();
    d.append('file', this.file);
    d.append('startdate', getFromData.start_date);
    d.append('truck_no', getFromData.truck_no);
    d.append('enddate', getFromData.end_date);
    d.append('documentname', data.name);

    this.fleetManagement.addAttachDocuments(d).subscribe(res => {
      this.toast.error({ 'message': 'Uploaded..!' });
      this.fetch();
      this.addAttachment.get('file').reset();
      // data.disabled = false;
    }, err => {
      console.log('err   >>>', err);
      this.toast.error({ 'message': 'Something went wrong..!' });
    });

  }

  editDocument(d) {
    d.truck_no = this.addAttachment.value.truck_no.truck_no ? this.addAttachment.value.truck_no.truck_no: this.addAttachment.value.truck_no;
    this.navCtrl.push(EditAttachDocumentsPage, {data: d});
  }

  deleteDocument(f) {
    if (confirm('Are you sure you want to delete?')) {
      const data = {
        id: f.parent_id,
        document_id: f._id
      };
      this.fleetManagement.deleteDocument(data).subscribe(res => {
        this.fetch();
      }, err => {
        this.toast.error({ 'message': 'Something went wrong..!' });
      });
    }
  }

  getOldtrucksRecords() {
    this.activeList = true;
    this.documentsList = [];
    this.loader.show();
    const truck_no = this.addAttachment.value.truck_no.truck_no ? this.addAttachment.value.truck_no.truck_no: this.addAttachment.value.truck_no;

    this.fleetManagement.listDocumentsMaster().subscribe((res: any) => {
      var data = res.json(); 
      this.documentsList = data.results;

      this.fleetManagement.getfilesList(truck_no).subscribe((res: any)  => {
  
        this.fileList = res.json(); // existing files
  
        // add existing files to documents master
        this.documentsList.forEach(d => {
          this.fileList.forEach(f => {
            if (d.name === f.documentname) {
              d.files = f.files;
  
              if(d.files[0]) {
                d.files.forEach(at => {
                  var attachdocument_row = at.attachdocument.split('uploads/');
                  at.attachdocument_format = attachdocument_row[1];
                });
              }
            }
          });
        });
  
        this.loader.hide();

          }, err => {
            // on error
        });

    }, err => {
        // on error
    });  
  }


}



