import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HomePage } from '../home/home';
import { Accounts } from '../../providers/accounts';
import { ToastProvider } from '../../providers/toast';
import { LoaderProvider } from '../../providers/loader';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { Trucks } from '../../providers/truck';

/**
 * Generated class for the ManageAccountsLedgerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-accounts-ledger',
  templateUrl: 'manage-accounts-ledger.html',
})
export class ManageAccountsLedgerPage {
  accountsName = [];
  branchName = [];
  fetchForm: FormGroup;
  tableList = false;
  totalCredit = 0;
  totalDebit = 0;
  accountsLedgerList = [];
  closingAccount = 0;
  agencyName;
  toDate = new Date().toISOString();
  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public accounts: Accounts,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public formBuilder: FormBuilder,
    public trucks: Trucks
  ) {

    this.fetchForm = this.formBuilder.group({
      account_name: [''],
      to_date: [this.toDate],
      from_date: [this.toDate],
      agency_name: [''],
      branch_name: [''],
      truck_no: ['']
    });

    this.agencyName = localStorage.getItem('agency_name');

    this.fetchForm.controls['agency_name'].setValue(this.agencyName);

    this.getAccountsLedgerBranchName();
    this.getLedgerAccountsName();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageAccountsLedgerPage');
  }


  home(){
    this.navCtrl.setRoot(HomePage);
  }

  getLedgerAccountsName(){
    this.loader.show();
    this.accounts.getAccountsLedgerAccountsName().subscribe( (res: any) => {
      this.loader.hide();
      var data = res.json();
      this.accountsName = data.results;
      console.log("Accounts Name >>>>>", this.accountsName);
    }, err => {
      this.loader.hide();
      // this.toast.error({ 'message': 'Something went wrong while.' });
      console.log(">>>>>Err", err);
    });

  }

  getAccountsLedgerBranchName() {
    // this.loader.show();
    this.accounts.getAccountsLedgerBranchName().subscribe( (res: any) => {
      // this.loader.hide();
      var data = res.json();
      this.branchName = data.results;
      console.log("Accounts Name >>>>>", this.branchName);
    }, err => {
      // this.loader.hide();
      // this.toast.error({ 'message': 'Something went wrong while.' });
      console.log(">>>>>Err", err);
    });

  }

  // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  fetch() {
    this.accountsLedgerList = [];
    var accountName = this.fetchForm.value.account_name;
    var fromDate = this.fetchForm.value.from_date;
    var toDate = this.fetchForm.value.to_date;

    console.log("Data >>>>>", accountName, fromDate, toDate);

    var filters = {
      account_name: '',
      from_date: '',
      to_date: '',
      branch_name: '',
      truck_no: ''
    };

    if (this.fetchForm.value.account_name){
      filters.account_name = this.fetchForm.value.account_name;
    } else {
      filters.account_name = '';
    }

    if (this.fetchForm.value.branch_name){
      filters.branch_name = this.fetchForm.value.branch_name;
    } else {
      filters.branch_name = '';
    }

    if (this.fetchForm.value.truck_no){
      filters.truck_no = this.fetchForm.value.truck_no.truck_no;
    } else {
      filters.truck_no = '';
    }
    if (this.fetchForm.value.from_date){
      filters.from_date = this.fetchForm.value.from_date;
    } else {
      filters.from_date = '';
    }

    if (this.fetchForm.value.to_date){
      filters.to_date = this.fetchForm.value.to_date;
    } else {
      filters.to_date = '';
    }

    

    this.loader.show();
    this.tableList = true;
    this.accounts.getFetchAccountsLedger(filters).subscribe( (res: any) => {
      this.loader.hide();
      var data = res.json();
      console.log("Accounts >>>>>", data);

      this.totalCredit = data.total_credit;
      this.totalDebit = data.total_debit;

      var totalClosing = 0;
      
      data.results.forEach(element => {
        element.date_format = moment(element.voucher_date).format('DD/MM/YYYY');
        
        if(element.debit_amount){
          totalClosing = totalClosing + element.debit_amount;
        } else {
          totalClosing = totalClosing - element.credit_amount;
        }

        this.closingAccount = Math.abs(totalClosing);
        element.closing_list = Math.abs(totalClosing);
        this.accountsLedgerList.push(element);
      });

    }, err => {
      this.loader.hide();
      this.toast.error({ 'message': 'Something went wrong while.' });
      console.log(">>>>>Err", err);
    });


  }
}



