import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { LoaderProvider } from '../../providers/loader';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { RequestLoad } from '../../providers/request_load';
import { AddAccountMasterPage } from './add-account-master/add-account-master';
import { EditDealFinalPage } from '../manage-deal-final/edit-deal-final/edit-deal-final';
import { EditAccountMasterPage } from './edit-account-master/edit-account-master';

/**
 * Generated class for the ManageAccountsMasterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-accounts-master',
  templateUrl: 'manage-accounts-master.html',
})
export class ManageAccountsMasterPage {

  dataList = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider
  ) {
    this.getAccountsMasterList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageAccountsMasterPage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Action',
    //   buttons: [{
    //     text: 'Add',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(AddAccountMasterPage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(data, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit',
        icon: 'ios-create',
        handler: () => {
          this.navCtrl.push(EditAccountMasterPage, {"data": data });
        }
      }, {
        text: 'Remove',
        icon: 'ios-trash',
        handler: () => {
          this.deleteApi(data);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteApi(id) {
    var account_id = id._id;
    console.log("id  >>>", account_id);
    this.loader.show();
    this.admin.deleteAccountsMaster(account_id).subscribe((res: any) => {
      this.loader.hide();
      this.navCtrl.push(ManageAccountsMasterPage);
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

  getAccountsMasterList() {
    this.loader.show();
    this.admin.getAccountsMasterList().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      this.dataList = data.results;

      console.log('Data >>>>>>>', this.dataList);
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

}



