import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { RequestLoad } from '../../../../providers/request_load';
import { LoaderProvider } from '../../../../providers/loader';
import { ToastProvider } from '../../../../providers/toast';

/**
 * Generated class for the AddPaymentTypePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-payment-type',
  templateUrl: 'add-payment-type.html',
})
export class AddPaymentTypePage {

  addpaymentTypeForm: FormGroup;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public requestLoad: RequestLoad,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {
    this.addpaymentTypeForm = this.fb.group({
      name: [''],
      description: ['']
    });
  }

  ionViewDidLoad() {
  }

  save() {
    const val = this.addpaymentTypeForm.value;
    const obj = {
      name: val.name,
      description: val.description
    };

    this.loader.show();
    this.requestLoad.addPaymentType(obj).subscribe(res => {
      this.loader.hide();
      this.toast.show({ 'message': 'Added Successfully.' });
      this.navCtrl.push('ManagePaymentTypePage');
    }, err => {
      this.loader.hide();
      console.log('>>>>err', err);
    });
  }
}



