  // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for Transporter Name list
  transporterName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchTransporterName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  getDealDatabyPodDealNo() {
    // console.log("Re Results >>>>", this.dealsList);
    this.dealsList.forEach(element => {
      if (element.deal_no == this.podDetails.deal_no) {
        this.selectDealNoData(element);
      }
    });
  }

  getDealNosList() {

    if(this.editForm.value.truck_no){
      this.dealsList = [];

      var truckNo =  this.editForm.value.truck_no.truck_no ? this.editForm.value.truck_no.truck_no: this.editForm.value.truck_no;
      var transporterName =  this.editForm.value.transporter_name.transport_name ? this.editForm.value.transporter_name.transport_name: this.editForm.value.transporter_name;

      this.loader.show();
      // this.accounts.getDealNosList(transporterName, truckNo).subscribe((res: any) =>{
      // this.accounts.getNewDealNosList(transporterName, truckNo).subscribe((res: any) =>{
      this.accounts.getNewDealNosListByTruckNo(truckNo).subscribe((res: any) =>{
        var data = res.json();
        this.dealListFlag = true;
        this.dealsList = data.results;

        this.dealsList.forEach(df => {
          this.declare_advance = 0;
          this.receive_advance = 0;
          this.balance_advance = 0;
          this.advanceAmountList.forEach(aa => {
            
            if (df.deal_no == aa.deal_no && aa.name == truckNo) {
              if (aa.advance_amount[0]) {
          
                aa.advance_amount.forEach(e => {
                  this.receive_advance = this.receive_advance + e.amount;
                });
              }
            }
          });

          if(df.transporter_commission){
            this.receive_advance = this.receive_advance + df.transporter_commission;
          }

          this.declare_advance = df.transporter_freight;
          this.balance_advance = this.declare_advance - this.receive_advance;

          df.declare_advance = this.declare_advance;
          df.receive_advance = this.receive_advance;
          df.balance_advance = this.balance_advance;
          df.deal_date_fmt = moment( df.deal_date ).format("YYYY-MM-DD");
          
        });

        this.loader.hide();

        console.log("Results >>>>", data.results);
        
        if (this.podEditFlag == 1) {
          this.podEditFlag = 0;
          this.getDealDatabyPodDealNo();
        }
        
      }, err => {
        console.log("err >>>>", err);
      });

    }
  }

  uploadPOD(event) {
		let fileList: FileList = event.target.files;
		if (fileList.length > 0) {
      let file = fileList[0];
      this.podFlag = 1;
      this.formData.append('upload_pod', file, file.name);
		}
  }

  
  selectDealNoData(data) {
    console.log("Deal No Data >>>>", data);
    this.editForm.controls['deal_no'].setValue(data.deal_no);
    this.editForm.controls['transporter_name'].setValue(data.transporter_name);

    this.dealDate = data.deal_date;
    this.dealFrom = data.from;
    this.dealTo = data.to;
    this.dealConsignor = data.company_name;
    this.DealNoDetails = data;
    this.mainPODAmount = data.balance_advance;

    this.loader.show();
    this.accounts.getPostLoadByTicketNo(data.company_ticket_no).subscribe((res: any) =>{
      var postData = res.json();
      this.loader.hide();

      if(postData.results[0]) {
        this.postLoadData = postData.results[0];
        this.editForm.controls['dispatch_date'].setValue(this.postLoadData.despatch_date);
        this.editForm.controls['delivery_date'].setValue(this.postLoadData.delivery_date);

        if (this.postLoadData.late_delivery_amount) {
          this.lateDeliveryAmount = this.postLoadData.late_delivery_amount;
        }

        if (this.postLoadData.detention_amount) {
          this.detentionAmount = this.postLoadData.detention_amount;
        }

        this.postLoadPaymentMode = this.postLoadData.payment_mode;
      }

      

      this.getAgencyId();
      this.getCompanyId();
      this.getTransporterId();
      this.getAgencyListByName();
      
      
      console.log("postLoadData >>>>", data.results);
    }, err => {
      console.log("err >>>>", err);
    });
  }

  setLateDelivery(event) {

    var deliveryDate = moment(this.editForm.value.delivery_date, "YYYY-MM-DD");
    var arrivalDate = moment(this.editForm.value.arrival_date, "YYYY-MM-DD");

    var lateDelivery = arrivalDate.diff(deliveryDate, 'days')+1  
    this.editForm.controls['late_delivery'].setValue(lateDelivery);   

    var cal = this.lateDeliveryAmount * lateDelivery;
    this.editForm.controls['company_late_delivery_amount'].setValue(cal);  


    if(this.editForm.value.unloading_date) {
      this.setDetention("");
    }
  }

  setDetention(event) {
    var arrivalDate = moment(this.editForm.value.arrival_date, "YYYY-MM-DD");
    var unloadingDate = moment(this.editForm.value.unloading_date, "YYYY-MM-DD");


    var detentionDiff = unloadingDate.diff(arrivalDate, 'days')+1  
    this.editForm.controls['detention'].setValue(detentionDiff); 


    var cal = this.detentionAmount * detentionDiff;
    this.editForm.controls['company_detention_amount'].setValue(cal);  

  }

