  getAgencyId(){

    this.trucks.searchAgencyName(this.DealNoDetails.agency_name).subscribe((res: any) => {
      var data = res.json(); 
      
      this.agencyID = data[0]._id;
      console.log("agencyID >>>>", this.agencyID);
    }, err => {   
      console.log(">>>>error", err);
    });
    
  }

  getCompanyId(){
   
    this.requestLoad.searchCompany(this.DealNoDetails.company_name).subscribe((res: any) => {
      var data = res.json(); 
      
      this.companyId = data[0]._id;
      console.log("companyId >>>>", this.companyId);
    }, err => {
      console.log(">>>>error", err);
    });

  }

  getTransporterId(){

    this.trucks.searchTransporterName(this.DealNoDetails.transporter_name).subscribe((res: any) => {
      var data = res.json(); 
      
      this.transporterId = data[0]._id;
      console.log("transporterId >>>>", this.transporterId);
    }, err => {   
      console.log(">>>>error", err);
    });
    
  }

  getAgencyListByName() {

    var agencyName = this.DealNoDetails.agency_name;

    this.admin.getAgencyListByName(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      
      var agencyData = data.results;
      if(agencyData[0]) {
        
        this.investorName = agencyData[0].investor_name;
        this.agencyPercentage = agencyData[0].agency_percentage;
        this.indianaTruckingPercentage = agencyData[0].indiana_trucking_percentage;
        this.investorPercentage = agencyData[0].investor_percentage;
      }
      console.log("Agency Data >>>>",  agencyData[0]);
    }, err => {   
      console.log(">>>>error", err);
    });
      
  }

  getAdvanceAmountList() {
    // this.loader.show();
    this.accounts.getAdvanceAmountList().subscribe((res: any) => {
      // this.loader.hide();
      var data = res.json(); 
      this.advanceAmountList = data.results;

      this.advanceAmountList.forEach(element => {
        element.date_frmt = moment(element.date).format('YYYY-MM-DD');
      });
      
      console.log('Data >>>>>>>', this.advanceAmountList);
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }
