import { Component, OnInit, ViewChild, ElementRef, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Transporter } from '../../../providers/transporter';
import { NgForm, FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { ToastProvider } from '../../../providers/toast';
import { LoaderProvider } from '../../../providers/loader';
import { EventsService } from '../../../providers/event.service';
import { Observable } from 'rxjs';
import { Trucks } from '../../../providers/truck';
import { MapsAPILoader } from '@agm/core';
import { HomePage } from '../../home/home';
import { CommonHelper } from '../../../helpers/common';


/**
 * Generated class for the AddTransporterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-transporter',
  templateUrl: 'add-transporter.html',
})
export class AddTransporterPage implements OnInit {

  transportFlag = true;
  bankFlag = false;
  addressFlag = false;
  locationFlag = false;
  routeFlag = false;
  acountNo = false;

  addForm: FormGroup;
  address: FormArray;
  route_desired: FormArray;
  address_location = {
    type : "Point",
    coordinates: []
  }

  DestinationsArray = [];
  getTo = "";
  formValues;
  
  submitted = false;

  @ViewChild("searchCity")
  public searchElementRefCity: ElementRef;

  @ViewChild("searchStatue")
  public searchElementRefState: ElementRef;
  
  @ViewChild("search")
  public searchElementRef: ElementRef;

  @ViewChild("searchTo")
  public searchElementRefTo: ElementRef;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public transporter: Transporter,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public fb: FormBuilder,
    public trucks: Trucks,
    public events: EventsService,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
    ) {

    this.addForm = this.fb.group({
      agency_name:[''],
      transport_name: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      website:[''],
      owner_name: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      // location: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      omobile_no_1: ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(10)])],
      omobile_no_2: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
      contact_person: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      cmobile_no_1: ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(10)])],
      cmobile_no_2: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
      account_holder_name: ['', Validators.compose([Validators.minLength(2), Validators.maxLength(25)])],
      account_number: ['', Validators.compose([Validators.maxLength(16)])],
      confirm_account_number: ['', Validators.compose([Validators.maxLength(16)])],
      ifsc: ['', Validators.compose([Validators.minLength(11), Validators.maxLength(11)])],
      address: this.fb.array([ this.createAddressForm()]),
      location_allotment: [''],
      location_restriction: [''],
      latitude: [''],
      longitude: [''],
      route_desired: this.fb.array([this.routeDesiredForm()]),
      location_to: ['']
    });

  }

  ngOnInit() {
  }

  confirmAccount(){
    console.log(this.addForm.value);
    if (this.addForm.value.account_number == this.addForm.value.confirm_account_number) {
      console.log("Same");
      this.acountNo = false;
    } else {
      console.log("Not Same");
      this.acountNo = true;
    }
  }

  get routeDesired() { return <FormArray>this.addForm.get('route_desired'); }

  routeDesiredForm() {
    return this.fb.group({
      location: ['']
    })
  }

  addTo(): void {
    this.route_desired = this.addForm.get('route_desired') as FormArray;
    this.route_desired.push(this.routeDesiredForm());
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  removeTo(index) {
    if (index === 0) {
      this.toast.error({ 'message': 'Cannot remove this form.' });
    } else {
      this.route_desired.removeAt(index);
    }
  }

  addToArray() {
      var obj = {"location": this.getTo};
      this.DestinationsArray.push(obj);
      this.addForm.get('location_to').setValue('');
  }

  removeToArray(index) {
    this.DestinationsArray.splice(index, 1);
    console.log("array >>>", this.DestinationsArray);
  }
  // getIndex(ind) {
  //   console.log(ind);
  //   this.toIndex = ind;
  // }

  
  

  getLocation(e){
    e.preventDefault();
    this.events.broadcast('location');
  }

  createAddressForm() {
    return this.fb.group({
      address_line_1: ['', Validators.compose([Validators.required, , Validators.minLength(2)])],
      address_line_2: ['', Validators.compose([Validators.required, , Validators.minLength(2)])],
      city: ['', Validators.compose([Validators.required, , Validators.minLength(2)])],
      state: ['', Validators.compose([Validators.required, , Validators.minLength(2)])],
      pincode: ['', Validators.compose([Validators.required, Validators.minLength(6), Validators.maxLength(6)])]
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddTransporterPage');
  }

  // Get controls for form array for address
  get addressForm() {
    return this.addForm.get('address')['controls'][0].controls;
  }

  addressData(e: any):void {
    this.formValues = this.addForm.value;

    this.addressForm['address_line_1'].setValue(e.address_line1);
    this.addressForm['address_line_2'].setValue(e.address_line2);
    this.addressForm['city'].setValue(e.city);
    this.addressForm['state'].setValue(e.state);
    this.addressForm['pincode'].setValue(e.pincode);

    this.address_location.coordinates = [];
    this.address_location.coordinates.push(e.lng);
    this.address_location.coordinates.push(e.lat);
    this.formValues.address[0].address_location = this.address_location;
  }

  save(isValid, value) {
    var route_desired = [];
    console.log("isValid >>>>>>>>>", this.addForm); 
    this.addForm.value.route_desired.forEach(r => {
      console.log(">>>>>>>>>", r);  
      route_desired.push(r);
    });
    this.submitted = true;
    if (isValid) {

      this.formValues = value;
      this.formValues.address[0].address_location = this.address_location;
      this.formValues.route_desired = this.DestinationsArray;
      this.formValues.agency_name = this.addForm.value.agency_name.agency_name;
      console.log(">>>>>>>>>FormValue", this.formValues);

      this.loader.show();
      this.transporter.addTransporterDetails(this.formValues).subscribe(res => {
        console.log("res >>>>>", res);
        this.addForm.reset();
        this.loader.hide();
        this.toast.show({ 'message': 'Added successfully.' });
        this.navCtrl.setRoot('ManageTransportPage');
      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>Err", err);
      });
  
    }
  }


  // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }


  preTransport() {
    this.transportFlag = true;
    this.bankFlag = false;
    this.addressFlag = false;
    this.locationFlag = false;
    this.routeFlag = false;
  }

  nextBank() {
    this.transportFlag = false;
    this.bankFlag = true;
    this.addressFlag = false;
    this.locationFlag = false;
    this.routeFlag = false;
  }

  nextAddress() {
    this.transportFlag = false;
    this.bankFlag = false;
    this.addressFlag = true;
    this.locationFlag = false;
    this.routeFlag = false;

    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        let presentCity = new google.maps.places.Autocomplete(this.searchElementRefCity.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentCity.getPlace();
            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);

            (<HTMLInputElement>document.getElementById('City')).value = pre_city;

            let addr = this.addForm.get('address') as FormArray;
            let first = addr.at(0);
            first.get('city').setValue(pre_city);
          });
        });

        let presentState = new google.maps.places.Autocomplete(this.searchElementRefState.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentState.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentState.getPlace();
            let components = CommonHelper.extract_components(place);

            (<HTMLInputElement>document.getElementById('State')).value = components.administrative_area_level_1;

            let addr = this.addForm.get('address') as FormArray;
            let first = addr.at(0);
            first.get('state').setValue(components.administrative_area_level_1);
          });
        }); 
      });
    }, 2000);
  }

  nextLocation() {
    this.transportFlag = false;
    this.bankFlag = false;
    this.addressFlag = false;
    this.locationFlag = true;
    this.routeFlag = false;
    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        let presentCity = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentCity.getPlace();
            let lat = place.geometry.location.lat();
            let lng = place.geometry.location.lng();
            localStorage.setItem('latlng', JSON.stringify({
              lat: lat,
              lng: lng
            }));
            this.events.broadcast('location');
            this.addForm.controls['latitude'].setValue(lat);
            this.addForm.controls['longitude'].setValue(lng);

            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);

            (<HTMLInputElement>document.getElementById('Location')).value = pre_city;
            this.addForm.controls['location_allotment'].setValue(pre_city);

          });
        });
    
      });
    }, 1000);
  }

  nextRoute() {
    this.transportFlag = false;
    this.bankFlag = false;
    this.addressFlag = false;
    this.locationFlag = false;
    this.routeFlag = true;

    setTimeout(() => {
      let toSearch = new google.maps.places.Autocomplete(this.searchElementRefTo.nativeElement, {
        componentRestrictions: {"country": "IN"},
        types: []
      });
      toSearch.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = toSearch.getPlace();
          let components = CommonHelper.extract_components(place);
          let pre_city = CommonHelper.get_sublocality(components);
          console.log(components, pre_city);
          (<HTMLInputElement>document.getElementById('TO')).value = pre_city;
          this.getTo = pre_city;
        });
      });
    }, 2000);
  }
  

}



