import { Component, ViewChild, ElementRef, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { FormBuilder, FormGroup, FormArray, FormControl, Validators } from '@angular/forms';
import { RequestLoad } from '../../providers/request_load';
import { Trucks } from '../../providers/truck';
import { Observable } from 'rxjs';
import 'rxjs/add/observable/of';
import { LoaderProvider } from '../../providers/loader';
import { ToastProvider } from '../../providers/toast';
import { HomePage } from '../home/home';
import { MapsAPILoader } from '@agm/core';
import { CommonHelper } from '../../helpers/common';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';

/**
 * Generated class for the EditRequestLoadPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-request-load',
  templateUrl: 'edit-request-load.html',
})
export class EditRequestLoadPage {

  data;
  id;
  editForm: FormGroup;
  addMultipleDestinations: FormArray;
  submitted = false;
  statuses = ['Pending', 'Cancel', 'Deal Final'];

  DestinationsArray = [];
  getTo = "";

  cancelFlag = false;
  mainCancelMaster = [];
  cancelReasons = [];

  salesmanList = [];


  @ViewChild("searchTo")
  public searchElementRefTo: ElementRef;
  
  @ViewChild("searchFrom")
  public searchElementRefFrom: ElementRef;
  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public formBuilder: FormBuilder,
    public requestLoad: RequestLoad,
    public trucks: Trucks,
    public toastController: ToastController,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    public admin: AdminPrivilegeProvider,
    
    ) {
      this.data = navParams.get('data');  
      this.id = this.data._id;
      this.getSalesmanList();
  }

  get destinationsData(): FormArray { return this.editForm.get('addMultipleDestinations') as FormArray; }

  createItem(data): FormGroup {
    return this.formBuilder.group({
      to: [data.to ? data.to : '', Validators.compose([Validators.required, Validators.minLength(2)])]
    });
  }

  createNewItem(): FormGroup {
    return this.formBuilder.group({
      to: ['', Validators.compose([Validators.required, Validators.minLength(2)])]
    });
  }

  addTo(): void {
    this.addMultipleDestinations = this.editForm.get('addMultipleDestinations') as FormArray;
    this.addMultipleDestinations.push(this.createItem(''));
  }

  removeTo(index) {
    if (index === 0) {
      this.toast.error({ 'message': 'Cannot remove this form..' });
    } else {
      this.addMultipleDestinations = this.editForm.get('addMultipleDestinations') as FormArray;
      this.addMultipleDestinations.removeAt(index);
    }
  }

  addToArray() {
    var obj = {"to": this.getTo};
    this.DestinationsArray.push(obj);
    this.editForm.get('location_to').setValue('');
}

removeToArray(index) {
  this.DestinationsArray.splice(index, 1);
  console.log("array >>>", this.DestinationsArray);
}

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditRequestLoadPage');

    setTimeout(() => {
      let toSearch = new google.maps.places.Autocomplete(this.searchElementRefTo.nativeElement, {
        componentRestrictions: {"country": "IN"},
        types: []
      });
      toSearch.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = toSearch.getPlace();
          let components = CommonHelper.extract_components(place);
          let pre_city = CommonHelper.get_sublocality(components);
          console.log(components, pre_city);

          (<HTMLInputElement>document.getElementById('TO')).value = pre_city;
          this.getTo = pre_city;                
        });
      });

      let fromLoction = new google.maps.places.Autocomplete(this.searchElementRefFrom.nativeElement, {
        componentRestrictions: {"country": "IN"},
        types: []
      });
      fromLoction.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = fromLoction.getPlace();
          let components = CommonHelper.extract_components(place);
          let pre_city = CommonHelper.get_sublocality(components);
          console.log(components, pre_city);

          (<HTMLInputElement>document.getElementById('From')).value = pre_city;
          this.editForm.controls['from'].setValue(pre_city);
            
        });
      });
    }, 1000);
  }

  ngOnInit() {
    var destinations = [];
    this.data.destinations.forEach(element => {
      destinations.push(this.createItem(element));
      var obj = {"to": element.to};
      this.DestinationsArray.push(obj);
    });

    this.editForm = this.formBuilder.group({
      date: [this.data.date],
      agency_name: [this.data.agency_name, Validators.compose([Validators.required, Validators.minLength(2)])],
      transporter_name: [this.data.transporter_name, Validators.compose([Validators.required, Validators.minLength(2)])],
      truck_no: [this.data.truck_no, Validators.compose([Validators.required])],
      from: [this.data.from, Validators.compose([Validators.required])],
      // // loading_place: [this.data.loading_place],
      addMultipleDestinations: this.formBuilder.array(destinations),
      status: [this.data.status],
      location_to: [''],
      salesman: [this.data.salesman, Validators.compose([Validators.required])],
      
      
    });

    if (this.editForm.value.status == "Cancel") {
      this.getCancel();
    }
  }

  getSalesmanList(){
    var agencyName = localStorage.getItem('agency_name');

    this.admin.getListByAgency(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;

      console.log("mainAccoutsList >>>>", dataList);

      dataList.forEach(element => {
        this.salesmanList.push(element.first_name);
      });
      console.log("salesmanList >>>>", this.salesmanList);


    }, err => {
      console.log(">>>>error", err);
    });
  }


  getCancel(){
    console.log("Data >>>", this.editForm.value.status);

    if (this.editForm.value.status == "Cancel") {
      this.cancelFlag = true;
      this.requestLoad.getCancelReasons("Post Load").subscribe( (res: any) => {
        var data = res.json();
        this.cancelReasons = []; 
      
        data.results.forEach(element => {
          this.cancelReasons.push({"name": element.cancel_reason, "flag": false});
        });

        if(this.data.cancel_reasons[0]) {
          this.data.cancel_reasons.forEach(mainElement => {
            this.cancelReasons.forEach(subElement => {
            
              if (subElement.name == mainElement) {
                subElement.flag = true;
                this.mainCancelMaster.push(mainElement);
              }
            });
          });
        }
        console.log("Data >>>", this.cancelReasons);
      }, err => {
        console.log(">>>error", err);
      });
    }  else {
      this.cancelFlag = false;
      this.cancelReasons = [];
    }
  }

  addCancel(e, tt, ind) {
    
    if ( e.target.checked ) {
      this.mainCancelMaster.push(tt);
      this.cancelReasons[ind].flag = true;
      console.log("this.mainCancelMaster >>>>", this.mainCancelMaster);
    } else {
      var i = 0;
      this.mainCancelMaster.forEach(element => {
        if (element == tt) {
          console.log(" IN    >>>>", i);
          this.mainCancelMaster.splice(i, 1);
        }
        i = i + 1;
      });
      console.log("this.mainCancelMaster >>>>", this.mainCancelMaster);
    }

  }
   // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for Transporter Name list
  transporterName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchTransporterName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

    // Search query for Agency Name list
    agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  update(isValid) {
    this.submitted = true;
    if (isValid) {
      var getFromData = this.editForm.value;
      if (getFromData.truck_no.truck_no || getFromData.truck_no == this.data.truck_no) {
        var truckNo = getFromData.truck_no.truck_no ? getFromData.truck_no.truck_no : this.data.truck_no;
        var agencyName = getFromData.agency_name.agency_name ? getFromData.agency_name.agency_name : this.data.agency_name;
        var transporterName = getFromData.transporter_name.transporter_name ? getFromData.transporter_name.transporter_name : this.data.transporter_name;
  
        var obj = {
          "date": getFromData.date, 
          "agency_name": agencyName,
          "transporter_name": transporterName, 
          "truck_no": truckNo, 
          "from": getFromData.from,
          // // "loading_place": getFromData.loading_place,
          // "destinations": getFromData.addMultipleDestinations,
          "destinations": this.DestinationsArray,
          "status": getFromData.status,
          "cancel_reasons":this.mainCancelMaster,
          "salesman": getFromData.salesman,
        }

        console.log("obj  >>>>>", obj);
        console.log("id  >>>>>", this.id);
  
        this.requestLoad.updateRequestLoad(this.id, obj).subscribe(res => {
          this.editForm.reset();
          this.toast.show({ 'message': 'Updated successfully.' });
          this.navCtrl.setRoot('ManageRequestLoadPage');
        }, err => {
          console.log(">>>>>Err", err);
          this.toast.error({ 'message': 'Something went wrong.' });
        });
  
      } else {
        console.log("Else Select >>>");
        this.toast.error({ 'message': 'Please Add New Truck OR Select Truck from List..' });
      }
    }
  }

}



