import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { RequestLoad } from '../../../providers/request_load';
import { LoaderProvider } from '../../../providers/loader';
import { ToastProvider } from '../../../providers/toast';
import { HomePage } from '../../home/home';
import { AddAccountTypePage } from './add-account-type/add-account-type';
import { EditAccountTypePage } from './edit-account-type/edit-account-type';

/**
 * Generated class for the ManageAccountTypesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-account-types',
  templateUrl: 'manage-account-types.html',
})
export class ManageAccountTypesPage {

  types: any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public loader: LoaderProvider,
    public toast: ToastProvider
  ) {
    this.getTypes();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManagePaymentModePage');
  }

  getTypes() {
    this.loader.show();
    this.requestLoad.getAccountTypes().subscribe(res => {
      this.loader.hide();
      console.log('>>>>Hello res', res);
      let data = res.json();
      this.types = data.results;
    }, err => {
      this.loader.hide();
      console.log('>>>>Hello err', err);
    });
  } 

  home(){
    this.navCtrl.setRoot(HomePage);
  }
  
  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Action',
    //   buttons: [{
    //     text: 'Add',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push(AddAccountTypePage);
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  async presentActionSheet(userData, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Edit',
        icon: 'create',
        handler: () => {
          this.navCtrl.push(EditAccountTypePage, {data: userData});
        }
      }, {
        text: 'Delete',
        icon: 'trash',
        handler: () => {
          this.deleteType(userData._id);
        }
      }]
    });
    await actionSheet.present();
  }

  deleteType(id) {
    const conf = confirm('Are you sure you want to delete this type ?');

    if(conf) {
      this.loader.show();
      this.requestLoad.deleteAccountType(id).subscribe((res: any) => {
        this.loader.hide();
        this.toast.show({ 'message': 'Deleted Successfully.' });
        this.getTypes();
      }, err => {
        this.loader.hide();
        console.log("error");
      });
    }
  }
}




