import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { RequestLoad } from '../../providers/request_load';
import { MatchCompanyListPage } from '../match-company-list/match-company-list';
import { LoaderProvider } from '../../providers/loader';
import { EditRequestLoadPage } from '../edit-request-load/edit-request-load';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { AddPostLoadPage } from '../manage-post-load/add-post-load/add-post-load';
import { HomePage } from '../home/home';
import { LoadsAvailabilityPage } from '../loads-availability/loads-availability';
import * as moment from 'moment';
import { DealFinalPage } from '../manage-deal-final/deal-final/deal-final';

/**
 * Generated class for the ManageRequestLoadPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manage-request-load',
  templateUrl: 'manage-request-load.html',
})
export class ManageRequestLoadPage {
  status = ['Pending', 'Deal Final', 'Unsuccess'];
  dataList = [];
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public requestLoad: RequestLoad,
    public actionSheetController: ActionSheetController,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider
    ) {
      
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ManageRequestLoadPage');
  }

  ngOnInit(){
    this.getRequestLoadList();
  }

  edit(data) {
    this.navCtrl.push(EditRequestLoadPage, {"data": data });
  }

  async addActionSheet() {
    // const actionSheet = await this.actionSheetController.create({
    //   title: 'Actions',
    //   buttons: [{
    //     text: 'Add Request Load',
    //     icon: 'add',
    //     handler: () => {
          this.navCtrl.push('RequestLoadPage');
    //     }
    //   }]
    // });
    // await actionSheet.present();
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  async presentActionSheet(data, e: Event) {
    e.preventDefault();
    const actionSheet = await this.actionSheetController.create({
      title: 'Actions',
      buttons: [{
        text: 'Load Available',
        icon: 'cube',
        handler: () => {
          this.navCtrl.push(LoadsAvailabilityPage);
        }
      }, {
        text: 'Post Load',
        icon: 'add',
        handler: () => {
          this.navCtrl.push(AddPostLoadPage);
        }
      }, {
        text: 'See Companies',
        icon: 'ios-information-circle-outline',
        handler: () => {
          console.log("data >>>", data);
          // "loading_place": data.loading_place,
          this.navCtrl.push(MatchCompanyListPage, {"srno": data.srno, "id": data._id, "transporter_name": data.transporter_name, "truck_no": data.truck_no, "from": data.from, "to": data.destinations_str });
        }
      }, {
        text: 'Edit Request Load',
        icon: 'ios-create',
        handler: () => {
          this.navCtrl.push(EditRequestLoadPage, {"data": data });
        }
      }, {
        text: 'Deal Final',
        icon: 'checkmark',
        handler: () => {
          this.navCtrl.push(DealFinalPage, {"requestData": data });
        }
      }, {
        text: 'Remove',
        icon: 'ios-trash',
        handler: () => {
          this.deleteApi(data);
        }
      }]
    });
    await actionSheet.present();
  }

  getRequestLoadList() {
    this.loader.show();
    this.requestLoad.getRequestLoad().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      
      var i = 1;
      data.results.forEach(element => {
        element.srno = i;
        element.createdDate = moment(element.date, 'DD-MM-YYYY').format('DD-MM-YYYY');
        i = i + 1;
      });

      console.log('Data >>>>>>>', data.results);
      this.dataList = data.results;


      this.dataList.forEach( r => {
        
        var str = [];
        r.destinations.forEach(t => {
          str.push(t.to);        
        });
        console.log("str >>>>>", str);
        r.status = r.status ? r.status:'Pending';
        r.destinations_str = str;

        var negotiate = "";
        if(r.negotiate[0]) {
          var Ton = r.negotiate[0].ton ? r.negotiate[0].ton: "0";
          var Rates = r.negotiate[0].rates ? r.negotiate[0].rates: "0";
          negotiate = Ton+ " ton * " + Rates + " rates";
        }
        r.negotiate = negotiate;

      });

      console.log('Data >>>>>>>', this.dataList);
    }, err => {
      this.loader.hide();
      console.log(">>>>error", err);
    });
  }

  deleteApi(id) {
    var load_id = id._id;
    console.log("id  >>>", load_id);
    this.admin.deleteRequestLoad(load_id).subscribe((res: any) => {
      this.navCtrl.push('ManageRequestLoadPage');
    }, err => {
      this.loader.hide();
      console.log("error");
    });
  }

}



