import { Component, NgZone, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, Content } from 'ionic-angular';
import { HomePage } from '../../home/home';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MapsAPILoader } from '@agm/core';
import { AdminPrivilegeProvider } from '../../../providers/admin-privilege/admin-privilege';
import { Trucks } from '../../../providers/truck';
import { EventsService } from '../../../providers/event.service';
import { ToastProvider } from '../../../providers/toast';
import { LoaderProvider } from '../../../providers/loader';
import { CommonHelper } from '../../../helpers/common';
import { Observable } from 'rxjs';
import { ManageAccountsMasterPage } from '../manage-accounts-master';
import { Accounts } from '../../../providers/accounts';

/**
 * Generated class for the AddAccountMasterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-account-master',
  templateUrl: 'add-account-master.html',
})
export class AddAccountMasterPage {

  addForm: FormGroup;

  main = true;
  address = false;
  drcr = ['Debit', 'Credit'];

  underLedgers = [];
  Groups = [];

  cateList = [];
  accountTypesList = [];

  @ViewChild("search1")
  public searchElementRef1: ElementRef;

  @ViewChild("search2")
  public searchElementRef2: ElementRef;

  @ViewChild("search3")
  public searchElementRef3: ElementRef;

  @ViewChild("search4")
  public searchElementRef4: ElementRef;

  @ViewChild(Content) content: Content;
  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public fb: FormBuilder,
    public events: EventsService,
    public trucks: Trucks,
    public admin: AdminPrivilegeProvider,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    public accounts: Accounts,
    
    ) {

      this.addForm = this.fb.group({
        account_name: [''],
        agency_name: [''],
        truck_no: [''],
        category: [''],
        group: [''],
        accounts_type: [''],
        under_ledger: [''],
        gst_number: [''],
        pan_number: [''],
        vendor_code: [''],
        contact_person: [''],
        email: ['', Validators.email],
        mobile_1: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
        mobile_2: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
        opening_balance: [''],
        o_drcr: [''],
        closing_balance: [''],
        c_drcr: [''],
        billing_street_1: [''],
        billing_street_2: [''],
        billing_city: [''],
        billing_state: [''],
        billing_pin: ['', Validators.compose([Validators.minLength(6), Validators.maxLength(6)])],
        billing_country: [''],
        billing_phone: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
        shipping_street_1: [''],
        shipping_street_2: [''],
        shipping_city: [''],
        shipping_state: [''],
        shipping_pin: ['', Validators.compose([Validators.minLength(6), Validators.maxLength(6)])],
        shipping_country: [''],
        shipping_phone: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])]
      });

      this.getAccountTypesList();
      this.getAccountsCategoryList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddAccountMasterPage');
  }

  home(){
    this.navCtrl.setRoot(HomePage);
  }
 

  getAccountsCategoryList() {
    
    this.admin.getAccountsCategoryList().subscribe((res: any) => {
      var data = res.json(); 
      this.cateList = data.results;

      console.log('Data >>>>>>>', this.cateList);

      this.cateList = this.cateList.slice(0);
      this.cateList.sort(function(a,b) {
          var x = a.name.toLowerCase();
          var y = b.name.toLowerCase();
          return x < y ? -1 : x > y ? 1 : 0;
      });

      console.log('Sort >>>>>>>', this.cateList);

    }, err => {
      console.log(">>>>error", err);
    });
  }

  getAccountTypesList() {
    
    this.admin.getAccountTypesList().subscribe((res: any) => {
      var data = res.json(); 
      this.accountTypesList = data.results;

      this.accountTypesList = this.accountTypesList.slice(0);
      this.accountTypesList.sort(function(a,b) {
          var x = a.name.toLowerCase();
          var y = b.name.toLowerCase();
          return x < y ? -1 : x > y ? 1 : 0;
      });

      console.log('Data >>>>>>>', this.accountTypesList);
    }, err => {
      console.log(">>>>error", err);
    });
  }


  // Search query for Accounts Type  Name list
  accountTypes = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.admin.searchAccountTypes(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  // Search query for Accounts Category Name list
  accountsCategory = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.admin.searchAccountsCategory(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

   // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }
  

  save() {

    var formData = this.addForm.value;

    // formData.accounts_type = formData.accounts_type.name ? formData.accounts_type.name: formData.accounts_type; 
    // formData.category = formData.category.name ? formData.category.name: formData.category; 

    var gstNumber = formData.gst_number;
    var panNumber = formData.pan_number;
    console.log("STR >>>>", gstNumber.toUpperCase());
    console.log("STR >>>>", panNumber.toUpperCase());
    formData.agency_name = formData.agency_name.agency_name ? formData.agency_name.agency_name: formData.agency_name;
    formData.gst_number = gstNumber.toUpperCase();
    formData.pan_number = panNumber.toUpperCase();

    console.log("Data >>>>", formData);
    this.loader.show();
    this.admin.addAccountsMaster(formData).subscribe(res => {
      this.loader.hide();
      console.log("res >>>>>", res);

      this.toast.show({ 'message': 'Added successfully.' });
      this.navCtrl.setRoot(ManageAccountsMasterPage);
    }, err => {
      this.loader.hide();
      this.toast.error({ 'message': 'Something went wrong.' });
      console.log(">>>>>Err", err);
    });

  }

  nextAdd() {
    this.main = false;
    this.address = true;
    this.content.scrollToTop();

    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        let billingCity = new google.maps.places.Autocomplete(this.searchElementRef1.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        billingCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = billingCity.getPlace();
            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);

            (<HTMLInputElement>document.getElementById('billingCity')).value = pre_city;
            this.addForm.controls['billing_city'].setValue(pre_city);

          });
        });

        let billingState = new google.maps.places.Autocomplete(this.searchElementRef2.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        billingState.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = billingState.getPlace();
            let components = CommonHelper.extract_components(place);

            (<HTMLInputElement>document.getElementById('billingState')).value = components.administrative_area_level_1;
            this.addForm.controls['billing_state'].setValue(components.administrative_area_level_1);
            this.addForm.controls['billing_country'].setValue(components.country);

          });
        });



        let shippingCity = new google.maps.places.Autocomplete(this.searchElementRef3.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        shippingCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = shippingCity.getPlace();
            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);

            (<HTMLInputElement>document.getElementById('shippingCity')).value = pre_city;
            this.addForm.controls['shipping_city'].setValue(pre_city);

          });
        });

        let shippingState = new google.maps.places.Autocomplete(this.searchElementRef4.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        shippingState.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = shippingState.getPlace();
            let components = CommonHelper.extract_components(place);
            
            (<HTMLInputElement>document.getElementById('shippingState')).value = components.administrative_area_level_1;
            this.addForm.controls['shipping_state'].setValue(components.administrative_area_level_1);
            this.addForm.controls['shipping_country'].setValue(components.country);
           
          });
        });
      });
    }, 2000);
  }

  preMain() {
    this.main = true;
    this.address = false;
  }


  truckFlag = false; 
  accountNameList = [];
  truckNoList = [];

  radioSelect(event) {
    console.log("Here ->", event);

    if(event == "account_name" ) {
      this.truckFlag = false;
      this.getAccountsNameByAgencyAndTruck();
    }

    if(event == "truck_no" ) {
      this.accountNameList = [];
      this.truckFlag = true;
    }
  }

  getAccountsNameByAgencyAndTruck(){
    this.accountNameList = [];
    var filters = {
      agency_name: '',
      truck_no: ''
    }

    if(this.addForm.value.agency_name) {
      filters.agency_name = this.addForm.value.agency_name.agency_name;
    }

    if(this.addForm.value.truck_no) {
        filters.truck_no = this.addForm.value.truck_no;
    }

    this.loader.show();
    this.accounts.getAccountsNameByAgencyAndTruck(filters).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;
      this.accountNameList = dataList;
      this.accountNameList = this.accountNameList.slice(0);
      this.accountNameList.sort(function(a,b) {
          var x = a.toLowerCase();
          var y = b.toLowerCase();
          return x < y ? -1 : x > y ? 1 : 0;
      });
      console.log("mainAccoutsList >>>>", dataList);
      this.loader.hide();


    }, err => {
      console.log(">>>>error", err);
    });

  }

  // Search query for truck no list
  truckNo = (keyword: any): Observable<any[]> => {
    if (keyword) {
      return this.trucks.searchTruckNo(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  selectTruck() {
    this.getAccountsNameByAgencyAndTruck();
  }

  getTruckNoByAgencyLedger() {

    var agencyName = this.addForm.value.agency_name.agency_name ? this.addForm.value.agency_name.agency_name: this.addForm.value.agency_name
    this.truckNoList = [];
    this.loader.show();

    this.accounts.getTruckNoByAgencyLedger(agencyName).subscribe((res: any) => {
      var data = res.json(); 
      var dataList = data.results;
      this.truckNoList = dataList;

      this.truckNoList = this.truckNoList.slice(0);
      this.truckNoList.sort(function(a,b) {
          var x = a.toLowerCase();
          var y = b.toLowerCase();
          return x < y ? -1 : x > y ? 1 : 0;
      });

      console.log("truck No List >>>>", this.truckNoList);
      this.loader.hide();


    }, err => {
      console.log(">>>>error", err);
    });

  }

}



